<template>
  <div class="box">
    <!-- HEADER | ICONES -->
    <header>
      <ul class="redesSociais">
        <li class="site"><a href="https://epsoft.com.br/" target="_blank"><img class="imagemMidiasIcone" :src="home.url"
              alt="" /></a></li>
        <li class="linkedin"><a href="https://www.linkedin.com/company/epsoft-privacy-security/" target="_blank"><img
              class="imagemMidiasIcone" :src="linkedin.url" alt="" /></a></li>
        <li class="facebook"><a href="https://www.facebook.com/flashsafe.epsoft" target="_blank"><img
              class="imagemMidiasIcone" :src="facebook.url" alt="" /></a></li>
        <li class="instagram"><a href="https://www.instagram.com/epsoft_brasil/" target="_blank"><img
              class="imagemMidiasIcone" :src="instagram.url" alt="" /></a></li>
        <li class="email"><a href="mailto:suporte@epsoft.com.br?subject=Suporte - Epsoft" target="_blank"><img
              class="imagemMidiasIcone" :src="email.url" alt="" /></a></li>
        <!--<li class="skype"><a href="https://join.skype.com/xY1VaXsZTMHE" target="_blank"><img class="imagemMidiasIcone" :src="skype.url" alt="" /></a></li>-->
        <!--<li class="telefone"><a href="#" target="_blank"><img class="imagemMidiasIcone" :src="phone.url" alt=""/></a></li>-->
      </ul>
    </header>
    <!-- FORMULARIO LOGIN -->
    <form class="login-form form-style" id="form-style" action="" method="" @submit.prevent="handleSubmit">

      <!-- IMG LOGO FLASH SAFE -->
      <b-row>
        <b-col class="imgFlash">
          <img v-bind:src="imgFlash.url" v-bind:alt="imgFlash.titulo" class="img-icon">
        </b-col>
      </b-row>
      <!-- FEEDBACK ERRO AO TENTAR LOGAR -->
      <p v-if="errors.length">
      <ul class="erro selectErrotxt">
        <li v-for="error in errors" :key="error.id">{{ error }}</li>
      </ul>
      </p>

      <!-- INPUT USUARIO -->
      <input type="text" placeholder="E-mail ou Usuário" id="escolha_usuario" name="usernameLogin"
        v-model="selecionausuario" autofocus>

      <!-- INPUT SENHA -->
      <div>
        <input :type="senhaFieldType" placeholder="Senha" id="escolha_senha" name="hash_senhaLogin"
          v-model="selecionasenha">
        <span class="span-password" v-if="senhaFieldType == 'password'" type="password" @click="switchVisibility">
          <img v-bind:src="imgeyeshow.url" v-bind:alt="imgeyeshow.titulo" />
        </span>
        <span class="mostra-eye" v-else type="password" @click="switchVisibility">
          <img v-bind:src="imgehidden.url" v-bind:alt="imgehidden.titulo" />
        </span>
      </div>

      <div class="g-recaptcha">
        <vue-recaptcha sitekey="6LdtO0AhAAAAAF57PF3GMDZj2TZXWxXp88R5JqtN" @verify="onVerify">
        </vue-recaptcha>
      </div>

      <!-- BOTAO LOGIN -->
      <!--input type="submit" name="Login" value="Login" class="bton login" id="login" :disabled="!isUsuarioValido"!-->
      <input type="submit" name="Login" value="Login" class="bton login" id="login" :disabled="!isUsuarioValido">
      <!--<div class="links">
				<a href="#">Forgot Password</a>
				<a href="#">Sign Up</a>
			</div>-->
      <!-- <button></button> -->
    </form>
    <loader :status="loaderCheck"></loader>




  </div>
</template>
<script>
//import imgFlash from '../../../assets/img/flash_nav_logotipo_new.png'
//import imgFlash from '../../../assets/img/horizontal_cores_DLP.svg'
import imgFlash from '../../../assets/img/epsoft.jpg'
import imgeyeshow from '../../../assets/img/eye_show.png'
import imgehidden from '../../../assets/img/eye-hidden.png'
//import { mapActions } from 'vuex'
import { userService } from '../../../_services';
import phone from '../../../assets/img/phone.png'
import email from '../../../assets/img/o-email.png'
import home from '../../../assets/img/home.png'
import linkedin from '../../../assets/img/linkedin.png'
import facebook from '../../../assets/img/facebook.png'
import skype from '../../../assets/img/skype.png'
import instagram from '../../../assets/img/instagram.png'
import Loader from '../../shared/loader/loader'

import { VueRecaptcha } from 'vue-recaptcha';

export default {
  components: {
    'loader': Loader,
    VueRecaptcha,
  },

  computed: {
    isUsuarioValido() {
      return this.listaUsuarios.includes(this.selecionausuario);
    },
  },
    data() {
      return {
        locationUrl: '',
        loaderCheck: false,
        submitted: false,
        loading: false,
        returnUrl: '',
        error: '',
        retorno: '',
        imgFlash: { url: imgFlash, titulo: 'foto usuario' },
        imgeyeshow: { url: imgeyeshow, titulo: 'foto olho aberto' },
        imgehidden: { url: imgehidden, titulo: 'foto olho fechado' },
        phone: { url: phone, titulo: 'imagem pequena de um telefone' },
        home: { url: home, titulo: 'imagem pequena de uma casa' },
        email: { url: email, titulo: 'imagem pequena de um email' },
        linkedin: { url: linkedin, titulo: 'imagem pequena do icone do linkedin' },
        facebook: { url: facebook, titulo: 'imagem pequena do icone do facebook' },
        instagram: { url: instagram, titulo: 'imagem pequena do icone do instagram' },
        skype: { url: skype, titulo: 'imagem pequena do icone do Skype' },
        errors: [],
        selecionausuario: null,
        selecionasenha: null,
        senhaFieldType: 'password',
        recaptcha: null,
        form: {
          usuario: '',
          senha: '',
        },
        listaUsuarios: [ ],

      }
    },
    mounted() {
      /* CAPITURA UM EVENTO DE PARAR DE PRESSIONAR UMA TECLA PARA ATIVAR O EVENTO */
      window.addEventListener('keyup', function (event) {
        if (event.keyCode === 13) {
          /* ATRIBUI O ID #login DO BOTAO DE LOGAR A UMA CONSTANTE */
          const button = document.getElementById("login");
          // button.click();
        }
      });
    },
    created() {
      /* INSERE NA VARIAVEL A URL DA APLICACAO */
      this.locationUrl = this.$store.state.variaveisAmbiente.locationUrl;
      // reset login status
      //userService.logout();

      // get return url from route parameters or default to '/'
      /* INSERE UMA BARRA NA URL */
      this.returnUrl = this.$route.query.returnUrl || '/';
    },
    methods: {
      onEvent() {
        this.$refs.recaptcha.execute();
      },
      onVerify(response) {
        this.recaptcha = response
        var button = document.querySelector('#login')
        button.disabled = false;
      },
      // ...mapActions(['ActionSetToken','ActionDoLogin', 'ActionSignOut']),
      /* HABILITA OU DESABILITA A VISIBILIDADE DA SENHA ALTERNANDO SEU ESTADO DE password para text */
      switchVisibility() {
        this.senhaFieldType = this.senhaFieldType === 'password' ? 'text' : 'password'
      },
      /* PELO QUE CONSEGUI OBSERVAR ESSE CAMPO E RESERVADO PARA REALIZAR A VALIDACAO DE USUARIOS LOGADOS NA SESSAO */
      /* E CASO REALIZE UMA NOVA CONEXAO MESMO ESTAO LOGADO, A SESSAO LOGADA E FINALIZADA PARA QUE SE POSSA REALIZAR A NOVA CONEXAO */
      checkUser: function (userNome) {
        let url = `${this.locationUrl}/dpo/acesso/sessao/desloga/${userNome}/resp_rest`
        console.log('//============>>')
        console.log(url)
        console.log(param)
        console.log(new Date())
        console.log('MilliSec: ' + Date.now())
        console.log('<<============//')
        this.$http.get(url)
          .then(function (res) {
            this.retorno = JSON.parse(res.bodyText)
            if (this.retorno[0].Status == 'logado') {
              //this.modalUsuarioDuplicado(userNome);

              var requestOptions = {
                method: 'POST',
                redirect: 'follow'
              };

              /* FINALIZA A SESSAO DE UM USUARIO */
              fetch(`${this.locationUrl}/dpo/acesso/sessao/${this.retorno[0].secret}/resp_rest`, requestOptions)
                .then(response => {
                  if (!response.ok) {
                    throw new Error(response.statusText)
                  }
                  /* RETIRA O TOKEN DO USUARIO DO LOCAL STORAGE, DESSA FORMA FORCADO O LOGOUT DO USUARIO DA SESSAO */
                  localStorage.removeItem('user');
                  return response.json()
                })
            }
          })
      },

      /* VERIFICA CONEXOES DUPLICADAS AO SE LOGAR EM UMA SESSAO  */
      modalUsuarioDuplicado: function (user) {
        this.$swal({
          // icon: 'error',
          title: `O usuário ${user} está logado`,
          html: `<h6>Deseja encerra a sessão anterior para começar uma nova ?</h6>`,
          text: "Você não poderá reverter isso!",
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          confirmButtonText: 'Sim, eu tenho!',
          cancelButtonColor: '#d33',
          cancelButtonText: 'Cancelar',
          showLoaderOnConfirm: true,
          preConfirm: () => {
            var myHeaders = new Headers();
            myHeaders.append("Content-Type", "application/json");
            var requestOptions = {
              method: 'POST',
              redirect: 'follow'
            };

            /* FINALIZA A SESSAO DE UM USUARIO */
            return fetch(`${this.locationUrl}/dpo/acesso/sessao/${this.retorno[0].secret}/resp_rest`, requestOptions)
              .then(response => {
                if (!response.ok) {
                  throw new Error(response.statusText)
                }

                /* RETIRA O TOKEN DO USUARIO DO LOCAL STORAGE, DESSA FORMA FORCADO O LOGOUT DO USUARIO DA SESSAO */
                localStorage.removeItem('user');
                return response.json()
              })
              .catch(error => {
                this.$swal.showValidationMessage(
                  `Request failed: ${error}`
                )
              })
          },
          allowOutsideClick: () => !this.$swal.isLoading()
        }).then((result) => {
          if (result.value) {
            this.$swal.fire({
              // icon: 'success',
              title: 'Encerrado!',
            })
          }
        });
      },

      /* VALIDA O LOGIN DO USUARIO EMITINDO MENSAGENS DE ERRO PARA USUARIO OU SENHE, OU VALIDANDO O ACESSO DO USUARIO */
      handleSubmit: function (submitEvent) {

        this.loaderCheck = true
        let request = {};
        this.errors = [];
        for (let item in submitEvent.target) {
          if (submitEvent.target.hasOwnProperty(item)) {
            const record = submitEvent.target[item]
            if ((record.name == 'usernameLogin') || (record.name == 'hash_senhaLogin')) {
              if (record.value == "") {
                record.classList.add('selectErro');
                if (record.name == 'usernameLogin') {
                  this.errors.push('O campo Usuário é obrigatório.');
                  this.loaderCheck = false
                } else if (record.name == 'hash_senhaLogin') {
                  this.errors.push('O campo Senha é obrigatório.');
                  this.loaderCheck = false
                }
              } else {
                record.classList.remove('selectErro');
                request[record.name] = record.value;
              }
            }
          }
        }
        if ((request.usernameLogin) && (request.hash_senhaLogin)) {
          localStorage.setItem('infoUser', JSON.stringify(request.usernameLogin));
          this.submitted = true;
          request.hash_senhaLogin = this.setCrpPsw(request.hash_senhaLogin)

          //userService.login(request.usernameLogin, request.hash_senhaLogin)
          setTimeout(() => {
            userService.login(request.usernameLogin, request.hash_senhaLogin)
              .then(
                /* E UM REDRECT E NAO UM EVIO VIA ENDPOINT */
                /* CASO ESTEJA TUDO OK E REDIRECIONADO O USUARIO PARA A TELA DE REDES COM O /, ETA CONFIGURADO DENTRO DO ARUQIVO DE ROTEAMENTO PARA REDIRECIONAR AUTOMATICAMENTE PARA O COMPONENTE DE REDE */
                user => {
                  this.loaderCheck = false
                  this.$router.push(this.returnUrl) // REDIRECIONAMENTO DE PAGINA - O ROTEAMENTO ESTA DENTRO DO ARQUIVO routes.js ex.: /alertas
                },
                /* CASO GERE ALGUM ERRO NAO E REDIRECIONADO O USUARIO PARA A TELA DE REDE COM O REDIRECIONEMTO E O USUARIO E MANTEIDO NA TELA DE LOGIN E INFORMADO O ERRO AO USUARIO */
                error => {

                  this.error = error;
                  //this.loading = false;
                  this.loaderCheck = false

                  if (error.msgRetorno) {
                    if (error.msgRetorno.includes('Duplicate entry')) {
                      this.checkUser(request.usernameLogin)
                    }
                    if (error.msgRetorno.includes('Acesso Negado') || error.msgRetorno.includes('consulta não retornou')) {
                      this.$swal({
                        // icon: 'error',
                        title: 'Atenção!',
                        html: '<h6>Por favor verifique o login e a senha.</h6>',
                      })
                    }
                  } else {
                    this.$swal({
                      // icon: 'error',
                      title: 'Atenção!',
                      html: '<h6>Não foi possível acessar o sistema, por favor, tente acessar novamente mais tarde, ou se caso persista o erro fale com o suporte.</h6>',
                    })
                  }
                }
              )
          }, 2000);

        }

      },
      setCrpPsw(psw) {
        var psw = psw
        psw = this.$CryptoJS.SHA256(psw).toString()
        return psw
      },
    }

  }

</script>>
<style scoped>
body {
  background-image: url('../../../assets/img/backgroundHexagonal.jpg');
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  font-size: 1.6rem;
  color: #545c5f;
  font-family: Lato, sans-serif;
  font-weight: 400;
  min-height: 90vh;
  min-width: 100%;
}

.g-recaptcha {
  justify-content: center;
  display: flex;
  margin-top: 1rem;
  margin-bottom: -1.5rem;
}

img.img-icon {
  max-width: 300px;
}

@import url('https://fonts.googleapis.com/css?family=Raleway&display=swap');

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Raleway', sans-serif;
}

.box {
  position: relative;
  display: flex;
  background-color: white;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-image: url('../../../assets/img/backgroundHexagonal.jpg');
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: transparent;
  font-size: 1.6rem;
  color: #545c5f;
  font-family: Lato, sans-serif;
  font-weight: 400;
  min-height: 90vh;
  min-width: 100%;
}

.login-form {
  min-width: 250px;
  max-width: 400px;
  border-radius: 24px;
  padding: 15px;
  background-color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.login-form h1 {
  text-align: center;
  font-size: 2.5rem;
  margin-top: 35px;
}

.login-form input[type="text"] {
  margin-top: 30px;
}

.login-form input[type="password"] {
  margin-top: 10px;
}

input {
  outline: none;
}

.links {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.links>a:first-of-type {
  margin-right: 5px;
}

.links>a {
  background-color: #E0E0E0;
  border-radius: 24px;
  font-weight: 400;
  color: black;
  line-height: 12px;
  flex: 1;
  text-align: center;
  padding: 10px;
  text-decoration: none;
  font-family: 'Raleway';
  transition: 0.25s;
}

.links>a:hover {
  opacity: 0.6;
}

.login-form input[type="submit"] {
  background-color: #E0E0E0;
  background-image: linear-gradient(19deg, #21D4FD 0%, #B721FF 100%);
  width: 100%;
  color: white;
  border: none;
  margin-top: 35px;
  cursor: pointer;
  padding: 10px;
  font-family: 'Raleway', sans-seriff;
  font-size: 1.3rem;
  font-weight: bold;
  border-radius: 24px;
  transition: 0.25s;
}

.login-form input[type="submit"]:disabled {
  background-image: linear-gradient(19deg, #aeaeb1 0%, #aeaeb1 100%);
  color: #666666;
}

.login-form input[type="submit"]:hover {
  opacity: 0.8;
}

.login-form input[type="text"]:focus,
.login-form input[type="password"]:focus {
  border: 2px #21D4FD solid;
}

.login-form input[type="text"],
.login-form input[type="password"] {
  width: 100%;
  border: none;
  border-radius: 24px;
  font-size: 1rem;
  font-family: 'Raleway', sans-seriff;
  background-color: gainsboro;
  padding: 15px;
}

.span-password img {
  height: 2em;
}

span.span-password {
  position: absolute;
  right: 5%;
}

.selectErrotxt {
  color: #ef858f !important;
}

.selectErro {
  border-bottom: 2px solid #ef858f !important;
}

/*inicio midias */

.redesSociais {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  justify-content: end;
  gap: 3%;
  margin: 15px 15px;
}

.redesSociais img {
  height: 50px;
}

/*fim midias */

@media (min-width: 20em) {

  /* 360px */
  ul.redesSociais {
    padding-left: 0;
    width: 320px;
    margin-left: -3rem
  }

  form#form-style {
    width: 90%;
    margin: auto;
  }

  img.img-icon {
    width: 90%;
    margin: auto;
  }

  input.bton.login {
    min-width: 10.5em;
  }

  .container {
    max-width: 100%;
    margin: auto;
  }

  form.form-style {
    top: 0;
    position: relative;
  }

  span.span-password {
    position: absolute;
    right: 10%;
    margin-top: 1em;
  }

  .mostra-eye {
    position: absolute;
    right: 10%;
    margin-top: 2.5em;
  }

  span.mostra-eye img {
    height: 2em;
  }

  span.span-password {
    position: absolute;
    right: 10%;
    margin-top: 5%;
  }

}

@media (min-width: 37em) {

  /* 592px */
  form#form-style {
    width: 100%;
    margin: auto;
  }
}

@media (min-width: 992px) {
  ul.redesSociais {
    padding-left: 0;
    width: 620px;
    margin-left: -3rem
  }


}
</style>
