<template>
  <b-modal id="modalRelatorio" title="Download Relatório" hide-footer>
    <b-form @submit.prevent="baixarRelatorio" class="form-inline">
      <div class="form-row">
        <div class="form-group col-md-6">
          <label for="data-inic-input">Data Inicial</label>
          <b-form-datepicker id="data-inic-input" v-model="dataInic" required></b-form-datepicker>
        </div>

        <div class="form-group col-md-6">
          <label for="data-fim-input">Data Final</label>
          <b-form-datepicker id="data-fim-input" v-model="dataFim" required></b-form-datepicker>
        </div>
      </div>

      <div class="form-group col-md-12">
        <label for="selecao" :style="{ 'margin-top': '3vh' }">Selecione um grupo:</label>
        <b-form-select v-model="grupoSelecionado" :options="grupos" :style="{ 'width': '100%' }"></b-form-select>
      </div>

      <div class="form-group col-md-12">
        <label for="lista-nomes"
          :style="{ 'margin-top': '3vh', 'margin-bottom': '1vh', 'font-size': '1.3em', 'width': '100%' }"
          class='font-weight-bold'>Campos do Relatório</label>

        <b-row :style="{ 'margin-bottom': '3vh' }">
          <b-col md="6" v-for="(nome, index) in nomes" :key="index">
            <b-form-checkbox :value="nome" v-model="nomesSelecionados">
              <span :style="{ 'text-align': 'left', 'width': '100%' }">{{ nome }}</span>
            </b-form-checkbox>
          </b-col>
        </b-row>
      </div>

      <b-button type="submit" variant="primary">Download</b-button>
    </b-form>
  </b-modal>
</template>


<script>
export default {
  props: {
    grupos: {
      type: Array,
      required: true,
    },
  },
  data() {

    const formatDateFim = (date) => {
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, '0');
      const day = String(date.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    };

    const formatDateInic = (date) => {
      const currentDate = new Date();
      currentDate.setMonth(currentDate.getMonth() - 1);
      const year = currentDate.getFullYear();
      const month = String(currentDate.getMonth() + 1).padStart(2, '0');
      const day = String(currentDate.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    };

    return {
      urlApi: ambientes.ativo[0].url + ":" + ambientes.ativo[0].porta_api,
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret,
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      ambiente: this.$store.state.variaveisAmbiente.ambiente,
      dataInic: formatDateInic(new Date()),
      dataFim: formatDateFim(new Date()),
      nomes: ['Alarmes', /* 'acessos', */ 'Atividades'],
      nomesSelecionados: [],
      grupoSelecionado: '',
      grupos_dlp: [],
      enderecoDigitado: '',

    };
  },
  methods: {



    async baixarRelatorio() {
      await Promise.all(this.nomesSelecionados.map(async tabela => {

        if (tabela === 'Alarmes') {

          let payload = {
            "tabela": "alarmes",
            "select": `grupo_dlp like '${this.grupoSelecionado}%' and momento_criacao between '${this.dataInic} 00:00:00' and '${this.dataFim} 23:59:59' ^ order by data_evento desc`,
            "pagina": "1",
            "qt": "10000000",
            "campos_select_end_point": "id_alarme^tipo_alarme^status_alarme^data_evento^user_name",
            "get_qt": "0"
          };

          let urlDados = this.locationUrl + "/sql/selectSql/" + this.token + "/resp_rest"

          try {
            const dadosRelatorio = await this.$http.post(urlDados, payload);
            console.log("Gerando relatório");


            let urlRelatorio = this.urlApi + '/api/relatoriosalarmes/criar/' + this.grupoSelecionado + "/" + this.token;

            try {
              const nomeRelatorio = await this.$http.post(urlRelatorio, dadosRelatorio.body);

              const urlDownload = this.locationUrl + "/relatorioDlp/" + nomeRelatorio.bodyText;
              window.open(urlDownload, '_blank');

            } catch (err) {
              console.error("Erro na requisição do Relatorio");
              console.log(err);
            }

          } catch (err) {
            console.error("Erro na requisição do Relatorio");
            console.log(err);
          }


        } else if (tabela === 'Atividades') {

          let payload = {
            "tabela": "atividades",
            "select": `grupo_dlp like '${this.grupoSelecionado}%' and dth_inicio between '${this.dataInic} 00:00:00' and '${this.dataFim} 23:59:59' ^ order by dth_inicio desc`,
            "pagina": "1",
            "qt": "10000000",
            "campos_select_end_point": "maquina^executavel^dth_ult_atividade^user_name",
            "get_qt": "0"
          };

          let urlDados = this.locationUrl + "/sql/selectSql/" + this.token + "/resp_rest"

          try {
            const dadosRelatorio = await this.$http.post(urlDados, payload);
            console.log("Gerando relatório");

            let urlRelatorio = this.urlApi + '/api/relatoriosatividades/criar/' + this.grupoSelecionado + "/" + this.token;

            try {
              const nomeRelatorio = await this.$http.post(urlRelatorio, dadosRelatorio.body);

              const urlDownload = this.locationUrl + "/relatorioDlp/" + nomeRelatorio.bodyText;
              window.open(urlDownload, '_blank');

            } catch (err) {
              console.error("Erro na requisição do Relatorio");
              console.log(err);
            }

          } catch (err) {
            console.error("Erro na requisição do Relatorio");
            console.log(err);
          }



        } else {
          console.error("Selecione pelo menos uma tabela");
        }
      }));
    }

  },
};
</script>