<template>
   <!-- <a class="header-menu-tab nav-alerta alerta font-weight-light header-menu-tab"> -->
      <b-nav-item-dropdown right class="perfil-menu">
        <!-- Using 'button-content' slot -->
        <template v-slot:button-content>
          <b-icon icon="person-circle" font-scale="1.65" variant="white"></b-icon>
          <span class="user-label">{{username}}</span>
          <!-- <img class="foto-combo" v-bind:src="foto.url" v-bind:alt="foto.titulo" height="30" width="32" style="border-radius:50%;"> -->
        </template>
          <!-- <b-dropdown-item>
            <router-link to="/conta" class="link-router-novo">Conta</router-link>
          </b-dropdown-item> -->
        <b-dropdown-item @click="redireciona"> 
          <router-link id="sair" to="" class="link-router-novo"><b-icon icon="x"></b-icon> Sair</router-link>
        </b-dropdown-item>
      </b-nav-item-dropdown>
  
   <!-- </a> -->
</template>


<script>
import img from '../../../assets/img/sync-rotate-reload-refresh-icon.png';
import foto from '../../../assets/img/user.png';
//import { mapActions } from 'vuex'
import { routes }  from '../../../routes';
const CryptoJS = require("crypto-js")

export default {
  data() {
    return {
      foto: {
        routes,
          url: foto,
          titulo: 'foto user'
      },
      locationUrl:'',
      username: ''
    }
  },
  created(){
    this.locationUrl = this.$store.state.variaveisAmbiente.locationUrl;
    
  },  

  methods: {
    //...mapActions(['ActionSignOut']),
    redireciona(){
      this.$store.state.variaveisAmbiente.memoriaMaquinasRede = [];
      this.$store.state.variaveisAmbiente.total = 0;
      this.$store.state.variaveisAmbiente.ativo = 0;
      this.$store.state.variaveisAmbiente.inativo = 0;

      var token =JSON.parse(localStorage.getItem('user')).keyAcesssSecret;
      var url = `${this.$store.state.variaveisAmbiente.locationUrl}/dpo/acesso/sessao/${token}/resp_rest`;
      this.$http.post(url)
     .then(response => response.text())
      //.then(result => this.ActionSignOut(),localStorage.removeItem('user'))
      .then(result => this.$store.dispatch('ActionSignOut'),localStorage.removeItem('user'))
      .catch(()=> console.log('Sessão encerrada'))   //.catch(error => console.log('error', error));  
      this.$router.push({name:'login'})
      localStorage.clear()
    },

  },
  mounted() {
    var hashTk = localStorage.getItem('user');
    hashTk = JSON.parse(hashTk);
    hashTk = hashTk.keyAcesssSecret
    //console.log(hashTk);
    var hashDec = CryptoJS.enc.Base64.parse(hashTk)
    var hashDec = hashDec.toString(CryptoJS.enc.Utf8);
        hashDec = hashDec.split("-")
    this.username = hashDec[0]
  }

}

</script>


<style scoped>
span.user-label {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  text-transform: lowercase;
  margin: 0 5px 7px;
  font-weight: 300;
  font-size: .8em;
  color: #FFF;
}
a.header-menu-tab.nav-alerta.alerta.font-weight-light.header-menu-tab {
    text-align: center;
    width: 90%;
    margin: auto;
}
.navbar-dark .navbar-nav .nav-link {
    color: rgba(255, 255, 255, 0.5);
    margin-left: 1em;
}
@media (min-width: 360px){
   a.header-menu-tab.nav-alerta.alerta.font-weight-light.header-menu-tab[data-v-42317f2b] {
    text-align: center;
    width: 60%;
    margin: auto;
  }
}
@media (min-width: 992px){
  .perfil-menu ul.dropdown-menu.dropdown-menu-right{
    position: static !important;

  }
  a.header-menu-tab.nav-alerta.alerta.font-weight-light.header-menu-tab {
    position: relative;
    top: -5px;
  }
}
</style>
