
export default {
    /*9- traz o status e a informação a ser alterada */
    'CHANGE_VARIAVELSISTEMA'(state, payload){
        state.variavelSistema = payload
    },
    'CHANGE_VARIAVELWATCHER'(state, payload){
        state.variavelWatcher = payload
    },
    'CHANGE_VARIAVELWATCHERFILTER'(state, payload){
        state.variavelWatcherFilter = payload
    },
    'CHANGE_SET_USER'(state, payload){
        state.user = payload
    },
    'CHANGE_SET_TOKEN'(state, payload){
        state.token = payload
    },    
    'CHANGE_SET_INFO_ALERTA'(state, payload){
        state.infoAlerta = payload
    },      

}