﻿import Vue from 'vue'
import App from './App.vue'

import VueResource from 'vue-resource';
import VueRouter from 'vue-router';
import { routes } from './routes';

import { BootstrapVue, BootstrapVueIcons } from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import 'bootstrap-vue/dist/bootstrap-vue-icons.min.css'

import { library } from '@fortawesome/fontawesome-svg-core'
import { faUserSecret } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

import VueSweetalert2 from 'vue-sweetalert2';
import 'sweetalert2/dist/sweetalert2.min.css';

import store from './store/store'
import beforeEach from './beforeEach'
import IdleVue from "idle-vue";
import { CarouselPlugin } from 'bootstrap-vue'
import VueCryptojs from 'vue-cryptojs'
import JsonCSV from 'vue-json-csv'
import JsonExcel from "vue-json-excel";
import VueMask from 'v-mask'
Vue.use(VueMask)

Vue.use(VueCryptojs)

Vue.use(CarouselPlugin)

Vue.use(VueSweetalert2);

Vue.component('downloadCsv', JsonCSV)
Vue.component("downloadXls", JsonExcel);



const options = {
  confirmButtonColor: '#41b882',
  cancelButtonColor: '#ff7674',
};

library.add(faUserSecret)
Vue.component('font-awesome-icon', FontAwesomeIcon)
Vue.config.productionTip = false
Vue.use(BootstrapVue)
Vue.use(BootstrapVueIcons)

Vue.use(require('vue-moment'));

Vue.use(VueRouter);
export const router = new VueRouter({
  //mode: 'history',
  routes
});
router.beforeEach(beforeEach);
Vue.use(VueResource);


router.beforeEach(beforeEach);
const eventsHub = new Vue()
Vue.use(IdleVue, {
  eventEmitter: eventsHub,
  store,
  //idleTime: 1800000, // 30 minutos,
  // idleTime: 30 * 60 * 1000, // 30 minutos,
  idleTime: 60 * 60 * 1000, // 60 minutos,
  startAtIdle: false
});

new Vue({
  el: '#app',
  store,
  router,
  beforeEach,
  render: h => h(App)
})

/* FICA OBSERVANDO SEMPRE QUE A PAGINA E RECARREGADA */
window.addEventListener('beforeunload', function(event) {
  console.log(localStorage.getItem('dataLogin'));

  if((new Date().getMinutes() - parseInt(localStorage.getItem('dataLogin'))) >= localStorage.getItem('limiteSessao')) {
    localStorage.removeItem('infoUser');
    localStorage.removeItem('user');
    localStorage.removeItem('dataLogin');
  }

}, false);
