<template>
    <div>
      <!-- {{!!modulo}} {{!!dados}} {{indice}} {{!!subMenu}} -->

         <!--
        <b-dropdown-item-button 
          :disabled="habilitaMenu()" 
          @click="setSubMenu('chat', dadosMaq, indice)"
          v-if="chkModulo('chat')"
        >
          Chat
        </b-dropdown-item-button>-->


        <b-dropdown-item-button 
          @click="setSubMenu('sms', dadosMaq, indice)"
          v-if="chkModulo('sms')"
          hidden
        >
          SMS
        </b-dropdown-item-button>

        <b-dropdown-item-button 
          @click="setSubMenu('email', dadosMaq, indice)"
          v-if="chkModulo('email')"
          hidden
        >
          E-mail
        </b-dropdown-item-button>
        

        <b-dropdown-divider hidden></b-dropdown-divider>
        

        <b-dropdown-item-button 
          @click="setSubMenu('versaoDlp', dadosMaq, indice)"
          v-if="chkModulo('versaoDlp')"
          hidden
        >
          Versão DLP
        </b-dropdown-item-button> 

        
        <b-dropdown-item-button 
          :disabled="habilitaMenu()" 
          @click="setSubMenu('desliga', dadosMaq, indice)"
          v-if="chkModulo('desliga')"
          hidden
        >
          Desliga Computador
        </b-dropdown-item-button>
        <b-dropdown-item-button 
          :disabled="habilitaMenu()" 
          @click="setSubMenu('reinicia', dadosMaq, indice)"
          v-if="chkModulo('reinicia')"
          hidden
        >
          Reincia Computador
        </b-dropdown-item-button>
        <b-dropdown-item-button 
          :disabled="habilitaMenu()" 
          @click="setSubMenu('reiniciaDlp', dadosMaq, indice)"
          v-if="chkModulo('reiniciaDlp')"
        >
          Reinicia o DLP
        </b-dropdown-item-button>

        <b-dropdown-item-button 
          :disabled="habilitaMenu() || chkApp()" 
          @click="setSubMenu('paraApp', dadosMaq, indice)"
          v-if="chkModulo('paraApp')"
          hidden
        >
          Para Aplicação
        </b-dropdown-item-button>
        <b-dropdown-item-button 
          :disabled="habilitaMenu()" 
          @click="setSubMenu('exeCmd', dadosMaq, indice)"
          v-if="chkModulo('exeCmd')"
          hidden
        >
          Execução de Comandos
        </b-dropdown-item-button>
        
        
        <template>
          <sms :dados="getItens" :idx="indice"></sms>
          <chat :dados="getItens" :idx="indice"></chat>
          <email :dados="getItens" :idx="indice"></email>
          <versao-dlp :dados="getItens" :idx="indice"></versao-dlp>
          <desliga-maquina :dados="getItens" :idx="indice" @carregaDados="getCarregaDados()"></desliga-maquina>
          <reinicia-maquina :dados="getItens" :idx="indice" @carregaDados="getCarregaDados()"></reinicia-maquina>
          <reinicia-dlp :dados="getItens" :idx="indice" @carregaDados="getCarregaDados()"></reinicia-dlp>
          <para-app :dados="getItens" :idx="indice" @carregaDados="getCarregaDados()"></para-app>
          <exe-cmd :dados="getItens" :idx="indice"></exe-cmd>
        </template>
    </div>
</template>
<script>
import sms from './sms'
import chat from './chat'
import email from './email'
import versaoDlp from './versaoDlp'
import desligaMaquina from './desligaMaquina'
import reiniciaMaquina from './reiniciaMaquina'
import reiniciaDlp from './reiniciaDlp'
import paraApp from './paraApp'
import exeCmd from './exec'
export default {
  components: {
    'chat':chat,
    'sms': sms,
    'email':email,
    'versao-dlp':versaoDlp,
    'desliga-maquina': desligaMaquina,
    'reinicia-maquina': reiniciaMaquina,
    'reinicia-dlp': reiniciaDlp,
    'para-app': paraApp,
    'exe-cmd':exeCmd
  },
  props: ['modulo','dados', 'indice', 'subMenu'],
  data() {
    return {
      modMaq: this.modulo,
      dadosMaq: this.dados,
      arrMenu: this.subMenu,
      // arrMenu: {
      //   redes: ['sms', 'chat', 'email', 'versaoDlp', 'desliga', 'reinicia', 'reiniciaDlp', 'exeCmd'],
      //   alertas: ['sms', 'chat', 'email', 'desliga', 'reinicia', 'reiniciaDlp', 'paraApp', 'exeCmd'],
      //   monitoramento: ['sms', 'chat', 'email', 'desliga', 'reinicia', 'reiniciaDlp', 'paraApp', 'exeCmd'],
      //   aplicacoes: ['sms', 'chat', 'email', 'desliga', 'reinicia', 'reiniciaDlp', 'paraApp', 'exeCmd'],
      //   atividades: ['sms', 'chat', 'email', 'desliga', 'reinicia', 'reiniciaDlp', 'paraApp', 'exeCmd'],
      // },
      getItens: []
    }
  },
  watch: {
    dados(d) {
      this.dadosMaq = d
    },
    indice(i) {
      this.idxMaq = i
    },
    modulo(m) {
      this.modMaq = m
    }
  },
  methods: {
    habilitaMenu(){
      return this.dadosMaq.maq_ativa == 'false' ? true : false
    },
    chkApp() {
      return this.dadosMaq.terminada === 1
    },
    chkModulo(m){
      let menu = m
      return this.subMenu ? this.subMenu.indexOf(menu) > -1 : false;
    },
    setSubMenu(menu, dados, idx){
     
      this.$bvModal.show(menu + idx)

      this.getItens = dados
      console.log(this.getItens)
      console.log(menu + idx)
    },
    
    getCarregaDados() {
      this.$emit('carregaDados')
    }
  },
  mounted(){
  }
}
</script>
<style>
.row {
  -webkit-box-orient: initial !important;
  flex-direction: inherit !important;
}
</style>