    /* DE ACORDO COM O USUARIO LOGADO, CAPTURA O SEU GRUPO DLP APARTIR DO TOKEN DO USUARIO SALVO DENTRO DO LOCAL STORAGE DO BROWSER (BANCO DE DADOS EM CACHE) */
    getGrupoDLP() {
      var hashTk = localStorage.getItem('user');
      hashTk = JSON.parse(hashTk);
      hashTk = hashTk.keyAcesssSecret
      var hashDec = this.$CryptoJS.enc.Base64.parse(hashTk)
      var hashDec = hashDec.toString(this.$CryptoJS.enc.Utf8);
      hashDec = hashDec.split("^").reverse()
      let gdlp = hashDec[0]
      gdlp = gdlp.replace("*", "");
      return gdlp
    },

    /* GERAR O ARRAY DE OPCOES A SEREM PESQUISADOS DOS GRUPOS DLP DISPONIVEIS AO USUARIO LOGADO */
    async trazGrupoDlpUsuario() {
      let url = this.locationUrl + '/diversos/getGrupos/' + this.token + '/resp_rest' // CRIA A URL PARA REALIZAR A REQUISICAO NA BASE DE DADOS
      await this.$http.get(url, myHeaders)
        .then(res => {
          let listaSeparada = res.data.split(",")
          let listaUnicos = [... new Set(listaSeparada)]
          this.grupos_dlp = listaUnicos;
        })
        .catch(function (err) {
          console.log(err);
        })
    },

    /* CARREGA ENDPOINT COM OS DADOS PARA O DASHBOARD */
    async getDadosDashboard() {

      this.validaLoader.alertas = false;
      this.loaderCheck = true;

      /* FORÇA O RECARREGAMENTO DE TODOS OS GRAFICOS */
      this.alertaAppGraph.labels = 0;
      this.alertaTipoGraph.labels = 0;
      this.alertaStatusGraph.labels = 0;
      this.alertaMaqGraph.labels = 0;
      this.alertaDataGraph.labels = 0;
      this.validaLoader.qtdAlertas = 0;

      let payload = {
        "grupo_dlp": this.grupoSelecionado,
        "data_inicial": `${this.dataInicial} 00:00:00`,
        "data_final": `${this.dataFinal} 23:59:59`
      }

      let url = this.locationUrl + '/dashBoard/getDashBoard/' + this.token + '/resp_rest';

      await this.$http.post(url, payload)
        .then(res => {

          let dados = res.data;

          dados.lista_maquinas = dados.lista_maquinas.split(",")

          let tratamentoListaExecutaveis = dados.lista_executaveis;
          let listaExecutaveisArray = tratamentoListaExecutaveis.split(',')
          dados.lista_executaveis = listaExecutaveisArray.reduce((acc, item) => {
            const [nome, quantidade] = item.split('^');
            acc[nome] = parseInt(quantidade, 10);
            return acc;
          }, {});

          let tratamentoTiposAlarmes = dados.tipos_alarmes;
          let tiposAlarmesArray = tratamentoTiposAlarmes.split(',')
          dados.tipos_alarmes = tiposAlarmesArray.reduce((acc, item) => {
            const [nome, quantidade] = item.split('^');
            acc[nome] = parseInt(quantidade, 10);
            return acc;
          }, {});

          let tratamentoStatusAlarmes = dados.status_alarmes;
          let statusAlarmesArray = tratamentoStatusAlarmes.split(',')
          dados.status_alarmes = statusAlarmesArray.reduce((acc, item) => {
            const [nome, quantidade] = item.split('^');
            acc[nome] = parseInt(quantidade, 10);
            return acc;
          }, {});

          let tratamentoAlarmesMaquina = dados.alarmes_maquina;
          let alarmesMaquinaArray = tratamentoAlarmesMaquina.split(',')
          dados.alarmes_maquina = alarmesMaquinaArray.reduce((acc, item) => {
            const [nome, quantidade] = item.split('^');
            acc[nome] = parseInt(quantidade, 10);
            return acc;
          }, {});

          let tratamentoAlarmesDia = dados.alarmes_dia;
          let alarmesDiaArray = tratamentoAlarmesDia.split(',')
          dados.alarmes_dia = alarmesDiaArray.reduce((acc, item) => {
            const [nome, quantidade] = item.split('^');
            acc[nome] = parseInt(quantidade, 10);
            return acc;
          }, {});

          console.log(dados)

          this.validaLoader.qtdAlertas = dados.total_alarmes;
          this.maquinas.ativas = dados.maquinas_ativas;
          this.maquinas.total = dados.maquinas_total;
          this.alertaMaqUnico = dados.alarmes_maquina;
          this.alertaMaqUnico2 = dados.lista_maquinas;
          this.alertaAppUnico = dados.lista_executaveis;
          this.alertaTipoUnico = dados.tipos_alarmes;
          this.alertaStatusUnico = dados.status_alarmes;
          this.alertaDataUnico = dados.alarmes_dia;

          this.loaderCheck = false;

        })
        .catch(err => {
          console.log('Ocorreu um erro ao consumir o endpoint ' + url);
        })

    },

    graphApp() {
      let item = []
      let valor = []
      let i = 0
      let color = []
      let conta = 0
      let limite = 9

      const dados = this.alertaAppUnico

      for (const [nome, quantidade] of Object.entries(dados)) {

        if (i < limite) {
          item.push(nome);
          valor.push(quantidade);
          color.push(this.geraCor(i + 1))
        } else {
          conta += quantidade;
        }
        i++
      }

      item.push('OUTROS')
      valor.push(conta)
      color.push('#555555')

      this.alertaAppGraph = {
        labels: item,
        datasets: [{
          label: "Quant. Alertas: ",
          backgroundColor: color,
          data: valor
        }]
      }
    },

