    //FAZ O DOWNLOAD DO ARQUIVO DO RELATÓRIO DA PASTA PUBLIC
    async getGerarDownload() {

      let payload = {
        "grupo_dlp": this.grupoSelecionado,
        "data_inic": this.dataInicial,
        "data_fim": this.dataFinal,
      }

      let url = this.locationUrl + '/sql/selectCsv/' + this.token + '/resp_rest';

      await this.$http.post(url, payload)
        .then(res => {
          const arqRelatorio = res.data.arquivo;
          const publicoIndex = arqRelatorio.indexOf('publico');
          const resultString = arqRelatorio.substring(publicoIndex + 'publico'.length);

          const urlDownload = this.locationUrl + resultString;
          window.open(urlDownload, '_blank');
        })
        .catch(err => {
          console.error("Erro na requisição do Relatorio");
          console.log(err)
        })
    },

