    /* ACREDITO QUE ESSE METODO SEJA RESPOSAVEL POR TRAZER OS USUARIOS DO SISTEMA */
    async getUsuarios() {
      this.validaLoader.usuarios = false
      let url = this.locationUrl + '/dpo/login/todos/' + this.token + '/resp_rest';
      await this.$http.get(url)
        .then(res => {
          this.validaLoader.usuarios = true
          this.carregaLoader()
          var dados = res.data


          !!dados ? this.usuarios = dados.length : this.usuarios = 0;
        })
        .catch(err => {
          this.validaLoader.usuarios = true
          this.carregaLoader()
          console.log(err)
        })
    },

