<template>
<div>
  <b-modal ok-only :ref="`dispRemove${idx}`" :id="`dispRemove${idx}`" size="md" no-close-on-backdrop no-close-on-esc>
    <template #modal-title>
      Remove Dispositivo:
    </template>
    <div class="container">
      <div class="row">
        <div class="col-12 pb-3">
          <span>Grupo DLP / Código de Máquina:</span><br>
          <strong class="text-uppercase">{{`${dadosDisp.grupo_dlp} / ${dadosDisp.maquina}`}}</strong>
        </div>
        <div class="col-12  pb-3">
          <b-form-group
            label="Confirmação de Remoção:"
            label-for="dispRemove"
            class="mb-0"
          >
            <b-form-checkbox
              id="dispRemove"
              class="disp-remove"
              button-variant="danger"
              v-model="valida.remove"
              value="true"
              unchecked-value="false"
              switch
            >
              Você tem certeza que deseja remover o dispositivo de código <strong>{{dadosDisp.codigo_dispositivo}}</strong>?
            </b-form-checkbox>
          </b-form-group>
        </div>
      </div>
      <b-alert :variant="resposta.status == 1 ? 'success' : 'warning'" show v-if="resposta.status !== 0" dismissible>
        <b-icon :icon="resposta.status == 1 ? 'check-circle-fill' : 'exclamation-circle-fill'" font-scale="1.2" class="mr-2"></b-icon>
        {{resposta.texto}}
      </b-alert>
    </div>
    <template #modal-footer>
      <b-icon icon="arrow-clockwise" animation="spin" variant="primary" v-if="loaderCheck" font-scale="2"></b-icon>
      <b-button-group>
        <b-button variant="warning" style="color: white" @click="removeDispositivo()" :disabled="!verificaRemove()">
          <b-icon icon="trash-fill" class="mr-1"></b-icon>
           Remover
        </b-button>
        <b-button variant="danger" @click="fechaModal()">
          <b-icon icon="x" class="mr-1"></b-icon> Cancelar
        </b-button>
      </b-button-group>
    </template>
  </b-modal>
  </div>
</template>

<script>
var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Content-Type", "text/plain");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
  props: ['dados', 'idx', 'id_dispositivo'],
  data() {
    return {
      // variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      loaderCheck: false,
      // vairáveis do componente
      modal: false,
      dadosDisp: this.dados,
      // validações de campos
      valida: {
        remove: null,
      },
      // resposta do endpoint
      resposta: {
        status: 0,
        texto: ''
      }
    }
  },
  watch: {
    modalDesliga(vlr) {
      this.modal = vlr
    },
    dados(d) {
      this.dadosDisp = d
    }
  },
  methods: {

    verificaRemove() {
      return this.valida.remove === "true"
    },
    removeDispositivo() {
      this.loaderCheck = true
      let param = JSON.stringify({"tabela":"dispositivos","select":"codigo_dispositivo='" + this.dadosDisp.codigo_dispositivo + "'"})
      let url = `${this.locationUrl}/sql/deleteSql/${this.token}/resp_rest`;
      this.$http.post(url, param, myHeaders)
      .then(res => {
        this.loaderCheck = false
        !!res ? true : false ;
        this.respostasEnvio(true)
        this.valida.remove = false
      })
      .catch(err => {
        this.loaderCheck = false
        !!err ? true : false ;
        this.respostasEnvio(false)
      })
    },
    respostasEnvio(resp) {
      if(!!resp){
        this.resposta = {
          status: 1,
          texto: `O dispositivo "${this.dadosDisp.codigo_dispositivo}" foi removido com sucesso!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
        setTimeout(() => {
          this.fechaModal()
          this.$emit('carregaDados')
        }, 3600);
      } else {
        this.resposta = {
          status: 2,
          texto: `Falha ao remover o dispositivo "${this.dadosDisp.codigo_dispositivo}"!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
      }
    },

    // modal __________________________________________________________________
    fechaModal() {
        this.$refs[`dispRemove${this.idx}`].hide()
      this.valida.remove = false
    },
    abreModal(){
      this.$refs[`dispRemove${this.idx}`].hide()
    }
  },
  mounted(){
      this.fechaModal()
  }
}
</script>

<style>
.row {
  -webkit-box-orient: initial !important;
  flex-direction: inherit !important;
}
.disp-remove > .custom-control-input:checked ~ .custom-control-label::before {
    color: #fff;
    border-color: #dc3545 !important;
    background-color: #dc3545 !important;
    outline: none !important
}
.disp-remove > .custom-control-input:focus:not(:checked) ~ .custom-control-label::before {
    border-color: #adb5bd !important;
}
.disp-remove > .custom-control-input:focus ~ .custom-control-label::before {
    box-shadow: 0 0 0 0.2rem rgb(200 35 51 / 25%);
}
.disp-remove > .custom-control-input:not(:disabled):active ~ .custom-control-label::before {
    color: #fff;
    background-color: rgba(200,35,51,0.15);
    border-color: rgba(200,35,51,0.15);
}
</style>