<template>
    <b-navbar class="e-header" toggleable="lg" type="dark" variant="info" style="background-color: rgb(76, 122, 178) !important">
<menu-lateral></menu-lateral>
    <!--<img :src="foto.url" style="max-width: 30px" class="mr-2" alt="FlashSafe Watcher">-->
    <img :src="foto.url" style="max-width: 45px" class="mr-2 mt-2" alt="FlashSafe Watcher">
    <ul class="ul_titulo_page">
      <li class="titulo_page">{{titulo}}</li>
    </ul>   
    <div class="frase maquina">
      <h6 class="mb-0">{{fraseMaquina}}</h6>
    </div>

    <b-collapse id="nav-collapse" is-nav>
      <b-navbar-nav class="navbar-nav-two">
        <btnNotification style="visibility: hidden;"></btnNotification>
        <buttonConfiguracoes></buttonConfiguracoes>
      </b-navbar-nav>
    </b-collapse>
  <b-button variant="transparente" class="text-white px-1 justify-content-end outline-none" @click="voltaPagina()" title="Voltar à Página Anterior"><b-icon icon="chevron-left" font-scale="1"></b-icon></b-button>

  </b-navbar>

</template>

<script>
//import iconeFlash from '../../../assets/img/flashIcon2.png';
import iconeFlash from '../../../assets/img/vertical_PB_APSF.svg';
import foot2 from '../../../assets/img/user.png';
import btnNotification from '../../shared/buttonNotification/ButtonNotification';
import buttonConfiguracoes from '../../shared/buttonConfiguracoes/buttonConfiguracoes';
import Menu from '../../shared/menu/Menu'
import { routes }  from '../../../routes';
export default {
  props:[
    'titulo', 
    'numeroJanelasTotal', 
    'numeroJanelasPermitidas',
    'numeroJanelasProibidas',
    'numeroJanelasDesconhecidas',
    'numeroJanelasOcorrencias',
    'fraseMaquina'
  ],
  components:{
    'btnNotification': btnNotification,
    'buttonConfiguracoes':buttonConfiguracoes,
    'menu-lateral':Menu
  },
  data() {
      return {
        routes,
        foto: {
          url: iconeFlash,
          titulo: 'logo flash'
        },
        foto2: {
          url: foot2,
          titulo: 'foto user'
        },
        filter: null,
      }
  },
  methods: {
    voltaPagina() {
      this.$router.go(-1)
    },
    alteraFiltro(valor){
      const payload = {
        filterWatcher: valor,
      }
      this.$store.commit('CHANGE_VARIAVELWATCHERFILTER', payload)
    },
    doIt(val){
      this.filter = val;
      this.alteraFiltro(this.filter);
    }, 
  }

}
</script>

<style scoped>
  @import '../../../assets/styles/header_estilo.css';
   :root {
  --margin: 3px;
}
h6 {
    text-transform: initial;
}
body {
  padding: 10px;
}

.container-box {
  display: flex;
  flex-wrap: wrap;
}

.a {
  width: calc((100% / 3) - (var(--margin) * 2));
}

.b {
  width: calc((100% / 5) - (var(--margin) * 2));
}

.box {
  /*background-color: #9009a0;*/
  border-radius: 5px;
  margin: var(--margin);
  height: 30px;
}
.numero:hover,.numero:active {
    background-color: #4c7ab2;
    color:white;
    max-width:10em;
    border-radius: 0.5em;
}
ul, ul.ul_titulo_page {
  margin-bottom: 0;
  text-transform: uppercase;
}
.frase.maquina {
    float: right;
    margin-left: 2em;
    color: white;
}
.outline-none {
  outline: none !important;
}
.btn:focus, .btn.focus {
  box-shadow: none !important;
}
</style>


