<template>
<div>
  <b-modal ok-only ref="plvRemove" id="plvRemove" size="md" no-close-on-backdrop no-close-on-esc>
    <template #modal-title>
      Remove Palavra Sensível:
    </template>
    <div class="container">
      <div class="row">
        <div class="col-12 pb-3">
          <span>Palavra:</span><br>
          <strong class="text-uppercase">{{dadosPalavra.palavra}}</strong>
        </div>
        <div class="col-6 pb-3">
          <span>Grupo DLP:</span><br>
          <strong class="text-uppercase">{{dadosPalavra.grupo_dlp}}</strong>
        </div>
        <div class="col-6 pb-3">
          <span>Gravidade:</span><br>
          <strong class="text-uppercase">{{dadosPalavra.gravidade}}</strong>
        </div>
        <div class="col-12  pb-3">
          <b-form-group
            label="Confirmação de Remoção:"
            class="mb-0"
          >
            <b-form-checkbox
              class="plv-remove"
              button-variant="danger"
              v-model="valida.remove"
              value="true"
              unchecked-value="false"
              switch
            >
              Você tem certeza que deseja remover a palavra <strong>{{dadosPalavra.palavra}}</strong>?
            </b-form-checkbox>
          </b-form-group>
        </div>
      </div>
      <b-alert :variant="resposta.status == 1 ? 'success' : 'warning'" show v-if="resposta.status !== 0" dismissible>
        <b-icon :icon="resposta.status == 1 ? 'check-circle-fill' : 'exclamation-circle-fill'" font-scale="1.2" class="mr-2"></b-icon>
        {{resposta.texto}}
      </b-alert>
    </div>
    <template #modal-footer>
      <b-icon icon="arrow-clockwise" animation="spin" variant="primary" v-if="loaderCheck" font-scale="2"></b-icon>
      <b-button-group>
        <b-button variant="warning" style="color: white" @click="removePalavra()" :disabled="!verificaRemove()">
          <b-icon icon="trash-fill" class="mr-1"></b-icon>
           Remover
        </b-button>
        <b-button variant="danger" @click="fechaModal()">
          <b-icon icon="x" class="mr-1"></b-icon> Cancelar
        </b-button>
      </b-button-group>
    </template>
  </b-modal>
  </div>
</template>

<script>

const urlApi = ambientes.ativo[0].url + ":" + ambientes.ativo[0].porta_api


var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Content-Type", "text/plain");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
  props: ['dados'],
  data() {
    return {
      // variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      ambiente: this.$store.state.variaveisAmbiente.ambiente,
      loaderCheck: false,
      // vairáveis do componente
      modal: false,
      dadosPalavra: this.dados,
      // validações de campos
      valida: {
        remove: null,
      },
      // resposta do endpoint
      resposta: {
        status: 0,
        texto: ''
      }
    }
  },
  watch: {
    modalDesliga(vlr) {
      this.modal = vlr
    },
    dados(d) {
      this.dadosPalavra = d
    }
  },
  methods: {

    verificaRemove() {
      return this.valida.remove === "true"
    },
    /* EXCLUI UMA PALAVRA DA BASE DE DADOS */
    /*removePalavra() {
      this.loaderCheck = true
      let param = JSON.stringify({
        "tabela":"palavras",
        "select":"palavra='" + this.dadosPalavra.palavra + "'"
      })
      let url = `${this.locationUrl}/sql/deleteSql/${this.token}/resp_rest`;
      this.$http.post(url, param, myHeaders)
      .then(res => {
        this.loaderCheck = false
        !!res ? true : false ;
        this.respostasEnvio(true)
        this.valida.remove = false
      })
      .catch(err => {
        this.loaderCheck = false
        !!err ? true : false ;
        this.respostasEnvio(false)
      })
    },*/
    removePalavra() {
      this.loaderCheck = true

      let param = {
        "palavras": this.dadosPalavra.palavra
      }
      //let ambiente = 'POCNEW';
      let url = `${urlApi}/api/palflash/deleteporpalavra/${this.getGrupoDpl().replace('*','')}/${this.ambiente}/${this.token}`;
      
      this.$http.put(url, param, myHeaders)
      .then(res => {
        this.loaderCheck = false
        !!res ? true : false ;
        this.respostasEnvio(true)
        this.valida.remove = false
      })
      .catch(err => {
        this.loaderCheck = false
        !!err ? true : false ;
        this.respostasEnvio(false)
      })
    },
    getGrupoDpl() {
      let tk = window.atob(this.token)
      console.log('MEU TOKEN CONVERTIDO');
      console.log(window.atob(this.token));
      tk = tk.split('^')
      return tk[1]
    },
    /* RETORNA UMA RESPOSTA AO USUARIO SEJA ELA POSITIVA OU NEGATIVA */
    respostasEnvio(resp) {
      if(!!resp){
        this.resposta = {
          status: 1,
          texto: `A palavra "${this.dadosPalavra.palavra}" foi removida com sucesso!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
        setTimeout(() => {
          this.fechaModal()
          this.$emit('carregaDados')
        }, 3600);
      } else {
        this.resposta = {
          status: 2,
          texto: `Falha ao remover a palavra "${this.dadosPalavra.palavra}"!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
      }
    },
    fechaModal() {
        this.$refs['plvRemove'].hide()
      this.valida.remove = false
    },
    abreModal(){
      this.$refs['plvRemove'].hide()
    }
  }
}
</script>

<style>
.row {
  -webkit-box-orient: initial !important;
  flex-direction: inherit !important;
}
.plv-remove > .custom-control-input:checked ~ .custom-control-label::before {
    color: #fff;
    border-color: #dc3545 !important;
    background-color: #dc3545 !important;
    outline: none !important
}
.plv-remove > .custom-control-input:focus:not(:checked) ~ .custom-control-label::before {
    border-color: #adb5bd !important;
}
.plv-remove > .custom-control-input:focus ~ .custom-control-label::before {
    box-shadow: 0 0 0 0.2rem rgb(200 35 51 / 25%);
}
.plv-remove > .custom-control-input:not(:disabled):active ~ .custom-control-label::before {
    color: #fff;
    background-color: rgba(200,35,51,0.15);
    border-color: rgba(200,35,51,0.15);
}
</style>
