<template>
  <b-modal v-model="modal" ok-only ref="user-adiciona" id="user-adiciona" size="lg" hide-header-close no-close-on-backdrop no-close-on-esc>
    <template #modal-title>
      Adicionar Usuário:
    </template>
    <div class="container">
      <div class="row">

        <!-- 
        <div class="col-sm-12 col-md-12 col-lg-4 pb-3">
          <b-form-group
            label="Nº de Inscrição/Matrícula"
            label-for="addId"
            description="Somente números"
            class="mb-0"
          >
            <b-form-input
              id="addId"
              v-model="dadosUser.login_idLogin"
              placeholder="Coloque a identificação"
              type="number"
              @keyup="verificaId(dadosUser.login_idLogin)"
              @blur="verificaId(dadosUser.login_idLogin)"
              :state="valida.id"
              maxlength="10"
            ></b-form-input>
          </b-form-group>
        </div>
        -->

        <div class="col-sm-12 col-md-12 col-lg-4 pb-3">
          <b-form-group
            label="E-mail"
            label-for="addUsuario"
            description="Caracteres permitidos: ([-_.], [a-z], [A-Z] e [0-9])"
            class="mb-0"
          >
            <b-form-input
              id="addUsuario"
              v-model="dadosUser.usernameLogin"
              placeholder="E-mail do usuário"
              @keyup="verificaUsuario(dadosUser.usernameLogin)"
              @blur="verificaUsuario(dadosUser.usernameLogin)"
              :state="valida.usuario"
              maxlength="50"
            ></b-form-input>
          </b-form-group>
        </div>
        
        <div class="col-sm-12 col-md-12 col-lg-4 pb-3">
          <b-form-group
            label="Confirme seu e-mail"
            label-for="addEmail"
            description="Caracteres permitidos ([-_.], [a-z], [A-Z] e [0-9])"
            class="mb-0"
          >
            <b-form-input
              id="addEmail"
              v-model="dadosUser.emailLogin"
              placeholder="Confirme seu e-mail"
              @keyup="verificaEmail(dadosUser.emailLogin)"
              @blur="verificaEmail(dadosUser.emailLogin)"
              :state="verificaEmailRep()"
              maxlength="50"
            ></b-form-input>
          </b-form-group>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-4 pb-3">
          <b-form-group
            label="Empresa"
            label-for="addEmpresa"
            description="Caracteres permitidos ([*], [a-z] e [0-9])"
            class="mb-0"
          >
            <b-form-input
              id="addEmpresa"
              v-model="dadosUser.empresa"
              placeholder="Identicação empresa"
              @keyup="verificaEmpresa(dadosUser.empresa)"
              @blur="verificaEmpresa(dadosUser.empresa)"
              :disabled="userLogado.grupoDlp !== '*'"
              maxlength="30"
            ></b-form-input>
          </b-form-group>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-4 pb-3">
          <b-form-group
            label="Departamento"
            label-for="addDepartamento"
            description="Caracteres permitidos ([*], [a-z] e [0-9])"
            class="mb-0"
          >
            <b-form-input
              id="addDepartamento"
              v-model="dadosUser.dpto"
              placeholder="Identicação deparamento!"
              @keyup="verificaDpto(dadosUser.dpto)"
              @blur="verificaDpto(dadosUser.dpto)"
              :disabled="!dadosUser.empresa || dadosUser.empresa === '*' || userLogado.grupoDlp.indexOf('*') < 0"
              maxlength="20"
            ></b-form-input>
          </b-form-group>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-4 pb-3">
          <b-form-group
            label="Grupo DLP"
            label-for="addGrupo"
            description="Resultado de empresa e depart."
            class="mb-0"
          >
            <b-form-input
              id="addGrupo"
              v-model="dadosUser.grupoDlp"
              placeholder="Grupo DLP gerado"
              disabled
              :state="valida.grupoDlp"
            ></b-form-input>
          </b-form-group>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-6 pb-3">
          <b-form-group
            label="Senha"
            label-for="addSenha"
            class="mb-0"
          >
            <b-form-input
              class="senha"
              id="addSenha"
              maxlength="40"
              autocomplete="off"
              :type="!verSenha.senha ? 'password' : 'text'"
              placeholder="Digite sua senha"
              v-model="dadosUser.senhanova" dadosUser.senharep
              :state="valida.senha"
              @keyup="verificaSenha(dadosUser.senhanova)"
              @blur="verificaSenha(dadosUser.senhanova)"
            ></b-form-input>
            <b-icon class="verSenha" font-scale="1.5" variant="secondary" :icon="!verSenha.senha ?'eye-fill' : 'eye-slash-fill'" @click="verSenha.senha = !verSenha.senha"></b-icon>
          </b-form-group>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-6 pb-3">
          <b-form-group
            label="Repita a Senha"
            label-for="addRepSenha"
            class="mb-0"
          >
            <b-form-input
              class="senha"
              id="addRepSenha"
              maxlength="40"
              autocomplete="nope" 
              :type="!verSenha.repete ? 'password' : 'text'"
              placeholder="Digite novamente sua senha"
              v-model="dadosUser.senharep"
              :state="valida.repete"
              @keyup="verificaSenhaRep(dadosUser.senharep)"
              @blur="verificaSenhaRep(dadosUser.senharep)"
            ></b-form-input>
            <b-icon class="verSenha" font-scale="1.5" variant="secondary" :icon="!verSenha.repete?'eye-fill' : 'eye-slash-fill'" @click="verSenha.repete = !verSenha.repete"></b-icon>
          </b-form-group>
        </div>
      </div>
      <div class="row">
        <div class="col-sm-12 col-md-12 col-lg-6 pb-3">
          Qualidade da Senha: 
          <div class="gadget">
            <small class="status">{{valSenha.txt}}</small>
            <div class="mark" :style="{left: [valSenha.val+'%']}"></div>
          </div>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-6 pb-3">
          <small>
            Verifique se a senha contem pelo menos uma letra maiúscula, uma letra minúscula, um caractere especial, um número e no mínimo 8 posições digitadas.<br>
          </small>
        </div>
      </div>
      <div class="row">
      </div>
      <b-alert :variant="resposta.status == 1 ? 'success' : 'warning'" show v-if="resposta.status !== 0" dismissible>
        <b-icon :icon="resposta.status == 1 ? 'check-circle-fill' : 'exclamation-circle-fill'" font-scale="1.2" class="mr-2"></b-icon>
        {{resposta.texto}}
      </b-alert>
    </div>
    <template #modal-footer>
      <b-icon icon="arrow-clockwise" animation="spin" variant="primary" v-if="loaderCheck" font-scale="2"></b-icon>
      <b-button-group>
        <b-button variant="success" @click="salvarUser()" :disabled="!verificaSalvar()">
          <b-icon icon="check" class="mr-1"></b-icon> Salvar
        </b-button>
        <b-button variant="danger" @click="fechaModal('user-adiciona')">
          <b-icon icon="x" class="mr-1"></b-icon> Cancelar
        </b-button>
      </b-button-group>
    </template>
  </b-modal>
</template>

<script>
var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Content-Type", "text/plain");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
  props: ['modalUsuario'],
  data() {
    return {
      // variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      loaderCheck: false,
      userLogado: {
        grupoDlp: '',
        user: ''
      },
      // vairáveis do componente
      modal: !!this.modalUsuario ? true : false ,
      dadosUser: {
        "login_idLogin":"",
        "usernameLogin": "",
        "emailLogin": "",
        "permissoesLogin":"adm",
        "senhanova": "",
        "senharep": "",
        "dpo_idLogin": "",
        "grupoDlp": "",
        "empresa": "",
        "dpto":""
      },

      // validações de campos
      valida: {
        usuario: null,
        email: null,
        grupoDlp: null,
        senha: null,
        repete: null,
        emailIgual: null,
      },
      verSenha: {
        senha: false,
        repete: false
      },
      valSenha: {
        val: 0,
        txt: ''
      },
      // resposta do endpoint
      resposta: {
        status: 0,
        texto: ''
      }
    }
  },
  watch: {
    modalUsuario(vlr) {
      this.modal = vlr
    },
    dados(d) {
      this.dados = d
    }
  },
  methods: {
  
    
    verificaEmailRep(){
      let emailIgual = this.dadosUser.emailLogin === this.dadosUser.usernameLogin ? true : false ;
      return emailIgual;
    },
    

    descSenha(s) {
      return window.atob(s)
    },

    verificaSalvar() {
      //!!this.valida.id  ||||REMOVIDO PARA ENVIAR O USUARIO COMO EMAIL E O ID COMO NULL
      return   !!this.valida.usuario && !!this.valida.email && !!this.valida.grupoDlp && !!this.valida.senha && !!this.valida.repete && !!this.verificaEmailRep()
    },

    /*
    verificaId(i){
      var num = !i ? 0 : i;
      num = num < 0 ? 0 : num;
      num = num > 2000000000 ? 2000000000 : num;
      num = parseInt(num)
      this.dadosUser.login_idLogin =  num
      this.valida.id = Number.isInteger(num)

    },
    */

    /*
    verificaUsuario(u) {
      var user = u.replace(/[`´~!@#$%^&*()|+\=?;:'"¨, <>\{\}\[\]\\\/àáâãäÀÁÂÃÄèéêëÈÉÊËìíîïÌÍÎÏóòôõöÓÒÔÕÖúùûüÚÙÛÜçÇñÑ]/gi, '');
      this.valida.usuario = !!user ? true : false; 
      this.dadosUser.usernameLogin = user
    },
    */

    verificaUsuario(e) {
      //let reg = /^[-a-z0-9_}{\'?]+(\.[-a-z0-9_}{\'?]+)*@([a-z0-9][-a-z0-9]*(\.[-a-z0-9]+)*\.(com|edu|gov|mil|net|[a-z][a-z])|([0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}))(:[0-9]{1,5})?$/ig;
      let reg = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/;
      let usuario = e
      if(!!usuario){
        let result = usuario.search(reg)
        result == -1 ? this.valida.usuario = false : this.valida.usuario = true;
      } else {
        this.valida.usuario = null
      }
    },

    verificaEmail(e) {
      let reg = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/;
      let email = e
      if(!!email){
        let result = email.search(reg)
        result == -1 ? this.valida.email = false : this.valida.email = true;
      } else {
        this.valida.email = null
      }
    },

    verificaEmpresa(e){
      var emp = e.replace(/[`´~!@#$\%^&()|_\+\-\=?;:'"¨., <>\{\}\[\]\\\/àáâãäÀÁÂÃÄèéêëÈÉÊËìíîïÌÍÎÏóòôõöÓÒÔÕÖúùûüÚÙÛÜçÇñÑ]/gi, '');
      emp = emp.toLowerCase()
      emp.indexOf(' ') != -1 ? emp = emp.replaceAll(' ', '') : emp = emp;
      this.dadosUser.empresa = emp
      if(emp.length > 1 && emp.indexOf('*') > -1 ){
          this.dadosUser.empresa = emp.replace('*', '')

        this.dadosUser.dpto = '*';
      }
      emp === '*' || !emp ? this.dadosUser.dpto = '' : this.dadosUser.dpto = this.dadosUser.dpto;
      this.dadosUser.grupoDlp = this.verificaGrupoDLPEdit();
    },

    verificaDpto(d){
      var dep = d.replace(/[`´~!@#$\%^&()|_\+\-\=?;:'"¨., <>\{\}\[\]\\\/àáâãäÀÁÂÃÄèéêëÈÉÊËìíîïÌÍÎÏóòôõöÓÒÔÕÖúùûüÚÙÛÜçÇñÑ]/gi, '');
      dep = dep.toLowerCase()
      this.dadosUser.dpto = dep
      
      if(dep.length > 1 && dep.indexOf('*') > -1 ){
          this.dadosUser.dpto = dep.replace('*', '')
      }
      this.dadosUser.grupoDlp = this.verificaGrupoDLPEdit();
    },

    verificaGrupoDLPEdit() {
      if(this.dadosUser.empresa == '' && this.dadosUser.dpto == '') {
        this.valida.grupoDlp = false
      } else if(this.dadosUser.empresa === '*'){
        this.valida.grupoDlp = true
        return this.dadosUser.empresa;
      } else if(!!this.dadosUser.empresa && this.dadosUser.dpto === '*') {
        this.valida.grupoDlp = true
        return this.dadosUser.empresa+this.dadosUser.dpto
      } else if(!!this.dadosUser.empresa && !!this.dadosUser.dpto) {
        this.valida.grupoDlp = true
        return this.dadosUser.empresa+'_'+this.dadosUser.dpto
      } else if(this.dadosUser.empresa.indexOf('*') > -1 && !this.dadosUser.dpto){
        this.valida.grupoDlp = true
        var emp = this.dadosUser.empresa.split('*')
        return !!emp[1] ? emp[0]+emp[1]+'*' : emp[0]+'*';
      } else if(this.dadosUser.empresa.indexOf('*') < 0 && !this.dadosUser.dpto) {
        this.valida.grupoDlp = true
        return this.dadosUser.empresa;
      } else if(!this.dadosUser.empresa){
        this.valida.grupoDlp = true
        this.dadosUser.dpto = ''
      } else {
        this.valida.grupoDlp = false
      }
    },

    verificaSenha(s) {
      let num = /(?=.*\d)/
      let min = /(?=.*[a-z])/
      let mai = /(?=.*[A-Z])/
      let esp = /(?=.*[$*&@#%!?+-=:;\(\)\{\}\<\>\\\?])/
      let tam = /[0-9a-zA-Z$*&@#%!?+-=:;\(\)\{\}\<\>\\\?)]{8,}$/
      let reg =/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[$*&@#%!?+-=:;\(\)\{\}\<\>\\\?])[0-9a-zA-Z$*&@#%!?+-=:;\(\)\{\}\<\>\\\?)]{8,}$/
      let conta = 0
      let chk = num.test(s) && min.test(s) && mai.test(s) && esp.test(s) && tam.test(s)
      conta += num.test(s) ? 12 : 0 ;
      conta += min.test(s) ? 12 : 0 ;
      conta += mai.test(s) ? 12 : 0 ;
      conta += esp.test(s) ? 12 : 0 ;
      conta += tam.test(s) ? 12 : 0 ;
      conta += s.length > 11 && chk  ? 20 : 0;
      conta += s.length > 15 && chk ? 20 : 0;
      this.valSenha.val = conta
      if(conta >= 100) {
        this.valSenha.txt = "Excelente"
      } else if(conta < 100 && conta >= 80) {
        this.valSenha.txt = "Ótimo"
      } else if(conta < 80 && conta >= 60) { 
        this.valSenha.txt = "Bom"
      } else if(conta < 60 && conta >= 40) {
        this.valSenha.txt = "Regular"
        } else if(conta < 40 && conta >= 20) {
        this.valSenha.txt = "Fraco"
      } else if(conta < 20 && conta >= 0) {
        this.valSenha.txt = "Ruim"
      } else {
        this.valSenha.txt = 'Força da senha'
      }
      let senha = reg.test(s)
      this.valida.senha = senha
      this.dadosUser.senhanova = s
      this.valida.repete = false
    },

    verificaSenhaRep(s){
      this.valida.repete = this.setCrpPsw(this.dadosUser.senhanova) === this.setCrpPsw(s) ? true : false;
      this.dadosUser.senharep = s
    },

    async salvarUser() {
      this.loaderCheck = true
      let psw = this.setCrpPsw(this.dadosUser.senhanova)
      let param = JSON.stringify({
        "idDpo": null ,
        "nomeDpo": this.dadosUser.usernameLogin,
        "emailDpo":this.dadosUser.emailLogin,
        "tipoPerfilDpo":1,
        "hash_senhaLogin":psw,
        "grupoDlp":this.dadosUser.grupoDlp 
      });
      let url = `${this.locationUrl}/dpo/perfil/novo/${this.token}/resp_rest`
      await this.$http.post(url, param, myHeaders)
      .then(res => {
        this.loaderCheck = false
        !!res ? true : false ;
        this.salvaLogin()
      })
      .catch(err => {
        this.loaderCheck = false
        !!err ? true : false ;
        this.respostasEnvio(false)
      })
    },

    async salvaLogin() {
      this.loaderCheck = true
      let psw =  this.setCrpPsw(this.dadosUser.senhanova)
      let param2 = {
        "login_idLogin": null ,
        "usernameLogin": this.dadosUser.usernameLogin,
        "emailLogin":this.dadosUser.emailLogin,
        "permissoesLogin": "adm",
        "hash_senhaLogin": psw,
        "dpo_idLogin": null ,
        "grupoDlp":this.dadosUser.grupoDlp
      }
      let url = `${this.locationUrl}/dpo/login/novo/${this.token}/resp_rest`;
      await this.$http.post(url, param2, myHeaders)
      .then(res => {
        this.loaderCheck = false
        !!res ? true : false ;
        this.respostasEnvio(true)
      })
      .catch(err => {
        this.loaderCheck = false
        !!err ? true : false ;
        this.respostasEnvio(false)  
      })
    },

    respostasEnvio(resp) {
      if(!!resp){
        this.resposta = {
          status: 1,
          texto: `Usuário "${this.dadosUser.usernameLogin}" foi criado com sucesso!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3000);
        setTimeout(() => {
          this.fechaModal()
        }, 3000);
      } else {
        this.resposta = {
          status: 2,
          texto: `Falha ao criar do usuário "${this.dadosUser.usernameLogin}"!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3000);
      }
    },

    setCrpPsw(psw){
      var psw = psw
      psw = this.$CryptoJS.SHA256(psw).toString()
      return psw
    },

    fechaModal() {
      this.$emit('limpaModal', 'user-adiciona')
    },

    userLog(){
      let dados = window.atob(this.token)
      let user = dados.split('-')
      this.userLogado.user = user[0]
      let grupo = dados.split('^')
      this.userLogado.grupoDlp = grupo[1]

      if(this.userLogado.grupoDlp !== '*') { 
        if(this.userLogado.grupoDlp.indexOf('*') > -1) {
          let grupo = this.userLogado.grupoDlp.replace('*', '')
          this.dadosUser.empresa =  grupo
          this.dadosUser.dpto =  ''
          this.dadosUser.grupoDlp = this.userLogado.grupoDlp
        } else {
          let grupo = this.userLogado.grupoDlp.split('_')
          this.dadosUser.empresa =  grupo[0]
          this.dadosUser.dpto =  grupo[1]
          this.dadosUser.grupoDlp = this.userLogado.grupoDlp
          this.valida.grupoDlp = true
        }
      }
    }
  },

  mounted(){
    this.userLog()
    this.$refs['user-adiciona'].hide()
  }
}
</script>

<style>
.row {
  -webkit-box-orient: initial !important;
  flex-direction: inherit !important;
}
.gadget {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 5px 0;
  width: 100%;
  /* max-width: 300px; */
  height: 38px;
  border-radius: 5px;
  background: #ff3232;
  background: -moz-linear-gradient(left,  #ff3232 0%, #ff9000 25%, #ffff00 50%, #2ff000 75%, #0f9000 100%);
  background: -webkit-linear-gradient(left,  #ff3232 0%,#ff9000 25%,#ffff00 50%,#2ff000 75%,#0f9000 100%);
  background: linear-gradient(to right,  #ff3232 0%,#ff9000 25%,#ffff00 50%,#2ff000 75%,#0f9000 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff3232', endColorstr='#0f9000',GradientType=1 );
}
.gadget > .mark {
  position: absolute;
  display: block;
  width: 20px;
  height: 20px;
  top: 30px;
  transform: rotate(45deg);
  transition: .5s;
  margin-left: -10px;
  left: 0;
  box-shadow: #000 1px 1px 3px;
  z-index: 2;
}
.gadget .status {
  position: relative;
  display: block;
  width: 100%;
  margin-top: -2px;
  color: #FFF;
  background-color: rgba(0,0,0,0.15);
  text-shadow: #000 1px 1px 5px;
  text-align: center;
  text-transform: uppercase;
}
.senha {
  width: calc(100% - 30px) !important;
}
.verSenha {
    position: absolute;
    display: block;
    right: 15px;
    top: 40px;
    cursor:pointer;
}
</style>