import Header from '../../shared/header/Header'
import Menu from '../../shared/menu/Menu'
import Loader from '../../shared/loader/loader'
import dadosToken from '../../shared/dadosToken/dadosToken.js'

import modalAdiciona from './modal-adiciona'
import modalEditaUsuarios from './modal-edita'
import modalRemoveUsuarios from './modal-remove'

// parametrização de Configurações
const paramConfig = require('../../../assets/apis/param.json'); 

// mockup do endpoint
const configServer = require('../../../assets/apis/api-config-server.json'); 

var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
/* REALIZA A INJEÇÃO DE DEPENDENCIA */
  components: {
    'meu-painel': Header,
    'menu-lateral': Menu,
    'modal-adiciona': modalAdiciona,
    'modal-edita': modalEditaUsuarios,
    'modal-remove': modalRemoveUsuarios,
    'loader': Loader
  },
  data() {
    return {
      //variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      loaderCheck: true,

      chkToken: dadosToken.getToken(),
      chkPermissao: paramConfig.permissaoDlp,
      editCampo: -1,
      ultVerifServer: '',
      //seleciona maquina
      dadosParam: '',
      dadosTemp: {},
      modalParam: {
        adiciona: false,
        edita: false,
        remove: false
      },
      listaParam: [], // ARMAZENA A LISTA DE PARAMETROS PARA QUE POSSA SER CONSUMIDA POSTARIORMENTE
      tabTitulo: 'Lista de Parâmetros',
      evidencias: [],
      imgFull: '',
      chkIndex: null,
      estatisticas: {
        total: 0,
      },
      btnSel: {
        total: false,
      },
      resposta: {
        status: 0,
        texto: ''
      }
    }
  },
  watch: {

  },
  methods: {
    horaFormat(t) {
      var tempo = new Date(t).toString()
      tempo = tempo.split(' GMT')
      tempo = tempo[0].split(' ').reverse()
      tempo = tempo[0]
      return tempo 
    },

    formatDate(d) {
      let tempo = d
      if(tempo != 'NULL'){
        tempo = tempo.split(' ')
        let hora = tempo[1]
        let data = tempo[0].split('-').reverse()
        data = data[0]+'/'+data[1]+'/'+data[2]
        var dataOk = `${data} - ${hora}`
        return dataOk
      } else {
        return 'Data Indefinida'
      }
    },

    formatDateMilisec(d) {
      let tempo = d
      let bGMT =  3 * 60 * 60 * 1000
      tempo = tempo - bGMT
      tempo = new Date(tempo)
      var dia = tempo.getDate()
      var mes = tempo.getMonth() + 1
      var ano = tempo.getFullYear()
      var hora = tempo.getUTCHours()
      var min = tempo.getMinutes()
      var seg = tempo.getSeconds()
      var dataOk = `${dia < 10 ? '0' + dia : dia}/${mes < 10 ? '0' + mes : mes}/${ano} - ${hora < 10 ? '0' + hora : hora}:${min < 10 ? '0' + min : min}:${seg < 10 ? '0' + seg : seg}`
      return dataOk
    },

    htmlFormat(txt){
      let  texto = txt
      texto = texto.replace(/=(\r\n|\n|\r)/gm , '<br>')
    },

    /* SOCILITA UM ACESSO AO BANCO DE DADO PARA QUE POSSA SER RETORNADO OS DADOS CONTIDOS EM JSON_CONFIGS REFERENTES A CONFIGURACAO DO PERFIL DA EMPRESA */
    /* ESSES DADOS SAO COLETADOS E INSERIDOS DENTRO DE UM OBJETO PARA QUE POSSA SER MANIPULADO POSTERIORMENTE */
    getParamServer() {
      this.loaderCheck = true
      this.listaParam = [] // ARMAZENA A RESPOSTA DO JSON
      let param = JSON.stringify({
        "tabela":"json_configs", 
        "select":"1=1", 
        "pagina":this.currentPage, // ESSE DADOS VEM NULOS
        "qt":this.perPage, // ESSE DADOS VEM NULOS
        "campos_select_end_point":"palavra^tipo^status^fonetico^grupo_dlp^dataCriacao^gravidade" // ESTA UTILIZANDO UM TEMPLATE DE JSON JA POPULADO
      })
      
      let url = this.locationUrl + '/sql/selectSql/'+this.token+'/resp_rest' // CRIA UMA URL DE VALIDACAO DE ACESSO E SOLICITA ACESSO A BASE DE DADOS
      console.log("QUERY: "+ param);
      console.log("URL: "+ url);
      console.log("Ocorrencias por pagina: "+ this.currentPage);
      console.log("Quantidade de itens por pagina: "+ this.perPage);

      /* TEM ALGUMA INFORMACAO SENDO PASSADA ERRADA AO SERVIDOS, POIS ESTA RETORNADO ERRO 400 (Bad Request) */
      /*this.$http.post(url, param, myHeaders)
      .then(res => {
        let resp = res.data
        console.log("Resposta:  "+ res.data);*/

        /* TRAZ OS DADOS DE UM JSON TEMPLATE */
        let resp = configServer[0] // alterar pela linha de cima
        //console.log("Configuracao Server: "+ configServer[0]);
        /* ================================= */

        /* NAO CONSEGUI ENTENDER PARA QUE SERVE */
        for(let key in resp) {
          let val = resp[key]
          val = typeof val === 'string' && val.indexOf(',') > -1 ? val.split(',') : val;
          val = typeof val === 'string' && !isNaN(val) ? parseInt(val) : val;
          val = typeof val === 'string' && val.indexOf('true') > -1 ? true : val;
          val = typeof val === 'string' && val.indexOf('false') > -1  ? false : val;
          resp[key] = val
        }

        this.listaParam = resp // INSERE A RESPOTA DENTRO DA LISTA DE PAREMETROS DE SISTEMA DA EMPRESA
        this.ultVerifServer = this.formatDateMilisec(Date.now())
        this.loaderCheck = false

      /*})
      .catch(err => {
        console.log(err)
        this.ultVerifServer = this.formatDateMilisec(Date.now())
        this.loaderCheck = false
      })*/


      //console.log("LISTA DE PARAMETROS: "+ this.listaParam)
      this.ultVerifServer = this.formatDateMilisec(Date.now())
    },

    /* VERIFICA QUAL O PRIVILEGIO DO USUARIO */
    checaArray(val) {
      return this.chkToken.grupoDlp === '*' ? true : this.chkPermissao.indexOf(val) < 0
    },

    getArray(arr) {
      return typeof arr === 'string' ? arr.split(',') : arr
    },

    /* HABILITA A EDICAO DO ITEM AO CLICAR NO BOTAO */
    /* RECEBENDO COMO PARAMETRO O INDEX DO ITEM A SER MODIFICADO COMPO A SER MODIFICADO DA LISTA DE PAREMTROS */
    habilitaEdicao(idx, dados) {
      console.log(idx)
      this.dadosTemp = dados
      this.editCampo = idx
    },

    /* DESABILITA A EDICAO DO COMPONENTE */
    cancelaEdicao() {
      this.listaParam[this.dadosTemp[0]] = this.dadosTemp[1] 
      this.editCampo = -1
    },

    /* RECEBE COMO PARAMETRO AS ALTERACOES REALIZADAS O CAMPO E O PARAMETRO NO QUAL REALIZAOU A ALTERACAO */
    /* ESSE PAREMTRO ESTA REALACIONADO COM O PARAMETRO CHAMADO ATRAVES DO METODO getParamServer() QUE ARMAZENA TODOS OS DADOS DE PAREMTROS DENTRO DE UM JSON */
    salvaEdicao(c, v) {
      const campo = c
      let valor = v
      valor = typeof valor === 'object' ? valor.join() : valor;
      valor = typeof valor === 'number' || typeof valor === 'boolean' ? valor.toString() : valor;
      this.loaderCheck = true
      let param = JSON.stringify({
        "tabela_end_point": "json_configs",
        "campos_select_end_point": "conteudo",
        "grupo_dlp": "*",
        "maquina": "SERVER",
        "descricao": campo,
        "conteudo": valor
      })
      let url = this.locationUrl + '/sql/updateSql/'+this.token+'/resp_rest' // SE CONSTROE E REQUISICAO PARA COMUNICAR COM O BANCO DE DADOS E SOLICITAR A ALTERACAO
      console.log(param)
      console.log(url)
      // this.$http.post(url, param, myHeaders)
      // .then(() => {
        this.loaderCheck = false
        this.respostasEnvio(1, `Parâmetro alterado com Sucesso!`)
      // })
      // .catch(() => {
      //   this.respostasEnvio(2, `Falha ao alterar o Parâmetro!`)
      //   this.loaderCheck = false
      // })

      this.loaderCheck = false
    },

    // modal __________________________________________________________________
    /* ABRE O MODAL APOS ALGUMA SOLICITACAO DE ALTERACAO */
    abreModal(ref, dados) {
      console.log(ref)
      console.log(dados)
      !!dados ? this.dadosParam = dados : '';
      this.modalParam = {
        adiciona: ref == 'modal-adiciona' ? true : false,
        edita:  ref == 'modal-edita' ? true : false,
        remove:  ref == 'modal-remove' ? true : false,
        senha:  ref == 'modal-senha' ? true : false
      }
      setTimeout(()=> {
        this.$bvModal.show(ref)
      },10)
    },

    /* FECHA O MODAL APOS ALGUMA SOLICITACAO DE ALTERACAO */
    fechaModal(ref) {
      this.$bvModal.hide(ref)
      this.getParamServer() // RECARREGA A LISTA DE PARAMETROS ALTERANDO QUALQUER ALTERACAO 
      setTimeout(()=>{
        this.dadosParam = ''
        this.modalParam = {
          adiciona: false,
          edita: false,
          remove: false,
          senha: false
        }
      },500)
    },

    // resposta __________________________________________________________________
    /* RECEBE COMO PAREMETRO 2 ITENS SENDO UM DELES O STATUS DA REQUISICAO E O OUTRO UM TEXTO PARA RESPOSTA */
    respostasEnvio(stt, resp) {
      console.log(stt)
      console.log(resp)
      this.resposta = {
        status: stt,
        texto: resp
      }
      setTimeout(() => {
        this.resposta = {status: 0, texto: ''}
        this.editCampo = -1
      }, 3000);
    },

  },

  /* AO INICIALIZAR O COMPONENTE TUDO QUE ESTA AQUI DENTRO E INICIALIZADO JUNTO */
  mounted() {
    this.getParamServer() // CRIAR A LISTA DE PARAMETROS PARA QUE POSSA SER CHAMADO NO FRONT
  }
  
}
