<template>
  <b-modal ok-only :ref="`versaoDlp${idx}`" :id="`versaoDlp${idx}`" size="md" no-close-on-backdrop no-close-on-esc>
    <template #modal-title>
      Versão do DLP:
    </template>
    <div class="container">
      <div class="row">
        <div class="col-12 pb-3">
          <span>Máquina:</span><br>
          <strong class="text-uppercase">{{`${dadosMaq.grupo_dlp}-${dadosMaq.computer_name}-${dadosMaq.cod_maquina}`}}</strong>
        </div>
        <div class="col-12 pb-3">
          <span>Versão:</span><br>
          <strong class="text-uppercase">{{chkVersaoDLP(dadosMaq.versoes)}}</strong>
        </div>
      </div>
    </div>
    <template #modal-footer>
      <b-button-group>
        <b-button variant="danger" @click="fechaModal()">
          <b-icon icon="x" class="mr-1"></b-icon> Fechar
        </b-button>
      </b-button-group>
    </template>
  </b-modal>
</template>
<script>
export default {
  props: ['dados', 'idx'],
  data() {
    return {
      dadosMaq: this.dados,
    }
  },
  watch: {
    dados(d) {
      this.dadosMaq = d
    },
  },
  methods: {
    chkVersaoDLP(vs){
      let versao = vs
      versao = !!versao ? versao.split('.jar').reverse() : [];
     // let versaoNew = versao[0];
      //alert(versao[0])
      let versaoDlp = versao[0].substring(14);
      //alert(versaoDlp)
      return versaoDlp
    },
    fechaModal() {
      this.$refs[`versaoDlp${this.idx}`].hide()
    },
    abreModal(){
      this.$refs[`versaoDlp${this.idx}`].show()
    }
  },
  mounted(){
    this.fechaModal()
  }
}
</script>
<style>
.row {
  -webkit-box-orient: initial !important;
  flex-direction: inherit !important;
}
</style>