import store from '../store/state'

export const userService = {
    checkUser,
    login,
    logout
};

function login(usernameLogin, hash_senhaLogin) {
    var url = `${store.variaveisAmbiente.locationUrl}/dpo/login/autentica/resp_rest`
    //console.log('endpoints: '+url)
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ usernameLogin, hash_senhaLogin })
    };
    return fetch(url , requestOptions)
        .then(handleResponse)
        .then(result => {
            // login bem-sucedido se houver um usuário na resposta
            if (result) {
                // armazena detalhes do usuário e credenciais de autenticação básica no armazenamento local
                // para manter o usuário logado entre as atualizações da página
                result.authdata = window.btoa(usernameLogin + ':' + hash_senhaLogin);
                localStorage.setItem('user', JSON.stringify(result));
                localStorage.setItem('dataLogin', new Date().getMinutes());
                localStorage.setItem('limiteSessao', 30);
            }else{
                result.authdata = result
            }

            return result;
        })
        
}

function checkUser(user){
    var requestOptions = {
        method: 'GET',
        redirect: 'follow'
      };
      
    return  fetch(`${store.variaveisAmbiente.locationUrl}/dpo/acesso/sessao/desloga/${user}/resp_rest`, requestOptions)
    .then(handleResponse)    
    .then(result => {
            // login bem-sucedido se houver um usuário na resposta
            if (result) {
                // armazena detalhes do usuário e credenciais de autenticação básica no armazenamento local
                // para manter o usuário logado entre as atualizações da página
                result = result
            }else{
                result = result
            }

            return result;
        })

}

function logout() {
    var requestOptions = {
        method: 'POST',
        redirect: 'follow'
      };

    // remove o usuário do armazenamento local para desconectar o usuário
    var token = sessionStorage.getItem('user');


    if(token){
        var url = `${store.variaveisAmbiente.locationUrl}/dpo/acesso/sessao/${token}/resp_rest`;
        return  fetch(url, requestOptions)
        .then(result => {
            if (result) {
                result.authdata = result
                localStorage.removeItem('user');
            }else{
                result.authdata = result
            }
    
            return result;
        })
    }
}

function handleResponse(response) {
    return response.text().then(text => {
        const data = text && JSON.parse(text)[0];
        if (!response.ok) {
            if (response.status === 403) {
                // auto logout if 401 response returned from api
                //logout();
                //location.reload(true);
            }

            //const error = (data && data.message) || response.statusText;
            return Promise.reject(data);
        }

        return data;
    });
}
