import Header from '../../shared/header/Header'
import Menu from '../../shared/menu/Menu'
import subMenu from '../../shared/subMenu/subMenu'
import subMenuDetalhes from '../../shared/subMenu/subMenuDetalhes'
import Loader from '../../shared/loader/loader'

const checkedNames = [];

var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
  components: {
  'meu-painel': Header,
  'menu-lateral':Menu,
  'sub-menu': subMenu,
  'subMenuDetalhes': subMenuDetalhes,
  'loader': Loader,
  
  },
  data() {
    return {
      //variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,

      memoriaMaquinasRede: this.$store.state.variaveisAmbiente.memoriaMaquinasRede,
      total: this.$store.state.variaveisAmbiente.total,
      ativo: this.$store.state.variaveisAmbiente.ativo,
      inativo: this.$store.state.variaveisAmbiente.inativo,
      
      loaderCheck: true,
      //Definição dos itens do sub-menu
      listaSubMenu: ['sms', 'email', 'versaoDlp', 'desliga', 'reinicia', 'reiniciaDlp', 'exeCmd'],
      // Recarrega pagina
      tempoAtividade: Date.now()  - (2 * 60 * 1000), // tempo de 10 min da ultima atividade 
      tempoAtualizacao: 10 * 1000, // // intervalo de tempo de verificação de enpoint
      ultVerifServer: '',
      modVersaoDLP: false,
      versaoDLP: {
        maquina: '',
        codigo: '',
        versao: ''
      },
      listaMaquinas: [],
      tabListaMaquinas: [],
      tabTitulo: 'Total',
      evidencias: [],
      imgFull: '',
      chkIndex: null,
      // modal
      modSelMaquina: false,
      getMaquinaState: '',
      getMaquinaStateCodMaquina: '',
      getMaquinaStateFull: '',
      getItems: [],
      titTabela: [
        { key: 'actions',           label: 'Ações'},
        { key: 'status',            label: 'Status',                            class: 'text-center'},
        { key: 'grupo_dlp',         label: 'Grupo DLP',         sortable: true, class: 'text-left'},
        { key: 'cod_maquina',       label: 'Código de máquina', sortable: true, class: 'text-left'},
        { key: 'computer_name',     label: 'Nome da máquina',   sortable: true, class: 'text-left'},
        { key: 'user_name',         label: 'Usuário',           sortable: true, class: 'text-left', sortDirection: 'desc'},
        { key: 'testeversao',     label: 'Versao',   sortable: true, class: 'text-left'},
        //{ key: 'cpu_maquina',       label: 'CPU Maq',           sortable: true, class: 'text-left'},
        //{ key: 'cpu_total',         label: 'CPU Total',         sortable: true, class: 'text-left'},
        //{ key: 'memoria_maquina',   label: 'Memoria Maq',       sortable: true, class: 'text-left'},
        //{ key: 'memoria_dlp',       label: 'Memoria DLP',       sortable: true, class: 'text-left'},
        //{ key: 'memoria_usada',     label: 'Memoria usada',     sortable: true, class: 'text-left'},
        { key: 'ultima_atividade',  label: 'Útima Atividade',   sortable: true, class: 'text-left'},
      ],
      estatisticas: {
        total: 0,
        ativo: 0,
        inativo: 0
      },
      btnSel: {
        total: false,
        permitidas: false,
        proibidas: false,
        desconhecidas: false,
        ocorrencias: false
      },
      totalRows: 1,
      currentPage: 1,
      perPage: 20,
      pageOptions: [10, 20, 50, 100],
      filter: null,
      infoModal: {
        id: 'info-modal',
        title: '',
        content: '',
      },
      text: '',
      xlsTitulo: {
        'Máquina Ativa': 'ativo',
        'Código da Máquina': 'cod_maquina',
        'Máquina': 'computer_name',
        'Discos': 'discos',
        'Última Atividade': 'ult_atividade',
        'Login na Máquina': 'user_name',
        'Grupo DLP': 'grupo_dlp',
        'Versão do DLP': 'versoes',
      },
      xlsDados: []
    }
  },
  computed: {
   
  
  
  },
  watch: {
    totalRows (t) {
      this.totalRows = t;
    },
    tabListaMaquinas (t) {
      this.geraXls(t)
    },
    listaMaquinas(l) {
      this.geraEstatisticas()
    },
    getItems(g){
      this.getItems = g
    }
  },
  methods: {
    geraXls(d){
      var dados = d;
      if(!!dados) {
        dados.forEach(obj => {
          obj.ativo = obj.maq_ativa === "true" ? 'Sim' : 'Não';
          var arrDisco = obj.discosArray;
          arrDisco = JSON.stringify(arrDisco);
          arrDisco = arrDisco.replaceAll('[', '').replaceAll(']', '').replaceAll('},{', '; \n').replaceAll('{', '').replaceAll('}', '').replaceAll('"', '');
          arrDisco = arrDisco.replaceAll('codigoDisco', 'Código de Disco');
          arrDisco = arrDisco.replaceAll('descricao', 'Disco');
          arrDisco = arrDisco.replaceAll('drive', 'Drive');
          obj.discos = arrDisco;
          var tempo = obj.ultima_atividade;
          var tempo = this.formatDateMilisec(tempo);
          obj.ult_atividade = tempo;
        })
        this.xlsDados = dados
      }
    },
    horaFormat(t) {
      var tempo = new Date(t).toString()
      tempo = tempo.split(' GMT')
      tempo = tempo[0].split(' ').reverse()
      tempo = tempo[0]
      return tempo 
    },
    formatDateMilisec(d) {
      let tempo = d
      let bGMT =  3 * 60 * 60 * 1000
      tempo = tempo - bGMT
      tempo = new Date(tempo)
      var dia = tempo.getDate()
      var mes = tempo.getMonth() + 1
      var ano = tempo.getFullYear()
      var hora = tempo.getUTCHours()
      var min = tempo.getMinutes()
      var seg = tempo.getSeconds()
      var dataOk = `${dia < 10 ? '0' + dia : dia}/${mes < 10 ? '0' + mes : mes}/${ano} - ${hora < 10 ? '0' + hora : hora}:${min < 10 ? '0' + min : min}:${seg < 10 ? '0' + seg : seg}`
      return dataOk
    },
    htmlFormat(txt){
      let  texto = txt
      texto = texto.replace(/=(\r\n|\n|\r)/gm , '<br>')
    },
    botaoSelecionado(btn) {
      this.btnSel = {
        total: false,
        permitidas: false,
        proibidas: false,
        desconhecidas: false,
        ocorrencias: false
      },
      this.btnSel[btn] = true
    },
    rowDisable(item, type) {
      if(!!item && item.maq_ativa !== null) {
        return item.maq_ativa === 'false' ? 'inativo' : 'ativo';
      }
    },
    chkVersaoDLP(maq, cod, vs){
      this.modVersaoDLP = true
      let versao = vs.split('.jar').reverse()
      this.versaoDLP = {
        maquina: maq,
        codigo: cod,
        versao: versao[1]
      }
    },

    //PEGA A LISTA DE MÁQUINAS DO BACK E IMPRIMI NA TABELA
    async getListaMaquina() {
      this.loaderCheck = true
      this.listaMaquinas = []
      let url = this.locationUrl + '/maquinas/listaSecret/'+this.token+'/resp_rest'

      //PEGA O GRUPO DLP DO TOKEN E ATRIBUI A VARIAVEL grupoToken
      let grupoToken = Buffer.from(this.token , 'base64').toString().split('^')[1];

      //SE O GRUPO FOR DIFERENTE DE * ELE TRATA O NOME DO GRUPO PARA REMOVER O *
      grupoToken !== '*' && (grupoToken = grupoToken.replace( "*" , "" ))
      //CONSOME A API
      await this.$http.get(url, myHeaders)
      .then(res => {
        this.loaderCheck = false
        let resp = res.data

        //  TRATAMENTO PARA REMOVER MÁQUINAS COM CÓDIGO QUEBRADO
        resp = resp.filter(resp => !/:|<|>|\(|\)|volume|unidade|drive/i.test(resp.cod_maquina)) 

        //VERIFICA SE O GRUPO ENVIADO PELO ENDPOINT FAZ PARTE DO grupoDlp OU SE O GRUPO É * - SE FOR VERDADEIRO RETORNA A LISTA COM OS GRUPOS
        let mostraDados = true;

              
        //CRIA UM INDEX AO ITEM DO RETORNO
        let cont = 0
        resp.forEach(obj => { obj.idx = cont++ });
        
        //PERCORRE O ARRAY COMPARANDO O GRUPO TOKEN COM O GRUPO DA MÁQUINA, CASO SEJA DIFERENTE ELE MUDA O MOSTRA DADOS PARA FALSE
        for (let i = 0; i < resp.length; i++) {
          // TRATA O GRUPOdlp PARA REMOVER O SUBGRUPO
          let grupoTratado = resp[i].grupo_dlp.split('_')[0];

          if (grupoTratado !== grupoToken ) {
            mostraDados = false;
            //break
          }  
        }

        //TRATAMENTO PARA O GRUPO * VISUALIZAR AS MAQUINAS
        if (grupoToken === "*") {
          mostraDados = true;
          console.log("Usuario * ")
        }

        //CONDICAO PARA O RETORNO DO LOOP ACIMA, SE FOR TRUE EXIBE A LISTA SE FOR FALSE EXIBE UMA LISTA VAZIA
        if(mostraDados) {
          this.listaMaquinas = resp
          this.geraEstatisticas()
          this.listaMaquinasTodas()
        } else {
          console.error('Problemas com o grupo do Token, entre em contato com o suporte')
          this.listaMaquinas = []
          this.geraEstatisticas()
          this.listaMaquinasTodas()
          }

        this.ultVerifServer = this.formatDateMilisec(Date.now())
      })
      .catch( (err) => {
        this.loaderCheck = false
        this.listaMaquinas = []
        this.listaMaquinasTodas()
        this.ultVerifServer = this.formatDateMilisec(Date.now())
        console.log(err)
      })
    },

    listaMaquinasTodas() {
      this.tabTitulo = "Todas as Máquinas"
      this.botaoSelecionado('total')
      this.tabListaMaquinas = this.listaMaquinas;
      this.totalRows = this.tabListaMaquinas.length;
    },

    listaMaquinasAtivas() {
      this.tabTitulo = "Máquinas com Atividades"
      this.botaoSelecionado('ativo')
      this.tabListaMaquinas = this.listaMaquinas.filter(obj => obj.maq_ativa == 'true')
      this.totalRows = this.tabListaMaquinas.length;
    },

    listaMaquinasInativas() {
      this.tabTitulo = "Maquinas sem Atividades "
      this.botaoSelecionado('inativo')
      this.tabListaMaquinas = this.listaMaquinas.filter(obj => obj.maq_ativa == 'false')
      this.totalRows = this.tabListaMaquinas.length;
    },

    geraEstatisticas() {
      this.estatisticas = { total: 0, ativo: 0, inativo: 0 },
      this.estatisticas.total = this.listaMaquinas.length
      this.listaMaquinas.forEach(maq => {
        if (maq.maq_ativa == 'true') { this.estatisticas.ativo++ }
        if (maq.maq_ativa === 'false') { this.estatisticas.inativo++ }
      });
      this.$store.state.variaveisAmbiente.total = this.estatisticas.total;
      this.$store.state.variaveisAmbiente.ativo = this.estatisticas.ativo;
      this.$store.state.variaveisAmbiente.inativo = this.estatisticas.inativo;
    },
    selDisco() {
      var e = document.getElementById("select-disco");
      var strUser = e.options[e.selectedIndex].value;
      this.$bvModal.show('modal-1');
    },
    abreModulo(link, maq, cod, gdlp){
      var link = '/' + link + '/' + gdlp + '/'
      var param = maq + '-' + cod
      this.$router.push(link + param)
    },
    getParaphase() {
      return this.token +'%'+ this.locationInfo
    },
    setCrpStorage(dados){
      var dados = dados
      dados = this.$CryptoJS.AES.encrypt(dados, this.getParaphase()).toString()
      localStorage.setItem('x-mon-maq', dados)
    },
    getCrpStorage() {
      var dados = localStorage.getItem('x-mon-maq')
      dados = !!dados ? this.$CryptoJS.AES.decrypt(dados, this.getParaphase()).toString(this.CryptoJS.enc.Utf8) : '';
      return dados
    },
    abreModal(ref) {
      this.$refs[ref].show()
    },
    fechaModal(ref) {
      this.$refs[ref].hide()
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
    },

    //ADICIONA MÁQUINA PARA LIMPAR OFFLINE
    adicionaMaquina(computerName) {
      var arr = checkedNames;

      if (arr.indexOf(computerName) === -1) {
        arr.push(computerName);
      } else {
        var index = arr.indexOf(computerName);
        arr.splice(index, 1);
      }

      console.log(arr)
    },

    //ENVIA MÁQUINAS OFFLINE PARA LIMPAR O FRONT
    limpaOffline() {
      const newCleanPC = '{"maquinas":"' + checkedNames + '"}';
      console.log(newCleanPC);
      let url = this.locationUrl + '/maquinas/retira/' + this.token + '/resp_rest';
      this.$http.post(url, newCleanPC);
      location.reload()
    }
  },

  mounted() {
    if(this.memoriaMaquinasRede.length > 0) {
      this.loaderCheck = false;
      this.tabListaMaquinas = this.memoriaMaquinasRede;

      this.estatisticas.total = this.total;
      this.estatisticas.ativo = this.ativo;
      this.estatisticas.inativo = this.inativo;

      this.ultVerifServer = this.formatDateMilisec(Date.now())

    }else {
      this.getListaMaquina();

    }
  },

}
