<template>
  <div class="loader" :style="{ display: [!!_props.status ? 'flex' : 'none'] }">
    <strong>Carregando aguarde...</strong>
    <div class="icone">
      <b-icon
        icon="arrow-clockwise"
        animation="spin"
        variant="primary"
        font-scale="10"
      ></b-icon>
    </div>
  </div>
</template>
<script>
export default {
  props: ["status"]
};
</script>
<style>
.loader {
  position: fixed;
  display: none;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  align-items: center;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.75);
  z-index: 9999999;
}

.loader .icone {
  position: absolute;
  margin: 0px 0 0 0px;
  -webkit-animation: spin 2s linear infinite;
  -moz-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}

@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.loader strong {
  position: absolute;
  margin: -60px 0 0 0px;
  top: 40%;
  font-size: 1.5rem;
}
</style>
