import Header from '../../shared/header/Header'
import Menu from '../../shared/menu/Menu'
import Loader from '../../shared/loader/loader'
import dadosToken from '../../shared/dadosToken/dadosToken.js'

import modalAdiciona from './modal-adiciona'
import modalEditaUsuarios from './modal-edita'
import modalRemoveUsuarios from './modal-remove'

// parametrização de Configurações
const paramConfig = require('../../../assets/apis/param.json'); 

// mockup do endpoint
const configServer = require('../../../assets/apis/api-config-default.json'); 

var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
  components: {
    'meu-painel': Header,
    'menu-lateral': Menu,
    'modal-adiciona': modalAdiciona,
    'modal-edita': modalEditaUsuarios,
    'modal-remove': modalRemoveUsuarios,
    'loader': Loader
  },
  data() {
    return {
      //variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      loaderCheck: true,

      chkToken: dadosToken.getToken(),
      chkPermissao: paramConfig.permissaoDlp,
      editCampo: -1,
      ultVerifServer: '',
      //seleciona maquina
      dadosParam: '',
      dadosTemp: {},
      modalParam: {
        adiciona: false,
        edita: false,
        remove: false
      },
      listaParam: [],
      tabTitulo: 'Configuração Padrão',
      evidencias: [],
      imgFull: '',
      chkIndex: null,
      estatisticas: {
        total: 0,
      },
      btnSel: {
        total: false,
      },
      resposta: {
        status: 0,
        texto: ''
      }
    }
  },
  watch: {

  },
  methods: {
    horaFormat(t) {
      var tempo = new Date(t).toString()
      tempo = tempo.split(' GMT')
      tempo = tempo[0].split(' ').reverse()
      tempo = tempo[0]
      return tempo 
    },
    formatDate(d) {
      let tempo = d
      if(tempo != 'NULL'){
        tempo = tempo.split(' ')
        let hora = tempo[1]
        let data = tempo[0].split('-').reverse()
        data = data[0]+'/'+data[1]+'/'+data[2]
        var dataOk = `${data} - ${hora}`
        return dataOk
      } else {
        return 'Data Indefinida'
      }
    },
    formatDateMilisec(d) {
      let tempo = d
      let bGMT =  3 * 60 * 60 * 1000
      tempo = tempo - bGMT
      tempo = new Date(tempo)
      var dia = tempo.getDate()
      var mes = tempo.getMonth() + 1
      var ano = tempo.getFullYear()
      var hora = tempo.getUTCHours()
      var min = tempo.getMinutes()
      var seg = tempo.getSeconds()
      var dataOk = `${dia < 10 ? '0' + dia : dia}/${mes < 10 ? '0' + mes : mes}/${ano} - ${hora < 10 ? '0' + hora : hora}:${min < 10 ? '0' + min : min}:${seg < 10 ? '0' + seg : seg}`
      return dataOk
    },
    htmlFormat(txt){
      let  texto = txt
      texto = texto.replace(/=(\r\n|\n|\r)/gm , '<br>')
    },
    getParamServer() {
      this.loaderCheck = true
      this.listaParam = []
      let param = JSON.stringify({
        "tabela":"json_configs", 
        "select":"1=1", 
        "pagina":this.currentPage, 
        "qt":this.perPage,
        "campos_select_end_point":"palavra^tipo^status^fonetico^grupo_dlp^dataCriacao^gravidade"
      })
      let url = this.locationUrl + '/sql/selectSql/'+this.token+'/resp_rest'
      console.log(param)
      console.log(url)
      // this.$http.post(url, param, myHeaders)
      // .then(res => {
        // let resp = res.data
        let resp = configServer // alterar pela linha de cima
        console.log(resp)
        resp = resp.find(obj => obj.indexOf('true') > -1 ? obj.replaceAll('"true"', true) : obj)
        resp = resp.find(obj => obj.indexOf('false') > -1 ? obj.replaceAll('"false"', false) : obj)

        // resp = JSON.stringify(resp).replaceAll('\\n', '')
        // console.log(resp)
        // resp = resp.indexOf('"true"') > -1 ? resp.replaceAll('"true"', true) : resp
        // resp = resp.indexOf('"false"') > -1 ? resp.replaceAll('"false"', false) : resp
        // resp = JSON.parse(resp)
        console.log(resp)

        this.listaParam.permitidas = resp.filter(obj => obj.tipo === 1 ? obj : '')
        // this.listaParam.proibidas = resp.filter(obj => obj.tipo === 2 ? obj : '')
        // this.listaParam.desconhecidas = resp.filter(obj => obj.tipo === 3 ? obj : '')


        // for(let key in resp) {
        //   let val = resp[key]
        //   val = typeof val === 'string' && val.indexOf(',') > -1 ? val.split(',') : val;
        //   val = typeof val === 'string' && !isNaN(val) ? parseInt(val) : val;
        //   val = typeof val === 'string' && val.indexOf('true') > -1 ? true : val;
        //   val = typeof val === 'string' && val.indexOf('false') > -1  ? false : val;
        //   resp[key] = val
        // }
        this.listaParam = resp
        this.ultVerifServer = this.formatDateMilisec(Date.now())
        this.loaderCheck = false
      // })
      // .catch(err => {
        // console.log(err)
        // this.ultVerifServer = this.formatDateMilisec(Date.now())
        // this.loaderCheck = false
      // })


      console.log(this.listaParam)
      this.ultVerifServer = this.formatDateMilisec(Date.now())
    },
    checaArray(val) {
      return this.chkToken.grupoDlp === '*' ? true : this.chkPermissao.indexOf(val) < 0
    },
    getArray(arr) {
      return typeof arr === 'string' ? arr.split(',') : arr
    },
    habilitaEdicao(idx, dados) {
      console.log(idx)
      this.dadosTemp = dados
      this.editCampo = idx
    },
    cancelaEdicao() {
      this.listaParam[this.dadosTemp[0]] = this.dadosTemp[1] 
      this.editCampo = -1
    },
    salvaEdicao(c, v) {
      const campo = c
      let valor = v
      valor = typeof valor === 'object' ? valor.join() : valor;
      valor = typeof valor === 'number' || typeof valor === 'boolean' ? valor.toString() : valor;
      this.loaderCheck = true
      let param = JSON.stringify({
        "tabela_end_point": "json_configs",
        "campos_select_end_point": "conteudo",
        "grupo_dlp": "*",
        "maquina": "SERVER",
        "descricao": campo,
        "conteudo": valor
      })
      let url = this.locationUrl + '/sql/updateSql/'+this.token+'/resp_rest'
      console.log(param)
      console.log(url)
      // this.$http.post(url, param, myHeaders)
      // .then(() => {
        this.loaderCheck = false
        this.respostasEnvio(1, `Parâmetro alterado com Sucesso!`)
      // })
      // .catch(() => {
      //   this.respostasEnvio(2, `Falha ao alterar o Parâmetro!`)
      //   this.loaderCheck = false
      // })

      this.loaderCheck = false
    },

    // modal __________________________________________________________________
    abreModal(ref, dados) {
      console.log(ref)
      console.log(dados)
      !!dados ? this.dadosParam = dados : '';
      this.modalParam = {
        adiciona: ref == 'modal-adiciona' ? true : false,
        edita:  ref == 'modal-edita' ? true : false,
        remove:  ref == 'modal-remove' ? true : false,
        senha:  ref == 'modal-senha' ? true : false
      }
      setTimeout(()=> {
        this.$bvModal.show(ref)
      },10)
    },
    fechaModal(ref) {
      this.$bvModal.hide(ref)
      this.getParamServer()
      setTimeout(()=>{
        this.dadosParam = ''
        this.modalParam = {
          adiciona: false,
          edita: false,
          remove: false,
          senha: false
        }
      },500)
    },
    // resposta __________________________________________________________________
    respostasEnvio(stt, resp) {
      console.log(stt)
      console.log(resp)
      this.resposta = {
        status: stt,
        texto: resp
      }
      setTimeout(() => {
        this.resposta = {status: 0, texto: ''}
        this.editCampo = -1
      }, 3000);
    },

  },
  mounted() {
    this.getParamServer()
  }
}
