<template>
  <b-modal ok-only :ref="`email${idx}`" :id="`email${idx}`" size="md" no-close-on-backdrop no-close-on-esc>
    <template #modal-title>
      E-mail:
    </template>
    <div class="container">
      <div class="row">
        <div class="col-12 pb-3" v-if="dados.id_alarme">
          <b-form-group
           label="Id Alerta:" 
           label-for="emlAlerta"
           description="Separe os e-mail por vírgula (,)"
          >
            <b-form-input
              id="emlAlerta"
              v-model="dados.id_alarme"
              type='text'
              disabled
            ></b-form-input>
          </b-form-group>

        </div>
        <div class="col-12 pb-3">
          <b-form-group
           label="Para:" 
           label-for="emlPara"
           description="Separe os e-mail por vírgula (,)"
          >
            <b-form-input
              id="emlPara"
              v-model="mensagem.para"
              type='email'
              placeholder="Coloque o email de contato"
              @keyup="verificaEmail(mensagem.para, 'para')"
              @blur="verificaEmail(mensagem.para, 'para')"
              :state="valida.para"
            ></b-form-input>
          </b-form-group>
        </div>
        <!--<div class="col-12 pb-3">
          <b-form-group 
            label="Com Cópia:" 
            label-for="emlCc"
            description="Separe os e-mail por vírgula (,)"
            >
            <b-form-input
              id="emlCc"
              v-model="mensagem.ccs"
              type='email'
              placeholder="Insira o email com cópia ..."
              @keyup="verificaEmail(mensagem.ccs, 'ccs')"
              @blur="verificaEmail(mensagem.ccs, 'ccs')"
              :state="valida.ccs"
            ></b-form-input>
          </b-form-group>
        </div>-->
        <!--<div class="col-12 pb-3">
          <b-form-group 
            id="input-group-1" 
            label="Com Cópia Oculta:" 
            label-for="emlCco"
            description="Separe os e-mail por vírgula (,)"
          >
            <b-form-input
              id="emlCco"
              v-model="mensagem.ccos"
              type='email'
              placeholder="Insira o email com cópia oculta ..."
              @keyup="verificaEmail(mensagem.ccos, 'ccos')"
              @blur="verificaEmail(mensagem.ccos, 'ccos')"
              :state="valida.ccos"
            ></b-form-input>
          </b-form-group>
        </div>-->
        <div class="col-12 pb-3">
          <b-form-group
            label="Assunto:" 
            label-for="emlAssunto"
          >
            <b-form-input
              id="emlAssunto"
              v-model="mensagem.assunto"
              type="text"
              required
              autocomplete="off"
              placeholder="Insira o asssunto aqui ..."
              @keyup="verificaAssunto(mensagem.assunto)"
              @blur="verificaAssunto(mensagem.assunto)"
              :state="valida.assunto"
            ></b-form-input>
          </b-form-group>
        </div>
        <div class="col-12 pb-3">
          <b-form-group 
            id="input-group-3" 
            label="Mensagem:" 
            label-for="emlTexto"
          >
            <b-form-textarea
              id="emlTexto"
              v-model="mensagem.texto"
              placeholder="Digite algo ..."
              rows="3"
              max-rows="6"
              @keyup="verificaTexto(mensagem.texto)"
              @blur="verificaTexto(mensagem.texto)"
              :state="valida.texto"
            ></b-form-textarea>
          </b-form-group>
        </div>
      </div>   
      <b-alert show :variant="resposta.status == 1 ? 'success' : 'warning'" dismissible v-if="resposta.status == 1 || resposta.status == 2">
        <b-icon :icon="resposta.status == 1 ? 'check-circle-fill' : 'exclamation-circle-fill'" font-scale="1.2" class="mr-2"></b-icon>
        {{resposta.texto}}
      </b-alert>
    </div>
    <template #modal-footer>
      <b-icon icon="arrow-clockwise" animation="spin" variant="primary" v-if="loaderCheck" font-scale="2"></b-icon>
      <b-button-group>
        <b-button variant="success" style="color: white" @click="enviaEmail()" :disabled="!verificaEnvio()">
          <b-icon icon="envelope-fill" class="mr-1"></b-icon>
          Enviar
        </b-button>
        <b-button variant="danger" @click="fechaModal()">
          <b-icon icon="x" class="mr-1"></b-icon> Cancelar
        </b-button>
      </b-button-group>
    </template>
  </b-modal>
</template>
<script>

const urlApi = ambientes.ativo[0].url + ":" + ambientes.ativo[0].porta_api


var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');
export default {
  props: ['dados', 'idx'],
  data() {
    return {
      // variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      loaderCheck: false,
      // variáveis do componente
      dadosMaq: this.dados,
      show: true,
      mensagem: {
        para: '',
        assunto: '',
        ccs: '',
        ccos: '',
        texto: '',
        idAlerta: ''
      },
      // validações de campos
      valida: {
        para: null,
        assunto: null,
        ccs: null,
        ccos: null,
        texto: null
      },
      // resposta do endpoint
      resposta: {
        status: 0,
        texto: ''
      }
    }
  },
  methods: {
    verificaEnvio() {
      return !!this.valida.para && this.valida.ccs !== false && this.valida.ccos !== false && !!this.valida.assunto && !!this.valida.texto
    },
    formatDate(d) {
      let tempo = d.split(' ')
      let hora = tempo[1]
      let data = tempo[0].split('-').reverse()
      data = data[0]+'/'+data[1]+'/'+data[2]
      var dataOk = `${data} - ${hora}`
      return dataOk
    },
    verificaEmail(e, c) {
      let email = e
      let campo = c
      let chk = ''
      let tipo = email.indexOf(',') > -1;
      let reg = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/;
      if(tipo) {
        chk = []
        email = email.split(',')
        email.forEach(str => {
          let res = str.trim().search(reg)
          res = res == -1 ? false : true;
          chk.push(res)
        });
        chk = chk.indexOf(false) < 0 ? true : false;
      } else {
        if(!!email){
          email = email.search(reg)
          chk = email < 0 ?  false : true;
        } else {
          chk = null
        }
      }
      if(campo == 'para'){
        this.valida.para = chk
      }else if(campo == 'ccs'){
        this.valida.ccs = chk
      }else if(campo == 'ccos'){
        this.valida.ccos = chk
      }
    },
    verificaAssunto(a){
      let ass = a;
      this.valida.assunto = !!ass
    },
    verificaTexto(t){
      let txt = t;
      this.valida.texto = !!txt
    },
    enviaEmail() {
      this.loaderCheck = true
      let dadosAlerta = ''
      if(!!this.dados.id_alarme) {
        dadosAlerta = '<br><br><hr>';
        dadosAlerta += !!this.dados.id_alarme ? `<br> <strong>ID:</strong><br> ${this.dados.id_alarme}<br>` : '' ;
        dadosAlerta += !!this.dados.id_alarme ? `<br> <strong>Descrição do alerta:</strong><br> ${this.dados.alarme}<br>` : '' ;
        dadosAlerta += !!this.dados.providencias? `<br> <strong>Providências:</strong><br> ${this.dados.providencias}<br>` : '' ;
        dadosAlerta += !!this.dados.user_name ? `<br> <strong>Usuário:</strong><br> ${this.dados.user_name}<br>` : '' ;
        dadosAlerta += !!this.dados.computer_name ? `<br> <strong>Nome da máquina:</strong><br> ${this.dados.computer_name}<br>` : '' ;
        dadosAlerta += !!this.dados.cod_maquina ? `<br> <strong>Cód. máquina:</strong><br> ${this.dados.cod_maquina}<br>` : '' ;
        dadosAlerta += !!this.dados.status_alarme ? `<br> <strong>Status alerta:</strong><br> ${this.dados.status_alarme}<br>` : '' ;
        dadosAlerta += !!this.dados.handler_alarme ? `<br> <strong>Janela:</strong><br> ${this.dados.handler_alarme}<br>` : '' ;
        dadosAlerta += !!this.dados.executavel ? `<br> <strong>Executável:</strong><br> ${this.dados.executavel}<br>` : '' ;
        dadosAlerta += !!this.dados.tipo_alarme ? `<br> <strong>Tipo alerta:</strong><br> ${this.dados.tipo_alarme}<br>` : '' ;
        dadosAlerta += !!this.dados.grupo_dlp ? `<br> <strong>Grupo DLP:</strong><br> ${this.dados.grupo_dlp}<br>` : '' ;
        dadosAlerta += !!this.dados.data_evento ? `<br> <strong>Data do evento:</strong><br> ${this.formatDate(this.dados.data_evento)}<br>` : '' ;
        dadosAlerta = this.mensagem.texto + dadosAlerta
      } else {
        dadosAlerta = this.mensagem.texto
      }
      dadosAlerta = dadosAlerta.replaceAll(/\n/gi, '<br>')
      /*var param = JSON.stringify({
        "dests": this.mensagem.para, 
    		"ccs": this.mensagem.ccs, 
    		"ccos": this.mensagem.ccos, 
    		"assunto": this.mensagem.assunto, 
        "texto": dadosAlerta,
        "anexos": ""
      });*/
      var param = JSON.stringify({
        "to": this.mensagem.para,
        "subject": this.mensagem.assunto,
        "message": dadosAlerta,
      });
      //var auxiliar = this.locationUrl + "/subMenu/enviaEmail/" + this.token + "/resp_rest";
      var auxiliar = `${urlApi}/api/sendmail`;
      console.log(param)
      this.$http.post(auxiliar, param)
      .then(res => {
        !!res
        this.loaderCheck = false
        this.respostasEnvio(true)
      })
      .catch(err => {
        !!err
        this.loaderCheck = false
        this.respostasEnvio(false)
      })
      setTimeout(() => {
        this.emailMsg = 0;
      }, 3000)
    },
    resetDados() {
      this.mensagem.para = ''
      this.mensagem.assunto = ''
      this.mensagem.ccs = ''
      this.mensagem.ccos = ''
      this.mensagem.texto = ''
    },
    respostasEnvio(resp) {
      if(!!resp){
        this.resposta = {
          status: 1,
          texto: `Mensagem para o(s) e-mail(s) ${this.mensagem.para} enviada com sucesso!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
        setTimeout(() => {
          this.fechaModal()
          this.resetDados()
        }, 3600);
      } else {
        this.resposta = {
          status: 2,
          texto: `Falha ao enviar mensagem para o(s) e-mail(s) ${this.mensagem.para}!`
        }
        // setTimeout(() => {
        //   this.resposta = {status: 0, texto: ''}
        // }, 3500);
      }
    },
    abreModal() {
      this.$refs[`email${this.idx}`].show()
    },
    fechaModal() {
      this.$refs[`email${this.idx}`].hide()
      this.resetDados()
    },
  }
}
</script>
<style scoped>
  fieldset#box-actions {
    float: right;
  }
</style>
