<template> 
  <b-modal ok-only :ref="`chat${idx}`" :id="`chat${idx}`"  size="md" hide-header-close no-close-on-backdrop no-close-on-esc>
    <template #modal-title>
      Chat:
    </template>
    <div class="container">
      <div class="row">
        <div class="col-12 pb-3">
          <span>Máquina:</span><br>
          <strong class="text-uppercase">{{`${dadosMaq.grupo_dlp}-${dadosMaq.computer_name}-${dadosMaq.cod_maquina}`}}</strong>
        </div>
        <div class="col-12 pb-3" v-if="!!dadosMaq.id_alarme">
          <span>ID do Alerta:</span><br>
          <strong class="text-uppercase">{{dadosMaq.id_alarme}}</strong>
        </div>
        <div class="col-12 pb-3">
          <b-form-group
            label="Mensagem:"
            label-for="chatMensagem"
            class="mb-0"
          >
            <b-form-textarea
              id="chatMensagem"
              class="mensagem"
              v-model="mensagem"
              :state="valida.chat"
              placeholder="Coloque pelo menos 3 caracteres"
              no-resize
              rows="4"
            ></b-form-textarea>
          </b-form-group>
        </div>
      </div>
      <b-alert :variant="resposta.status == 1 ? 'success' : 'warning'" show v-if="resposta.status !== 0" dismissible>
        <b-icon :icon="resposta.status == 1 ? 'check-circle-fill' : 'exclamation-circle-fill'" font-scale="1.2" class="mr-2"></b-icon>
        {{resposta.texto}}
      </b-alert>
    </div>
    <template #modal-footer>
      <b-icon icon="arrow-clockwise" animation="spin" variant="primary" v-if="loaderCheck" font-scale="2"></b-icon>
      <b-button-group>
        <b-button variant="success" style="color: white" @click="enviaChat()" :disabled="!verificaChat()">
          <b-icon icon="chat-dots-fill" class="mr-1"></b-icon>
           Enviar
        </b-button>
        <b-button variant="danger" @click="fechaModal()">
          <b-icon icon="x" class="mr-1"></b-icon> Cancelar
        </b-button>
      </b-button-group>
    </template>
  </b-modal>
</template>
<script>
var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Content-Type", "text/plain");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
  props: ['dados', 'idx'],
  data() {
    return {
      // variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      loaderCheck: false,
      // vairáveis do componente
      dadosMaq: this.dados,
      mensagem: '',
      // validações de campos
      valida: {
        chat: null,
      },
      // resposta do endpoint
      resposta: {
        status: 0,
        texto: ''
      }
    }
  },
  watch: {
    modalDesliga(vlr) {
      this.modal = vlr
    },
    dados(d) {
      this.dadosMaq = d
    }
  },
  methods: {
    verificaChat() {
      var cont = this.mensagem.length
      this.valida.chat = cont > 2 ? true : null;

      let statusString = true;

      if(this.mensagem[0] === " ") {
        statusString = false;

      } else if(this.mensagem[0] === " " && this.mensagem[1] === " ") {
        statusString = false;

      }

      return this.valida.chat && statusString
    },
    async enviaChat() {
      this.loaderCheck = true
      let param = !!this.dadosMaq.id_alarme ? 
        JSON.stringify({"maquina":this.dadosMaq.cod_maquina,"chat": `[ID DO ALARME: ${this.dadosMaq.id_alarme}] - ` + this.mensagem.replace(/(\r\n|\n|\r)/gm," ")})
       : 
        JSON.stringify({"id_alarme": this.dadosMaq.id_alarme,"maquina":this.dadosMaq.cod_maquina,"chat":this.mensagem.replace(/(\r\n|\n|\r)/gm," ")})
      ;
      let url = `${this.locationUrl}/subMenu/enviaNovoChat/${this.token}/resp_rest`;
      console.log(param)
      await this.$http.post(url, param, myHeaders)
      .then(res => {
        this.loaderCheck = false
        !!res ? true : false ;
        this.respostasEnvio(true)
        this.valida.chat = null
        this.mensagem = ''
      })
      .catch(err => {
        this.loaderCheck = false
        !!err ? true : false ;
        this.respostasEnvio(false)
      })
    },
    respostasEnvio(resp) {
      if(!!resp){
        this.resposta = {
          status: 1,
          texto: `Mensagem para "${this.dadosMaq.computer_name}" enviada com sucesso!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
        setTimeout(() => {
          this.fechaModal()
          this.$emit('carregaDados')
        }, 3600);
      } else {
        this.resposta = {
          status: 2,
          texto: `Falha ao enviar mensagem para "${this.dadosMaq.computer_name}"!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
      }
    },
    fechaModal() {
      this.$refs[`chat${this.idx}`].hide()
    },
    abreModal(){
      this.$refs[`chat${this.idx}`].hide()
    }
  },
  mounted(){
      this.fechaModal()
  }
}
</script>

<style>
.row {
  -webkit-box-orient: initial !important;
  flex-direction: inherit !important;
}
.mensagem {
  width: 100%;
}
</style>