module.exports.TrataValor = function(valor) {
    var diafull = valor.split(" ")[0];
    var hora  = valor.split(" ")[1];
    var dia = diafull.split('-')[2]
    var mes = diafull.split('-')[1]
    var ano = diafull.split('-')[0]
    var dataFinal = hora + ' - '+ dia+'/'+mes+'/'+ano;
    dataFinal = JSON.stringify(dataFinal)
    dataFinal= dataFinal.replace(new RegExp('["]','gi'), ''); 
    return  dataFinal ;
}

module.exports.TrataValorTimestamp = function(valor) {
    let data = new Date(valor);
    data = data.toISOString()
    data = data.trim();
    var diafull = data.split('T')[0];
    var hora = data.split('T')[1].replace('Z', '');
    hora = hora.substring(0, hora.length -1);
    var dia = diafull.split('-')[2]
    var mes = diafull.split('-')[1]
    var ano = diafull.split('-')[0]
    var dataFinal = hora + ' - '+ dia+'/'+mes+'/'+ano;
    dataFinal = JSON.stringify(dataFinal)
    dataFinal= dataFinal.replace(new RegExp('["]','gi'), ''); 
    return dataFinal;
}

module.exports.TimestampData = function(valor) {
var valor = new Date(valor).toLocaleTimeString("pt-br")
return valor;
}