<template>
  <div>
    <b-modal ok-only ref="modIdle" id="modIdle"
      title="Encerrando Sessão" >
      <div class="row">
        <div class="col">
            <div class="p-3"> 
                <p>Sua sessão está sendo encerrada por inatividade durante 1 hora. Para continuar faça alguma ação para fechar essa janela.</p>
                <p><strong>{{ second }} segundos restantes</strong></p>
            </div>
        </div>
      </div>
    </b-modal>
  </div>
</template>

<script>
//import { mapActions } from 'vuex'
  export default {
    data() {
      return { 
        time: 60 * 1000
      }
    },
    mounted() {
        this.showModal();
    },
    computed: {
        second() {
            return this.time / 1000;
        }
    },
    created() {
        var url = window.location.href;
        if(url.includes('login')){
          this.$store.state.idleVue.isIdle = false;
        }
        this.locationUrl = this.$store.state.variaveisAmbiente.locationUrl;
        let timerId = setInterval(() => {
          this.time -= 1000;
          if (!this.$store.state.idleVue.isIdle){clearInterval(timerId);}
          if (this.time < 1)  {
              this.redireciona()
              this.hideModal()
              clearInterval(timerId);
              // Your logout function should be over here
              //alert('Saindo....');
          }
        }, 1000);
    },
    methods: {
       // ...mapActions(['ActionSignOut']),
        // redireciona(){   
        //   var token =JSON.parse(localStorage.getItem('user')).keyAcesssSecret;
        //   var url = this.locationUrl+"/dpo/acesso/sessao/"+token+"/resp_rest";
        //   this.$http.post(url)
        //   .then(response => response.text())
        //   //.then(result => this.ActionSignOut())
        //   .then(result => this.$store.dispatch('ActionSignOut'))
        //   .catch(error => console.log('error', error));      
        //   this.$router.push({name:'login'})   
        // },
        redireciona(){
          var token = JSON.parse(localStorage.getItem('user'));
          token = typeof token == 'string' ? token : token.keyAcesssSecret;
          console.log(token)
          var url = `${this.$store.state.variaveisAmbiente.locationUrl}/dpo/acesso/sessao/${token}/resp_rest`;
          this.$http.post(url)
            .then(response => response.text())
            //.then(result => this.ActionSignOut(),localStorage.removeItem('user'))
            .then(result => this.$store.dispatch('ActionSignOut'),localStorage.removeItem('user'))
         // .catch(error => console.log('error', error)); 
         .catch(()=> { console.log('Sessão Encerrada!')})
          this.$router.push({name:'login'})
          localStorage.clear()
        },
        showModal() {
        this.$root.$emit('bv::show::modal', 'modIdle', '#btnShow')
        },
        hideModal() {
        this.$root.$emit('bv::hide::modal', 'modIdle', '#btnShow')
        },
        toggleModal() {
        this.$root.$emit('bv::toggle::modal', 'modIdle', '#btnToggle')
        }
    }
  }
</script>

<style lang="postcss" scoped>
.overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal {
  width: 500px;
  box-shadow: 1px 2px 4px rgba(153, 155, 168, 0.12);
  border-radius: 4px;
  @apply bg-white p-2;
}

.modal__title {
  color: #38404f;
  @apply flex items-center justify-between p-3 font-bold;
}


</style>