<template> 
  <b-modal ok-only ref="plvEdita" id="plvEdita"  size="lg" hide-header-close no-close-on-backdrop no-close-on-esc>
    <template #modal-title>
      Edita Palavra Sensível
    </template>
    <div class="container" v-if="!!dadosPlv">
      <div class="row">
        <div class="col-xs-12 col-sm-6  pb-3">
          <b-form-group
            label="Palavra"
            label-for="plvPalavra"
            :description="`Total de Caracteres: ${!!dadosPlv.palavra ? dadosPlv.palavra.length  : 0}/200`"
            class="mb-0"
          >
            <!-- TRAZ OS DADOS DO ITEM QUE SE DESEJA ALTERAR, DEIXANDO O OBSERVADOR FICAR DE OLHO CASO HAJA ALGUMA MUDANÇA INSIRA IMEDIATAMENTE O NOVO VALOR EM SUA VARAIVEL REPECTIVA -->
            <b-form-input
              id="plvPalavra"
              v-model="dadosPlv.palavra"
              placeholder="Coloque a palavra ou argumento"
              :state="valida.palavra = !!dadosPlv.palavra"
              maxlength="200"
            ></b-form-input>
          </b-form-group>
        </div>
        <div class="col-xs-12 col-sm-6  pb-3">
          <b-form-group
            label="Fonético"
            label-for="plvFonetico"
            class="mb-0"
          >
            <b-form-input
              id="plvFonetico"
              v-model="dadosPlv.fonetico"
              placeholder="Coloque a fonetico"
              @blur="verFonetico(dadosPlv.fonetico)"
              :state="valida.fonetico = !!dadosPlv.fonetico"
              maxlength="200"
              disabled
            ></b-form-input>
          </b-form-group>
        </div>
        <div class="col-xs-12 col-sm-6  pb-3" v-if="!!listaTipo">
          <b-form-group
            label="Tipo"
            label-for="plvTipo"
            class="mb-0"
          >
            <!-- <b-form-select 
              id="plvTipo" 
              v-model="dadosPlv.tipo" 
              :options="listaTipo"
              :state="valida.tipo = !!dadosPlv.tipo"
            >
            </b-form-select> -->
            <b-form-input 
              list="lista-tipos"
              v-model="dadosPlv.tipo" 
              :state="valida.tipo = !!dadosPlv.tipo"
              @keyup="chkTipo(dadosPlv.tipo)"
            ></b-form-input>
            <datalist id="lista-tipos">
              <option v-for="item in listaTipo" :key="item">{{ item }}</option>
            </datalist>
          </b-form-group>
        </div>
        <div class="col-xs-12 col-sm-6  pb-3">
          <b-form-group
            label="Grupo DLP"
            label-for="plvGrupo"
            class="mb-0"
          >
            <b-form-input
              id="plvGrupo"
              v-model="dadosPlv.grupo_dlp"
              placeholder="Coloque tipo de palavra"
              :state="valida.grupo_dlp = !!dadosPlv.grupo_dlp"
              maxlength="100"
              disabled
            ></b-form-input>
          </b-form-group>
        </div>
        <div class="col-xs-12 col-sm-6" v-if="!!dadosPlv.status">
          <label class="d-block">Status</label>
          <b-button 
            id="plvStatus"
            :title="`Status ${dadosPlv.status === 'ATIVA' ? 'Ativada' : 'Inativada' } `" 
            size="md" 
            @click="verStatus(dadosPlv.status)" 
            :variant="dadosPlv.status === 'ATIVA' ? 'outline-success' : 'outline-warning'"
            block
          >
            <b-icon class="mr-2" icon="chat-left-text-fill" font-scale="1" v-if="dadosPlv.status === 'ATIVA'"></b-icon>
            <b-iconstack class="mr-2" font-scale="1" v-else>
              <b-icon icon="chat-right-text-fill" scale="0.6"></b-icon>
              <b-icon icon="slash-circle" scale="1.1"></b-icon>
            </b-iconstack>
            {{dadosPlv.status === 'ATIVA' ? 'Ativa' : 'Inativa'}}
          </b-button>
        </div>
        <div class="col-xs-12 col-sm-6  pb-3" v-if="!!listaGravidade">
          <b-form-group
            label="Forçar Gravidade"
            label-for="plvGrav"
            class="mb-0"
          >
            <!-- <b-form-select 
              id="plvGrav" 
              v-model="dadosPlv.gravidade" 
              :options="listaGravidade"
              :state="valida.gravidade = !!dadosPlv.gravidade"
            >
            </b-form-select> -->
            <b-dropdown 
              class="drop-custom" 
              right
              split
              :text="!dadosPlv.gravidade ? 'Selecione' : dadosPlv.gravidade" 
              :variant="!dadosPlv.gravidade ? 'outline-secondary' : 'outline-success'"
              :state="valida.gravidade = !!dadosPlv.gravidade"
            >
              <b-dropdown-item
                block
                v-for="(item, i) in listaGravidade" 
                :key="i" @click="dadosPlv.gravidade = item"
              >
                {{item}}
              </b-dropdown-item>
            </b-dropdown>
          </b-form-group>
        </div>
      </div>
      <b-alert :variant="resposta.status == 1 ? 'success' : 'warning'" show v-if="resposta.status !== 0" dismissible>
        <b-icon :icon="resposta.status == 1 ? 'check-circle-fill' : 'exclamation-circle-fill'" font-scale="1.2" class="mr-2"></b-icon>
        {{resposta.texto}}
      </b-alert>
    </div>
    <template #modal-footer>
      <b-icon icon="arrow-clockwise" animation="spin" variant="primary" v-if="loaderCheck" font-scale="2"></b-icon>
      <b-button-group>
        <b-button variant="success" style="color: white" @click="alteraPalavra()" :disabled="!chkBntEnvio()">
          <b-icon icon="pencil" class="mr-1"></b-icon>
           Alterar
        </b-button>
        <b-button variant="danger" @click="fechaModal()">
          <b-icon icon="x" class="mr-1"></b-icon> Cancelar
        </b-button>
      </b-button-group>
    </template>
  </b-modal>
</template>
<script>

const urlApi = ambientes.ativo[0].url + ":" + ambientes.ativo[0].porta_api

var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Content-Type", "text/plain");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
  props: ['dados', 'listaTipo', 'listaGravidade'],
  data() {
    return {
      // variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      ambiente: this.$store.state.variaveisAmbiente.ambiente,
      loaderCheck: false,
      // vairáveis do componente
      dadosPlv: this.dados,
      chkStatus: this.dados.status,
      mensagem: null,
      // validações de campos
      valida: {
        palavra: null,
        tipo: null,
        status: null,
        fonetico: null,
        grupo_dlp: null,
        gravidade: null
      },
      // resposta do endpoint
      resposta: {
        status: 0,
        texto: ''
      },
    }
  },
  watch:{
    dados (d) {
      this.dadosPlv = d
    }
  },
  methods: {
    dataAgora() {
      let agora = new Date();
      let hora = agora.toString().split(' ')
      let dia = agora.getDate()
      dia < 10 ? dia = '0' + dia : dia;
      let mes = agora.getMonth() + 1
      mes < 10 ? mes = '0' + mes : mes;
      let ano = agora.getFullYear() //- 100 
      let hoje = `${ano.toString()}-${mes.toString()}-${dia.toString()} ${hora[4]}`
      return hoje
    },
    chkBntEnvio() {
      let val = !!this.valida.palavra && !!this.valida.tipo && !!this.valida.fonetico && !!this.valida.gravidade && !!this.valida.grupo_dlp
      return val
    },
    getFonetico(p) {
      let palavra = p
      this.dadosPlv.palavra.length > 0 ?
      this.dadosPlv.fonetico = this.dadosPlv.fonetico
      :
      ''
    },
    chkTipo(t) {
      let tipo = t.replace(' ', '_').replace(/[`´~!@#$%^&*()|+\=?;:.'"¨,<>\{\}\[\]\\\/àáâãäÀÁÂÃÄèéêëÈÉÊËìíîïÌÍÎÏóòôõöÓÒÔÕÖúùûüÚÙÛÜçÇñÑ]/gi, '');
      this.dadosPlv.tipo = tipo.toUpperCase()
    },
    verStatus(s) {
      this.dadosPlv.status = s === 'ATIVA' ? 'INATIVA' : 'ATIVA'
    },
    /* ALTERA UMA DETERMINADA PLAVRA RECEBENDO OS DADOS VINDO DO INPUT */
    /*async alteraPalavra() {
     this.loaderCheck = true

     let memoriaGravidade = '';

      // ALTERANDO PALAVRA DE TIPO GRAVIDADE POR NUMEROS 
      if(this.dadosPlv.gravidade == 'NEUTRO') {
        memoriaGravidade = '0'

      }else if(this.dadosPlv.gravidade == 'SERIO') {
        memoriaGravidade = '1'

      }else if(this.dadosPlv.gravidade == 'GRAVE') {
        memoriaGravidade = '2'

      }else if(this.dadosPlv.gravidade == 'GRAVISSIMO') {
        memoriaGravidade = '3'
        
      }

      console.log('Tipo de gravidade: '+ memoriaGravidade);

      let param = JSON.stringify({
        "tabela_end_point":"palavras",
        "campos_select_end_point":"palavra",
        "palavra":this.dadosPlv.palavra,
        "tipo":this.dadosPlv.tipo,
        "status":this.dadosPlv.status,
        "fonetico":this.dadosPlv.fonetico,
        "grupo_dlp":this.dadosPlv.grupo_dlp,
        "dataCriacao":this.dadosPlv.dataCriacao,
        "gravidade": memoriaGravidade //this.dadosPlv.gravidade
        })
      let url = `${this.locationUrl}/sql/updateSql/${this.token}/resp_rest`; // CONSTROE UMA URL PARA SOLICITAR UMA REQUISICAO
      await this.$http.post(url, param, myHeaders) // ENVIA COMO PAREMETROS OS DADOS PARA QUE SE POSSA ATUALIZAR O CONTEUDO DA PALAVRA EM ESPECIFICO
      .then(res => {
        this.loaderCheck = false
        !!res ? true : false ;
        this.respostasEnvio(true)
        this.valida.mensagem = null
        this.mensagem = ''
      })
      .catch(err => {
        this.loaderCheck = false
        !!err ? true : false ;
        this.respostasEnvio(false)
      })
    },*/
    async alteraPalavra() {
     this.loaderCheck = true

     let memoriaGravidade = '';

      // ALTERANDO PALAVRA DE TIPO GRAVIDADE POR NUMEROS 
      if(this.dadosPlv.gravidade == 'NEUTRO') {
        memoriaGravidade = '0'

      }else if(this.dadosPlv.gravidade == 'SERIO') {
        memoriaGravidade = '1'

      }else if(this.dadosPlv.gravidade == 'GRAVE') {
        memoriaGravidade = '2'

      }else if(this.dadosPlv.gravidade == 'GRAVISSIMO') {
        memoriaGravidade = '3'
        
      }

      console.log('Tipo de gravidade: '+ memoriaGravidade);

      let param = {
        "palavras":this.dadosPlv.palavra,
        "tipo":this.dadosPlv.tipo,
        "status":this.dadosPlv.status,
        "fonetico":this.dadosPlv.palavra,
        "grupoDlp":this.dadosPlv.grupo_dlp,
        "dataCriacao":this.dadosPlv.dataCriacao,
        "gravidade": memoriaGravidade //this.dadosPlv.gravidade
      }
      
      console.log('PARAM UPDATE');
      console.log(param);

      //let ambiente = 'POCNEW';
      let url = `${urlApi}/api/palflash/updateporpalavra/${this.dadosPlv.fonetico}/${this.ambiente}/${this.token}`; // CONSTROE UMA URL PARA SOLICITAR UMA REQUISICAO
      console.log('URL UPDATE');
      console.log(url)
      await this.$http.put(url, param, myHeaders) // ENVIA COMO PAREMETROS OS DADOS PARA QUE SE POSSA ATUALIZAR O CONTEUDO DA PALAVRA EM ESPECIFICO
      .then(res => {
        this.loaderCheck = false
        !!res ? true : false ;
        this.respostasEnvio(true)
        this.valida.mensagem = null
        this.mensagem = ''
      })
      .catch(err => {
        this.loaderCheck = false
        !!err ? true : false ;
        this.respostasEnvio(false)
      })
    },
    /* RETORNA UMA RESPOSTA DE ACORDO COM O RETORNO DA REQUISICAO SENDO ELA POSITIVA OU NEGATIVA */
    respostasEnvio(resp) {
      if(!!resp){
        this.resposta = {
          status: 1,
          texto: `A alteração da palavra "${this.dadosPlv.palavra}" foi realizada com sucesso!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
        setTimeout(() => {
          this.fechaModal()
          this.$emit('carregaDados')
        }, 3600);
      } else {
        this.resposta = {
          status: 2,
          texto: `Falha ao realizar alteração da Palavra Sensível "${this.dadosPlv.palavra}"!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
      }
    },
    fechaModal() {
      this.$refs['plvEdita'].hide()
    },
    abreModal(){
      this.$refs['plvEdita'].hide()
    }
  }
}
</script>
<style>
.row {
  -webkit-box-orient: initial !important;
  flex-direction: inherit !important;
}
.btn-outline-warning:hover {
  color: #FFF !important
}
</style>
