<template>
  <div>
    <b-modal ok-only :ref="`plvMaq`" :id="`plvMaq`" :size="selPlvMaquina.modal" hide-header-close no-close-on-backdrop no-close-on-esc>
      <!-- ICONE DE CARREGAMENTO -->
      <div class="container-icone-carregamento" v-if="loaderCheck">
        <loader :status="loaderCheck"></loader>
        <!--<b-spinner class="icone-carregando" variant="primary" label="Large Spinner" ></b-spinner>-->
        <!--<strong>Carregando aguarde...</strong>-->
      </div>
      <template #modal-title>
        Processamento Arquivo/Maquinas: {{tabIndex}}
      </template>
        <b-card class="mb-3" no-body>
          <b-tabs class="proc" v-model="tabIndex" small card>
            <b-tab title="Sel. Arquivo" no-key-nav>
              <h4 class="my-0 mb-4">
                Seleção de Arquivo
              </h4>

              <!-- ICONE DE CARREGAMENTO -->
              <!--<b-icon class="icone-carregando" icon="arrow-clockwise" animation="spin" variant="primary" font-scale="2"></b-icon>
              
              <div variant="primary" style="position: absolute; z-index: 1 !important;">
                <b-spinner small></b-spinner>
              </div>-->

              <b-alert variant="light" show dismissible>
                <p><b-icon icon="info-circle-fill" font-scale="1.2" class="mr-2"></b-icon><strong>Processo de Inclusão de arquivo de PALAVRAS CLASSIFICADAS nas máquinas:</strong></p>
                <p>Primeiramente verifique no módulo de PALAVRAS CLASSIFICADAS.</p>
                <p>Verifique se as palavras estão cadastras com status “Ativas”, e se por acaso existirem palavras que não há necessidade de serem geradas no arquivo, por favor, altere o status para “Bloqueada”.</p>
                <p>Definido a etapa anterior, selecione um arquivo na lista (é necessário incluir um número de versão para o arquivo), e clique em Gerar Arquivo.</p>
                <p>Na próxima etapa selecione às máquinas que deseja enviar o arquivo gerado e clique em Processar Máquinas, nesse caso será gerado um “Número de JOB” e aparecerá o processo de envio de arquivos.</p>
                <p>Você pode clicar em atualizar para verificar quais máquinas foram processadas com o novo arquivo.</p>
                <p>Após o início do processo a janela não precisa ficar aberta, caso ocorra de máquina(s) esteja(ão) offline, esta(s) será(ão) atualizada(s) automáticamente assim que conectar(em).</p>
                <p>Clique em Fechar ao concluir o processo.</p>
              </b-alert>
              <div class="container">
                <div class="row">
                  <div class="col-12 mb-3 area">
                    <b-form-group
                      label="Versão do Arquivo"
                      label-for="plvVersao"
                      description="Essa informação é opcional."
                      class="mb-0"
                    >
                      <b-form-input
                        id="plvVersao"
                        v-model="selPlvMaquina.versao"
                        placeholder="Ex: 1.0"
                        maxlength="20"
                        :state="valida.versao"
                        @keyup="valVersao(selPlvMaquina.versao)"
                        @blur="valVersao(selPlvMaquina.versao)"
                      ></b-form-input>
                    </b-form-group>
                  </div>
                  <div class="col-12 mb-3 area">
                    <b-button variant="primary" @click="getListaArquivos()" v-if="listaArquivos == []"><b-icon icon="arrow-clockwise"></b-icon> Carrega Lista de Arquivos</b-button>
                    <b-form-group
                      label="Selecione um Arquivo"
                      label-for="plvMaqArquivo"
                      class="mb-0"
                    >
                      <b-button-group vertical style="width: 100%">
                        <b-button
                          v-for="(item, i) in listaArquivos"
                          :key="i"
                          class="text-left" 
                          :variant="selPlvMaquina.arq == item ? 'primary' : 'outline-secondary'"
                          @click="selPlvMaquina.arq = item"
                        ><b-icon :icon="selPlvMaquina.arq == item ? 'file-earmark-check-fill' : 'file-earmark'" class="mr-2"></b-icon>{{item}}</b-button>
                      </b-button-group>
                    </b-form-group>
                  </div>
                </div>
              </div>
              <b-button-group class="mt-2">
                <b-button 
                  variant="success"
                  font-scale="2"
                  @click="getProcessaArquivo()" 
                  :disabled="!selPlvMaquina.arq || !selPlvMaquina.versao || !valida.versao" 
                  class="botao-processamento-arquivo"
                >
                  <b-icon icon="check" class="mr-2"></b-icon> Processar Arquivo
                </b-button>
                <!-- <b-button variant="primary" @click="tabIndex--" :disabled="tabIndex == 0"><b-icon icon="chevron-left" class="mr-2"></b-icon> Anterior</b-button>
                <b-button variant="primary" @click="tabIndex++" :disabled="selPlvMaquina.arq == '' || selPlvMaquina.versao == ''"><b-icon icon="chevron-right" class="mr-2"></b-icon> Próximo</b-button> -->
              </b-button-group>
            </b-tab>
            <b-tab title="Sel. Maquina" no-key-nav>
              <h4 class="my-0 mb-4">
                Seleção de Maquinas
                <b-button class="float-right"
                  :variant="selPlvMaquina.maquinas.length == listaMaquinas.length ? 'primary' : 'outline-secondary'"
                  @click="selPlvMaquina.maquinas !== listaMaquinas ? selPlvMaquina.maquinas = listaMaquinas : selPlvMaquina.maquinas = []"
                >
                  <b-icon :icon="selPlvMaquina.maquinas.length == listaMaquinas.length ? 'list-check' : 'list'"></b-icon> Adicionar Todos
                </b-button>
              </h4>
              <div class="container">
                <div class="row">
                  <div class="col-12 mb-3 area">
                    <b-form-group
                      label-for="plvMaquinas"
                      class="mb-0"
                    >

                      <!-- INPUT DE PESQUISA -->
                      <b-col xs="12" sm="12" md="4" class="py-1">
                        <div style="display: flex; justify-content: space-around;" class="ajuste-botao-pesquisa">
                          <b-form-group style="padding: 0% 2% 0% 0%; width: 96% !important;"
                            description="Pesquisa na Tabela"
                            class="col-md-12"
                          >
                            <b-form-input
                              v-model="filtro"
                              type="search"
                              placeholder="Busca"
                              size="sm"
                            ></b-form-input>
                            
                            <!--<b-form-select
                              v-model="filtro"
                              :options="memoriaValores"
                              size="sm"
                            ></b-form-select>-->

                            <!--<select value=filtro >
                              <option v-for="(item, i) in memoriaValores" value=item>{{ item }}</option>
                            </select>-->
                                
                          </b-form-group>
                          <!-- BOTAO DE PESQUISA -->
                          <div class="pt-1" style="padding: 0 !important; margin: 0 !important;">
                            <b-button variant="primary" style="padding: 6% !important; margin: 0 !important; font-size: 14px !important;" @click="getLocalizaMaquinaArray(filtro)" >Pesquisar</b-button>
                          </div>
                        </div>
                      </b-col>

                      <b-button-group vertical style="width: 100%">
                        <b-button
                          v-for="(item, i) in listaMaquinas"
                          :key="i"
                          class="text-left" 
                          :variant="selPlvMaquina.maquinas.indexOf(item) > -1 ? 'primary' : 'outline-secondary'"
                          @click="selPlvMaquina.maquinas.indexOf(item) < 0 ? selPlvMaquina.maquinas.push(item) : selPlvMaquina.maquinas = selPlvMaquina.maquinas.filter( obj => obj !== item )"
                        ><b-icon :icon="selPlvMaquina.maquinas.indexOf(item) > -1 ? 'display' : 'display-fill'" class="mr-2"></b-icon>{{item.computer_name}}</b-button>
                      </b-button-group>
                    </b-form-group>
                  </div>
                </div>
              </div>
              <b-button-group class="mt-2">
                <b-button 
                  variant="secondary" 
                  @click="tabIndex--"
                >
                  <b-icon icon="chevron-left" class="mr-2"></b-icon> Voltar
                </b-button>
                <b-button
                  :variant="selPlvMaquina.maquinas.length == listaMaquinas.length ? 'primary' : 'outline-secondary'"
                  @click="selPlvMaquina.maquinas !== listaMaquinas ? selPlvMaquina.maquinas = listaMaquinas : selPlvMaquina.maquinas = []"
                >
                  <b-icon :icon="selPlvMaquina.maquinas.length == listaMaquinas.length ? 'list-check' : 'list'"></b-icon> Adicionar Todos
                </b-button>
                <b-button  
                  variant="success" 
                  @click="getExcluirArquivos()" 
                  :disabled="selPlvMaquina.maquinas.length < 1"
                >
                  <b-icon icon="check" class="mr-2"></b-icon> Processar Máquinas
                </b-button>
              </b-button-group>
            </b-tab>
            <b-tab title="Finaliza">
              <h4 class="my-0 mb-4">Processo Concluído</h4>
              <div class="container">
                <div class="row">
                  <div class="col-sm-12 col-md-12  mb-3">
                    <b-form-group
                      label="Job"
                      class="mb-0"
                    >
                      <b-form-input
                        v-model="selPlvMaquina.job"
                          disabled
                      ></b-form-input>
                    </b-form-group>
                  </div>
                  <div class="col-sm-12 col-md-5  mb-3">
                    <b-form-group
                      label="Arquivo"
                      class="mb-0"
                    >
                      <b-form-input
                        v-model="selPlvMaquina.arq"
                        disabled
                      ></b-form-input>
                    </b-form-group>
                  </div>
                  <div class="col-sm-12 col-md-4  mb-3">
                    <b-form-group
                      label="Versão do Arquivo"
                      class="mb-0"
                    >
                      <b-form-input
                        v-model="selPlvMaquina.versao"
                        disabled
                      ></b-form-input>
                    </b-form-group>
                  </div>
                  <div class="col-sm-12 col-md-3 mb-3">
                    <b-form-group
                      label="Ação"
                      class="mb-0"
                    >
                      <b-button
                        style="width: 100%"
                        variant="outline-primary" 
                        @click="getProcessaMaquinas()"
                      >
                        <b-icon icon="arrow-clockwise"></b-icon> Atualizar
                      </b-button>
                    </b-form-group>
                  </div>
                </div>
                <div class="row">
                  <div class="col-12 mb-3 area">
                    <b-table
                      ref="tabProcesso" 
                      id="tabProcesso"
                      class="tab-processo mb-0"
                      show-empty
                      small
                      striped 
                      stacked="md"
                      :items="listaProcesso"
                      :fields="titTabela"
                      empty-text="Não há registro para sua pesquisa."
                    >
                      
                      <template v-slot:cell(arquivo_destino)="row">
                        <div class="wrap">
                          {{row.item.arquivo_destino}}
                        </div>
                      </template>
                      <template v-slot:cell(dth)="row">
                        <div>
                          {{formatData(row.item.dth)}} 
                        </div>
                      </template>
                      <template v-slot:cell(status)="row">
                        <div class="block text-center">
                          <b-icon 
                            :icon="row.item.status == 'PEND' ? 'square' : 'check-square-fill'" 
                            :variant="row.item.status == 'PEND' ? 'secondary' : 'success'" 
                            :title="row.item.status == 'PEND' ? 'Pendente' : 'Instalado'" 
                            font-scale="1.2" 
                            class="mr-2"
                          >
                          </b-icon>
                        </div>
                      </template>
                    </b-table>
                  </div>
                </div>
              </div>
            </b-tab>
          </b-tabs>
        </b-card>
        <b-alert :variant="resposta.status == 1 ? 'success' : 'warning'" show v-if="resposta.status !== 0" dismissible>
          <b-icon :icon="resposta.status == 1 ? 'check-circle-fill' : 'exclamation-circle-fill'" font-scale="1.2" class="mr-2"></b-icon>
          {{resposta.texto}}
        </b-alert>
      <template #modal-footer>
        <!--<b-icon icon="arrow-clockwise" animation="spin" variant="primary" v-if="loaderCheck" font-scale="2"></b-icon>-->
        <b-button-group>
          <b-button variant="danger" @click="fechaModal('plvMaq')">
            <b-icon icon="x" class="mr-1"></b-icon> Fechar
          </b-button>
        </b-button-group>
      </template>
    </b-modal>
  </div>
</template>
<script>
import Loader from '../../shared/loader/loader'

var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Content-Type", "text/plain");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
  components: {
    'loader': Loader
  },
  props: ['arquivos'],
  data() {
    return {
      // variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      loaderCheck: true,
      // vairáveis do componente
      tabIndex: 0,
      setGrupoDLP: '',

      // PESQUISA MAQUINAS NO ARRAY
      filtro: '',
      travaFiltro: false,
      memoriaMaquinas: [],
      memoriaValores: [],

      selPlvMaquina: {
        arq: '',
        versao: '',
        grupo_dlp: '',
        maquinas: [],
        job: '',
        modal: 'lg'
      },
      valida: {
        versao: null
      },
      listaMaquinas: [{
        computer_name: '',
        hash_maquina: ''
      }],
      listaArquivos: [],
      listaProcesso: [],
      titTabela: [
        { key: 'fullMaq',label: 'Máquina',sortable: true, class: 'text-left', sortDirection: 'asc'},
        { key: 'grupo_dlp',label: 'Grupo DLP',sortable: true, class: 'text-left'},
        { key: 'arquivo_destino',label: 'Destino', sortable: true, class: 'text-left'},
        { key: 'dth', label: 'Data Processamento', sortable: true, class: 'text-left'},
        { key: 'status', label: 'Status', class: 'text-left'},
      ],
      totalRows: 1,
      currentPage: 1,
      perPage: 20,
      pageOptions: [5, 10, 20, 50, 100],
      filter: null,
      // resposta do endpoint
      resposta: {
        status: 0,
        texto: ''
      }
    }
  },
  methods: {
    formatData(d){
      let data = d
      if(!!data) {
        data = data.split(' ')
        let dia = data[0].split('-').reverse()
        return `${dia[0]}/${dia[1]}/${dia[2]} ${data[1]}`
      } else {
        return data
      }
    },
    valVersao (v) {
      // let reg = /^[-0-9.]?$/ig;
      let versao = v.replace(/[a-zA-Z_`´~!@#$%^&*()|+\=?;:'"¨, <>\{\}\[\]\\\/àáâãäÀÁÂÃÄèéêëÈÉÊËìíîïÌÍÎÏóòôõöÓÒÔÕÖúùûüÚÙÛÜçÇñÑ]/gi, '');
      this.selPlvMaquina.versao = versao
      if(!!versao){
        // let result = versao.search(reg)
        let result = versao.split('')
        result[0] == '.' || result[0] == '-'   ? this.valida.versao = false : this.valida.versao = true;
      } else {
        this.valida.versao = null
      }
    },
    /* LISTA OS ARQUIVOS DISPONIVEIS */
    getListaArquivos() {
      this.loaderCheck = false
      this.listaArquivos = this.arquivos
      this.getListaMaquinas() // CARREGA A LISTA DE MAQUINAS
      console.log('OPA, ME ACESSARAM AQUI PESSOAL!! :) ');
      console.log('QTD DE ARQUIVOS DUPLICADOS: '+ this.listaArquivos.length);
      
    },
    /* PROCESSA OS DADOS NO ARQUIVO DESEJADO */
    getProcessaArquivo() {
      this.loaderCheck = true
      this.listaMaquinas.length == 0  ? this.getProcessaMaquinas() : '';
      let arquivo = this.selPlvMaquina.arq
      let url = `${this.locationUrl}/palavras/geraArqPals/${this.token}/${this.setGrupoDLP}/${arquivo}/resp_rest`;
      
      document.querySelector('.botao-processamento-arquivo').disabled = true;
      setTimeout(() => {
        this.$http.get(url, myHeaders)
        .then(() => {
          this.tabIndex++
          this.respostasEnvio(true)
          this.loaderCheck = false

          console.log('URL: ');
          console.log(url);

          console.log('ARQUIVO ENVIADA');
          console.log(this.selPlvMaquina);

        })
        .catch(() => {
          this.respostasEnvio(false)
        this.loaderCheck = false
        })
      }, 5000)
    },
    /* EXCLUI ARQUIVOS DA MAQUINA LOCAL DO USUARIO */
    getExcluirArquivos() {
      this.loaderCheck = true
      this.listaMaquinas.length == 0  ? this.getProcessaMaquinas() : '';
      
      let codMaquinas = '';

      let contadorItens = 0;

      this.selPlvMaquina.maquinas.forEach((obj) => {
        if(contadorItens > 0) {
          codMaquinas = codMaquinas +','+ obj.hash_maquina;
        
        }else {
          codMaquinas = obj.hash_maquina;

        }
        
        contadorItens++;

      });

      /*console.log('MINHA RESPOTA: ');
      console.log(this.selPlvMaquina.maquinas[0].hash_maquina);

      console.log('CODIGOS DE MAQUINA: ');
      console.log(codMaquinas);*/

      let url = '';
      let cod = '';

      document.querySelector('.botao-processamento-arquivo').disabled = true;

      console.log('CODIGOS DE MAQUINA: ');
      console.log(codMaquinas);

      this.selPlvMaquina.maquinas.forEach((obj) => {
        setTimeout(() => {
          url = `${this.locationUrl}/palavras/limpaPalavrasSensiveis/${this.token}/${obj.hash_maquina}/resp_rest`;

          this.$http.get(url, myHeaders)
          .then(() => {
            this.respostasEnvio(true)
            this.loaderCheck = false

            console.log('URL: ');
            console.log(url);

            console.log('ARQUIVO ENVIADA');
            console.log(this.selPlvMaquina);

            //this.getGeraJob();

          })
          .catch(() => {
            this.respostasEnvio(false)
            this.loaderCheck = false
          })
        }, 5000)
      })

      this.getGeraJob();

    /*this.selPlvMaquina.maquinas.forEach((obj) => {
        setTimeout(() => {
          cod = obj.hash_maquina;

          console.log('CODIGOS DE MAQUINA: ');
          console.log(cod);

          setTimeout(() => {
            url = `${this.locationUrl}/palavras/limpaPalavrasSensiveis/${this.token}/${cod}/resp_rest`;

            this.$http.get(url, myHeaders)
            .then(() => {
              this.respostasEnvio(true)
              this.loaderCheck = false

              console.log('URL: ');
              console.log(url);

              console.log('ARQUIVO ENVIADA');
              console.log(this.selPlvMaquina);

              this.getGeraJob();

            })
            .catch(() => {
              this.respostasEnvio(false)
              this.loaderCheck = false
            })
          }, 5000)

        }, 5000)

      });

      this.getGeraJob();*/

    },
    /* LISTA AS MAQUINAS DESEJADAS PARA QUE POSSA SER ATRIBUIDA A ELAS AS PALAVRAS NA QUAL SE DESEJA MONITORAR */
    getGeraJob() {
      this.loaderCheck = true

      let arquivo = this.selPlvMaquina.arq
      let versao = this.selPlvMaquina.versao

      let codMaquinas = '';

      let contadorItens = 0;

      this.selPlvMaquina.maquinas.forEach((obj) => {
        if(contadorItens > 0) {
          codMaquinas = codMaquinas +','+ obj.computer_name;
        
        }else {
          codMaquinas = obj.computer_name;

        }
        
        contadorItens++;

      });

      console.log('MAQUINAS A SEREM INSERIDOS OS ARQUIVOS: ');
      console.log(codMaquinas);

      //let maquinas = JSON.stringify(this.selPlvMaquina.maquinas).replaceAll('"', '').replaceAll(' ','').replace('[','').replace(']','')
      let maquinas = codMaquinas;
      
      let url = `${this.locationUrl}/palavras/enviaPals/${this.token}/${maquinas}/${arquivo}/${versao}/resp_rest`;

      setTimeout(() => {
        this.$http.get(url, myHeaders)
        .then(res => {
          let resp = res.data
          resp = resp.split(',')
          this.selPlvMaquina.job = resp[1]
          setTimeout(() => {
            this.getProcessaMaquinas() 
          }, 100)
          this.loaderCheck = false

          console.log('URL');
          console.log(url);

          console.log('ARQUIVO ENVIADA');
          console.log(this.selPlvMaquina);

          console.log('RESPOSTA DA REQUISICAO');
          console.log(resp);

        })
        .catch(() => {
          this.respostasEnvio(false)
          this.loaderCheck = false
        })  
      }, 5000)
    },
    /* PROCESSA/ATUALIZA OS DADOS PARA AS MAQUINAS LISTADAS */
    getProcessaMaquinas() {
      this.loaderCheck = true
      let job = this.selPlvMaquina.job
      let param = JSON.stringify({
        "tabela":"gestao_arquivos",
        "select":`job='${job}'`,
        "pagina":"1",
        "qt":"1000",
        "campos_select_end_point":"job^maquina^computer_name^grupo_dlp^arquivo_origem^arquivo_destino^status^versao^dth",
        "get_qt":"1"
      })
      let url = `${this.locationUrl}/sql/selectSql/${this.token}/resp_rest`;
      setTimeout(() => {
        this.$http.post(url, param, myHeaders)
        .then((res) => {
          let resp = res.bodyText
          resp = resp.replace('[', '[{').replace(']', ']}]').replace(', ', '}, {"dados":[').replaceAll(/\n/ig,'').replaceAll('\\', '\\\\')
          resp = JSON.parse(resp)
          resp = resp[1].dados
          resp.forEach((obj) => {
            obj.fullMaq = `${obj.computer_name}-${obj.maquina}`
          })
          this.listaProcesso = resp
          this.selPlvMaquina.modal = 'xl'
          this.tabIndex = 2
          this.respostasEnvio(true)
          this.loaderCheck = false
        })
        .catch(() => {
          this.respostasEnvio(false)
          this.loaderCheck = false
        })
      }, 5000)
    },
    /* LISTA AS MAQUINAS CADASTRADAS */
    getListaMaquinas() {
      this.loaderCheck = true
      let url = `${this.locationUrl}/maquinas/listaSecret/${this.token}/resp_rest`;
      setTimeout(() => {
        this.$http.get(url, myHeaders)
        .then(res => {
          let resp = res.data
          let dados = [];

          resp.forEach(obj => {

            const dadosUsuario = {
              computer_name: obj.computer_name,
              hash_maquina: obj.hash_maquina
            };

            dados.push(dadosUsuario);

          });
          
          this.listaMaquinas = [ ... new Set(dados)]
          this.loaderCheck = false

          console.log('MEU OBJETO::: ');
          console.log(this.listaMaquinas);

        })
        .catch(() => {
          this.respostasEnvio(false)
          this.loaderCheck = false
        })
      }, 5000)
    },
    /* TRAZ OS DADOS DA LISTA DE PALAVRAS SENSÍVEIS */
    getLocalizaMaquinaArray(pesquisa) {
      /* AO CARREGAR O MODAL DE ENVIO DE PALAVRAS SENSIVEIS, E ZERADO O ARRAY DE MAQUINAS E ARMAZENADOS OS DADOS COLETADOS DO BANCO EM UMA VARIAVEL DE MEMORIA (AUXILIAR) */
      if(this.travaFiltro == false) {
        /* RECEBE OS DADOS DAS MAQUINAS VINDAS DO BANCO DE DADOS */
        this.memoriaValores = this.listaMaquinas;
        /* INSERE OS DADOS POPULADOS NA VARIAVEL DE MEMORIA (AUXILIAR) */
        this.memoriaMaquinas = this.memoriaValores;

        /* ZERA O ARRAY DE MAQUINAS */
        this.listaMaquinas = [];

        /* BLOQUEIA ESSE IF ATE QUE O USUARIO FECHE O MODAL E ABRA DE NOVAMENTE */
        this.travaFiltro = true;

      }

      /* NAVEGO NA VAVIAVEL DE MEMORIA (AUXILIAR) E BUSCA PELA PESQUISA DE MAQUINA REALIZADA PELO USUARIO NO MODAL */
      if(this.memoriaMaquinas.indexOf(pesquisa) >= 0) {
        /* COM O ARRAY QUE E APRESENTADO AO USUARIO ZERADO, SEMPRE QUE UMA MAQUINA E SELECIONADA ELA E INSERIDO NOVAMENTE NO ARRAY FICANDO VISIVEL AO USUARIO */
        this.listaMaquinas.push(this.memoriaMaquinas[this.memoriaMaquinas.indexOf(pesquisa)]);

      } else {
        console.log("Não está no array");

      }

    },
    /* RETORNA UMA RESPOSTA DA REQUISICAO CASO O ENVIO TENHA SIDO BEM SUCEDIDO OU NÃO  */
    respostasEnvio(resp) {
      if(!!resp) {
        if(this.tabIndex == 0) {
          this.resposta = {
            status: 1,
            texto: `Lista de Arquivos foi processado com sucesso!`
          }
        } else if(this.tabIndex == 1) {
          this.resposta = {
            status: 1,
            texto: `O processamento do arquivo ${ this.selPlvMaquina.arq } com a versão ${ this.selPlvMaquina.versao } foi realizado com sucesso!`
          }
        } else if(this.tabIndex == 2) {
          this.resposta = {
            status: 1,
            texto: `O processamento de arquivo para as maquinas foi realizado com sucesso, caso haja pendência de máquinas as mesmas serão atualizadas quando estiverem online.`
          }
        } else {
          this.resposta = {
            status: 2,
            texto: `Estamos em Processamento!`
          }
        }
      } else {
        this.resposta = {
          status: 2,
          texto: `Ocorreu erro no processamento, tente novamente!`
        }
      }
    },
    fechaModal(ref) {
      this.$refs[ref].hide()
      this.tabIndex = 0
      this.listaArquivos = []
      this.listaMaquinas = []
      this.listaProcesso = []
      this.selPlvMaquina = {
        arq: '',
        versao: '',
        grupo_dlp: '',
        maquinas: [],
        job: '',
        modal: 'lg'
      }
      this.getListaArquivos()
    },
    abreModal(ref){
      //this.$refs[ref].hide()
      this.$bvModal.show(ref);
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
    }
  },
  mounted(){
    let tk = window.atob(this.token)
    tk = tk.split('^')
    this.setGrupoDLP = tk[1]
    this.getListaArquivos()
  }
}
</script>
