<template>
  <div>
    <b-button
      class="btn-group-left"
      :id="`btnEvidencia_${maquina}`" 
      :ref="`btnEvidencia${maquina}`" 
      title="Evidências" 
      
      @click="chamaEvidencias(), showModal()"
      variant="primary"
      close-on-esc
    >
      <b-icon icon="images"></b-icon> <!--Evidências-->
    </b-button>
     <b-modal 
      :id="`modEvidencia_${maquina}`" 
      :title="`Evidências: ${_props.setMaquina} - ${_props.codMaquina} / ID: ${_props.idAlarme}`"
      ok-only :ref="`modEvidencia_${maquina}`" 
      centered 
      no-close-on-backdrop 
      close-on-esc 
      hide-footer 
      v-if="show" 
      size="xl"
    >
      <div>
        <b-carousel
          id="slideEvidencia"
          v-model="slide"
          controls
          no-animation
          indicators
          background="transparent"
          :interval="60 * 60 * 1000"
          v-if="listaEvidencias.length > 0"
        >
          <b-carousel-slide v-for="(item, index) of listaEvidencias" :key="index">
            <template v-slot:img>
              <p class="imagem-rodape" v-if="!!item.src">
                {{ trataNomeArquivo(item.src) }} <br>
              </p>
              <img
                class="tamanho"
                :src="locationImg + item.src"
                v-if="!!item.src"
              >
              <div class="slideTexto" v-if="item.texto"><p v-html="item.texto"></p></div>
            </template>
          </b-carousel-slide>
        </b-carousel>
      </div>
     </b-modal>
  </div>    
</template>
<script>
var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');
myHeaders.append('Origin', 'https://app.epsoft.com.br', 'https://app.epsoft.com.br:8086');
  export default {
    props:[
      'grupoDlp',
      'setMaquina',
      'codMaquina',
      'setMaquinaFull',
      'tempo',
      'handler',
      'idAlarme',
      'milis'
    ],
    data() {
      return {
        //maquina: !this.setMaquinaFull ? `${this._props.grupoDlp.toUpperCase()}-${this._props.setMaquina}-${this._props.codMaquina}` : this.setMaquinaFull,
        maquina: `${this.grupoDlp.toUpperCase()}-${this.setMaquina}-${this.codMaquina}`,
       
        listaEvidencias: [],
        slide: 0,
        sliding: null,
        show: true,
        text: '',
        token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret,
        locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
        locationImg: this.$store.state.variaveisAmbiente.locationImg,
        isDisabled: false,
      }
    },
    methods: {
      trataNomeArquivo(nomeArquivo) {
        const parte = nomeArquivo.split('/');
        return parte[parte.length - 1];
      },

    converteMilisegundos(d) {
      var data = d
      if(data.indexOf(':') > -1) {
        var tempo = Date.parse(data)
        return tempo
      } else {
        data = data.split(' ')
        var dia = data[0]
        var hora = data[1]
        hora = hora.replaceAll('-', ':')
        data = dia + ' ' + hora
        var tempo = Date.parse(data)
        return tempo
      }
    },
  async chamaEvidencias() {
      this.loaderCheck = true
      this.listaEvidencias = [];

      // console.log(this.tempo)
      //var inicio = this.converteMilisegundos(this.tempo) - 90000;
      //var fim = this.converteMilisegundos(this.tempo) + 90000;
      //var inicio = this.converteMilisegundos(this.tempo);
      //var fim = this.converteMilisegundos(this.tempo);
      var id = this.handler
      var novaBusca =  this.grupoDlp.toUpperCase()+"-"+this.setMaquina+"-"+this.codMaquina+"|"+this.milis+"|"+this.milis+"|"+id;
      //var info = `${this.maquina}|${inicio}|${fim}|${id}`
      var info = `${this.maquina}|${this.milis}|${this.milis}|${id}`
      // alert(this.milis);
      var url = `${this.locationUrl}/getScreenshotsListSecret/${this.token}/resp_rest`;
      console.log("ANTES: " + novaBusca);
      // console.log(url + ' | ' + info)
      
      await this.$http.post(url, novaBusca)
        .then(res => {

          setTimeout(() => {
            this.loaderCheck = false
            var dados = res.data;
            this.listaEvidencias = dados
             console.log('EVIDENCIAS ENCONTRADAS: ');
             console.log('DADOS/INFORMACOES DE PESQUISA DE EVIDENCIAS DA MAQUINA ENVOLVIDA NO ALERTA: ');
             console.log(info);
             console.log("OK máquina: " + novaBusca);
             info = "";
          }, 1000)
        })
        .catch(err => {
          if(err.status == 404) {
            '<p style="color:red;">This is tip</p>';
            console.log('Status Erro: '+ err.status);
            console.log('Nao existe evidencias nesse alerta');
            console.log("catch erro: " + novaBusca);
            info = "";
          }
        });
      },
      onSlideStart(slide) {
        this.sliding = true
      },
      onSlideEnd(slide) {
        this.sliding = false
      },
      showModal() {
        this.$refs[`modEvidencia_${this.maquina}`].show()
      },
      hideModal() {
        this.$refs[`modEvidencia_${this.maquina}`].hide()
      },
      showModalErro() {
        this.atualizaImagem()
        this.$refs[`modErro${this.maquina}`].show()
      },
      hideModalErro() {
        this.$refs[`modErro${this.maquina}`].hide()
        this.pararFilmagem()
      },
      showModalMsg() {
        this.atualizaImagem()
        this.$refs[`modMsg${this.maquina}`].show()
      },
      hideModalMsg() {
        this.$refs[`modMsg${this.maquina}`].hide()
      },
    },   

    mounted(){
      this.hideModal()
      // this.chamaEvidencias()
    }
  }
</script>
<style scoped>
#slideEvidencia img.tamanho {
  display: block;
  width: 100%;
  height: auto;
  max-height: 90vh; 
  max-width: 90vw;
  margin: 0 auto !important;
}
.modal {
  z-index: 2000;
}
#slideEvidencia .ajustaImg {
  display: block;
  width: auto !important;
  height: auto !important;
  max-width: 100%;
  max-height: 60vh;
}
#slideEvidencia .slideTexto {
  position: relative;
  display: block;
  overflow-x: hidden;
  overflow-y: scroll;
  width: 80%;
  min-height: 40vh;
  max-height: 60vh;
  box-sizing: border-box;
  padding: 10px 10px;
  margin: 15px auto 30px;
}
.msgEvidencia .imgLoad {
  font-size: 5em;
}
.msgEvidencia .imgAlert {
  font-size: 5em;
  display: block;
  margin: 15px auto;
}

</style>
