function getToken() {
  let token = JSON.parse(localStorage.getItem('user')).keyAcesssSecret;
  let tk = window.atob(token).split('^').reverse();
  let tk2 = tk[1].split('-')
  token = {"user": tk2[0], "grupoDlp": tk[0]}
  console.log(token)
  return token;
};