import Header from '../../shared/header/Header'
import Menu from '../../shared/menu/Menu'
import subMenu from '../../shared/subMenu/subMenu'
import Loader from '../../shared/loader/loader'
import { data } from 'jquery';

const filtroDados = require('../../shared/filtrosDeDados');
const retiraLoader = require('../../shared/retiraLoader');

var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
    components: {
    'meu-painel': Header,
    'menu-lateral': Menu,
    'sub-menu': subMenu,
    'loader': Loader
  },
  data() {
    return {
      //variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      loaderCheck: true,
      loaderCheckUsuario: true,
      //Definição dos itens do sub-menu
      listaSubMenu: ['sms','email', 'desliga', 'reinicia', 'reiniciaDlp', 'paraApp', 'exeCmd'],
      //seleciona maquina
      selMaquina: '',
      selMaquinaData: '',
      setListaMaquinas: [],
      setListaDatas: [],
      listaAcoes: [],
      tabListaAcoes: [],
      tabListaOcorrencias: [],
      tabTitulo: 'Total',
      evidencias: [],
      computer_maquina_alertas: '',
      imgFull: '',
      chkIndex: null,
      // modal
      modSelMaquina: false,
      liberaSMS: false,
      titTabela: [
        { key: 'actions', label: 'Ações' },
       // { key: 'tipoJanela', label: 'Tipo', sortable: true },
        { key: 'dataHoraPrimeiroEvento', label: 'Horário', sortable: true },
        { key: 'tituloJanela', label: 'Título', sortable: true },
        { key: 'userName', label: 'Usuário', sortable: false },
        { key: 'executavel', label: 'Executável', sortable: true },
        { key: 'pid', label: 'PID', sortable: false },
        { key: 'handlerJanela', label: 'Janela', sortable: false },
        { key: 'exibicao', label: 'Tempo em Exibição', sortable: true }
      ],
      titTabelaOcorrencias: [
        { key: 'dataHoraEvento', label: 'Horário', sortable: false },
        { key: 'tituloJanela', label: 'titulo', sortable: false },
        { key: 'tipoEvento', label: 'Tipo', sortable: false },
        { key: 'ocorrencia', label: 'Ocorrência', sortable: false }
      ],
      estatisticas: {
        total: 0,
        permitidas: 0,
        proibidas: 0,
        desconhecidas: 0,
        ocorrencias: 0
      },
      btnSel: {
        total: false,
        permitidas: false,
        proibidas: false,
        desconhecidas: false,
        ocorrencias: false
      },
      totalRows: 1,
      totalOcorrencias: 0,
      currentPage: 1,
      perPage: 20,
      pageOptions: [10, 20, 50],
      filter: null,
      infoModal: {
        id: 'info-modal',
        title: '',
        content: '',
      },
      //Carousel
      slide: 0,
      sliding: null,
      show: true,
      text: '',
      xlsTitulo: {
        'Tipo de Janela': 'tipoStatusJanela',
        'Data de Criação': 'dtCriacao',
        'Máquina': 'maquina',
        'Usuário da Máquina': 'userName',
        'Grupo DLP': 'grupo_dlp',
        'Data do Primeiro Evento': 'dtEvento',
        'Código da Janela': 'handlerJanela',
        'Título da Janela': 'tituloJanela',
        'Executável': 'executavel',
        'PID': 'pid',
        'Caminho das Imagens': 'caminhoScreenshots',
        'Caminho das Janelas do DLP': 'caminhoDlpJanelas',
        'Eventos da Janela': 'eventos',
        'Tempo de Duração (Seg)': 'tempoExibicao'
      },
      xlsDados: []
    }
  },
  computed: {
    sortOptions() {
      return this.fields
        .filter(f => f.sortable)
        .map(f => {
          return { text: f.label, value: f.key }
        })
    }
  },
  watch: {
    tabListaAcoes (t) {
     this.geraXls(t)
    },
    selMaquina(maq) {
      this.selMaquina = maq.toUpperCase()
      !!maq ? this.listaDatas(maq) : this.selMaquinaData = '';
    },
    selMaquinaData(dat) {
       var x = this.selMaquina+'|'+dat
       this.setCrpStorage(x)
    },
    totalRows (t) {
      this.totalRows = t;
    },
    totalOcorrencias (x) {
      this.totalOcorrencias = x;
    },
    listaAcoes(l) {
      this.geraEstatisticas()
    }
  },
  methods: {
    geraXls(d){
      var dados = d;
      if(!!dados) {
        dados.forEach(obj => {
          if(obj.tipoJanela === 1) {
            obj.tipoStatusJanela = 'Permitida';
          } else if(obj.tipoJanela === 2) {
            obj.tipoStatusJanela = 'Proibida';
          } else if(obj.tipoJanela === 3) {
            obj.tipoStatusJanela = 'Desconhecida'
          }
          obj.dtCriacao = this.formatDateMilisec(obj.dataHoraCriacao);
          obj.maquina = this.selMaquina;
          obj.dtEvento = this.formatDateMilisec(obj.dataHoraPrimeiroEvento);
          var duracao = obj.exibicao;
          duracao = duracao.toFixed()
          obj.tempoExibicao = duracao //.indexOf('.') > -1 ? duracao.replaceAll('.', ',') : duracao;
          var eventoJanela = obj.arrayEventosJanela;
          eventoJanela.dataHoraEvento = this.formatDateMilisec(eventoJanela.dataHoraEvento);
          eventoJanela = JSON.stringify(eventoJanela);
          eventoJanela = eventoJanela.indexOf('[') > -1 ? eventoJanela.replaceAll('[', '') : '';
          eventoJanela = eventoJanela.indexOf(']') > -1  ? eventoJanela.replaceAll(']', '') : '';
          eventoJanela = eventoJanela.indexOf('},{') > -1  ? eventoJanela.replaceAll('},{', '; \n') : '';
          eventoJanela = eventoJanela.indexOf('{') > -1  ? eventoJanela.replaceAll('{', '') : '';
          eventoJanela = eventoJanela.indexOf('}') > -1  ? eventoJanela.replaceAll('}', '') : '';
          eventoJanela = eventoJanela.indexOf('":') > -1  ? eventoJanela.replaceAll('":', ': ') : '';
          eventoJanela = eventoJanela.indexOf('","') > -1  ? eventoJanela.replaceAll('","', ', ') : '';
          eventoJanela = eventoJanela.indexOf('"') > -1  ? eventoJanela.replaceAll('"', '') : '';
          eventoJanela = eventoJanela.indexOf('\\\\') > -1  ? eventoJanela.replaceAll('\\\\', '\\') : '';
          eventoJanela = eventoJanela.indexOf('handlerJanela') > -1  ? eventoJanela.replaceAll('handlerJanela', 'Código da Janela') : '';
          eventoJanela = eventoJanela.indexOf('tituloJanela') > -1  ? eventoJanela.replaceAll('tituloJanela', 'Título da Janela') : '';
          eventoJanela = eventoJanela.indexOf('tipoEvento') > -1  ? eventoJanela.replaceAll('tipoEvento', 'Tipo Evento') : '';
          eventoJanela = eventoJanela.indexOf('dataHoraEvento') > -1  ? eventoJanela.replaceAll('dataHoraEvento', 'Hora do Evento') : '';
          obj.eventos = eventoJanela;
        })
        this.xlsDados = dados
      }
    },
    voltaPagina() {
      this.$router.go(-1)
    },
    verificaMaquina() {
      var maqSession = this.getCrpStorage() 
      var link = window.location.hash
      link = link.replace('#/monitoramento/','')
      var maquina = '';
      var grupo = '';
      var hoje = new Date()
      if(!!link) {
        link = link.split('/').reverse()
        maquina = `${link[1]}-${link[0]}`
        var dia = hoje.getDate()
        dia < 10 ? dia = '0' + dia : dia;
        var mes = hoje.getMonth() + 1
        mes < 10 ? mes = '0' + mes : mes;
        var ano = hoje.getYear() - 100 
        hoje = ano.toString() + mes.toString() + dia.toString()
        this.selMaquina = maquina
        this.selMaquinaData = hoje
        var param = this.selMaquina + '|' + this.selMaquinaData
        this.listaAcoesMaquina(param)
      } else  {
        if(!!maqSession) {
          var maq = maqSession.split('|')
          this.selMaquina = maq[0]
          this.selMaquinaData = maq[1]
          this.listaAcoesMaquina(maqSession)
          } else {
          this.listaMaquinas()
          this.modSelMaquina = true;
        }
      }
    },
    filtrarPorComputerName() {
      //this.listaMaquinas()


      let memoriaMaquinas = [];
      memoriaMaquinas = this.setListaMaquinas;
      this.setListaMaquinas = [];

      memoriaMaquinas.map(item => {
        if(item.includes(this.computer_maquina_alertas)) {
          console.log('ITEM IDENTIFICADO: '+ this.computer_maquina_alertas);
          this.setListaMaquinas.push(item);
        } 
      });

      if(this.setListaMaquinas.length < 2) {
        console.log('NAO FORAM ENCONTRADAS MAQUINAS COM O FILTRO DESEJADO, LISTA RECARREGADA');
        this.listaMaquinas()
      }

      if(this.computer_maquina_alertas.length == 0) {
        console.log('FILTRO ZERADO, LISTA RECARREGADA');
        this.listaMaquinas()
      }

      console.log('LISTA DE MAQUINAS FILTRADAS');
      console.log(this.setListaMaquinas);

    },
    novaPesquisa() {
      this.listaMaquinas()
      this.modSelMaquina = true;
    },
    dataHeader(data){
      return data.slice(4,6) + "/" + data.slice(2,4) + "/20" + data.slice(0,2)
    },
    horaFormat(t) {
      var tempo = new Date(t).toString()
      tempo = tempo.split(' GMT')
      tempo = tempo[0].split(' ').reverse()
      tempo = tempo[0]
      return tempo 
    },
    formatDateMilisec(d) {
      let tempo = d
      let bGMT =  3 * 60 * 60 * 1000
      tempo = tempo - bGMT
      tempo = new Date(tempo)
      var dia = tempo.getDate()
      var mes = tempo.getMonth() + 1
      var ano = tempo.getFullYear()
      var hora = tempo.getUTCHours()
      var min = tempo.getMinutes()
      var seg = tempo.getSeconds()
      var dataOk = `${dia < 10 ? '0' + dia : dia}/${mes < 10 ? '0' + mes : mes}/${ano} - ${hora < 10 ? '0' + hora : hora}:${min < 10 ? '0' + min : min}:${seg < 10 ? '0' + seg : seg}`
      return dataOk
    },
    htmlFormat(txt){
      let  texto = txt
      texto = texto.replace(/=(\r\n|\n|\r)/gm , '<br>')
    },
    formatOcorrencia(o) {
      let dados = o
      if(!!dados) {
        dados = JSON.stringify(dados + ';')
        dados = dados.replace(';|', ';')
        return dados
      } else {
        return ''
      }
    },
    async listaMaquinas() {
      this.loaderCheck = true
      this.loaderCheckUsuario = false;
      let url = this.locationUrl + '/getWatcherMachinesListSecret/'+ this.token +'/resp_rest'
      await this.$http.post(url, myHeaders)
      .then(res => {
        this.loaderCheck = false
        localStorage.removeItem('x-mon-maq')
        this.setListaMaquinas = res.data
        // console.log('LISTA DE MAQUINAS: ');
        // console.log(this.setListaMaquinas);
      })
      .catch(function (err){
        this.loaderCheck = false
        this.setListaMaquinas = []
        !!err
      })
    },   

    async listaDatas (maquina) {
      this.loaderCheck = true
      let url = this.locationUrl + '/getWatcherDatesListSecret/'+this.token+'/resp_rest'
      await this.$http.post(url, maquina, myHeaders)
      .then(res => {
        this.loaderCheck = false
        var lista = res.data;
        lista = lista.reverse();
        this.setListaDatas = lista.map(data => {
          return {
            text: data.slice(4,6) + "/" + data.slice(2,4) + "/20" + data.slice(0,2),
            value: data
          }
        })
      })
      .catch(function (e){
        this.loaderCheck = false
        this.setListaDatas = []

      })
    },

    botaoSelecionado(btn) {
      this.btnSel = {
        total: false,
        permitidas: false,
        proibidas: false,
        desconhecidas: false,
        ocorrencias: false
      },
      this.btnSel[btn] = true
    }, 
    
    async listaAcoesMaquina() {
      this.loaderCheck = true
      this.loaderCheckUsuario = true;
      this.listaAcoes = []
      let param = this.selMaquina + '|' + this.selMaquinaData;
      let url = this.locationUrl + '/getDataSecret/'+this.token+'/resp_rest'
      console.log("URL");
      console.log(url);
      console.log("PARAMETROS DE PESQUISA: ");
      console.log(param);
      console.log("Valor loaderCheck1: "+ this.loaderCheck);
      await this.$http.post(url, param, myHeaders)
      .then(res => {
        this.loaderCheck = false
        let resp = JSON.parse(res.data.arrayJanelas)

        console.log("DADOS MAQUINAS MONITORAMENTO WATCHER");
        console.log("URL: ");
        console.log(url);
        console.log("RESPOSTA REQUISICAO: ");
        console.log(res);
        console.log("PARAMETROS DE PESQUISA: ");
        console.log(param);

        if(!!resp) {
          let cont = 0
          let maq = this.selMaquina.split('-')
          resp.forEach(obj => {
            let tmp = obj.arrayEventosJanela
            obj.idx = cont++
            if(tmp && tmp.length > 0) {
              obj.exibicao = (tmp[tmp.length - 1].dataHoraEvento - tmp[0].dataHoraEvento) / 1000
              obj.dataHoraPrimeiroEvento = tmp[0].dataHoraEvento
            }
            obj.computer_name = maq[1]
            obj.cod_maquina = maq[2]
            obj._showDetails = false
          })
          this.listaAcoes = resp.reverse()
          console.log(this.listaAcoes)
          this.geraEstatisticas()
          this.listaAcoesTodas()
          console.log("QTD: "+ this.listaAcoes.length);
          this.liberaLoader(this.listaAcoes.length);
        } else {
          this.listaMaquinas()
          this.modSelMaquina = true;
        }
        console.log("Valor loaderCheck2: "+ this.loaderCheck);
      })
      .catch( (err) => {
        this.loaderCheck = false
        this.loaderCheckUsuario = false;
        !!err ? this.listaAcoes = [] : '';
      })
    },




    listaAcoesTodas() {
      this.tabListaAcoes = [];
      this.tabTitulo = "Total";
      this.botaoSelecionado('total');
      this.tabListaAcoes = this.listaAcoes;
      this.totalRows = this.tabListaAcoes.length;

      /**Lista ocorrencias */
      this.tabListaOcorrencias = this.listaAcoes.filter(obj => {
        for (let i = 0; i < obj.arrayEventosJanela.length; i++) {
          const arr = obj.arrayEventosJanela[i];
          if(arr.tipoEvento.toLowerCase().includes('ocorrencia')){	
            return arr;
          }
        }
      })
      this.totalOcorrencias = this.tabListaOcorrencias.length;      
    },
    liberaLoader(qtditens) {
      let tempo = qtditens * 5;
      console.log("tempo: "+ tempo);
      console.log("QTD itens: "+ qtditens);
      setTimeout(() => {
        this.loaderCheckUsuario = false;
        console.log(this.loaderCheckUsuario)
       }, tempo);
       console.log(this.loaderCheckUsuario)
    },
    listaAcoesPermitidas() {
      this.tabListaAcoes = [];
      this.tabTitulo = "Permitidas"
      this.botaoSelecionado('permitidas')
      this.tabListaAcoes = this.listaAcoes.filter(obj => obj.tipoJanela == 1)
      this.totalRows = this.tabListaAcoes.length;
    },
    listaAcoesProibidas() {
      this.tabListaAcoes = [];
      this.tabTitulo = "Proibidas"
      this.botaoSelecionado('proibidas')
      this.tabListaAcoes = this.listaAcoes.filter(obj => obj.tipoJanela == 2)
      this.totalRows = this.tabListaAcoes.length;
    },
    listaAcoesDesconhecidas() {
      this.tabListaAcoes = [];
      this.tabTitulo = "Desconhecidas"
      this.botaoSelecionado('desconhecidas')
      this.tabListaAcoes = this.listaAcoes.filter(obj => obj.tipoJanela == 3)
      this.totalRows = this.tabListaAcoes.length;
    },
    listaAcoesOcorrencias() {
      this.tabListaAcoes = [];
      this.tabTitulo = "Evidências"
      this.botaoSelecionado('ocorrencias')

      this.tabListaAcoes = this.tabListaOcorrencias;
      this.totalRows = this.tabListaAcoes.length;
      this.totalOcorrencias = this.tabListaAcoes.length;
    },
    async listaEvidencias(caminho, dtInicio, dtFim, janela) {
      this.loaderCheck = true
      this.evidencias = []
      caminho = caminho.replace(/\\/g, '\\\\')
      let param = caminho + "|" + dtInicio + "|" + dtFim + "|" + janela
      var url = this.locationUrl+"/getScreenshotsListSecret/"+this.token+"/resp_rest"
      await this.$http.post(url, param, myHeaders)
      .then(res => {
        this.loaderCheck = false
        this.evidencias = res.data
      })
      .catch( err => {
        this.loaderCheck = false
        this.evidencias = []
      })
    },
    modalImgFull(url) {
      this.imgFull = url
      this.abreModal('modImgFull')
    },
    geraEstatisticas() {
      this.estatisticas = { total: 0, permitidas: 0, proibidas: 0, desconhecidas: 0, ocorrencias: 0 },
      this.estatisticas.total = this.listaAcoes.length
      this.listaAcoes.forEach(janela => {
        if (janela.tipoJanela === 1) { this.estatisticas.permitidas++ }
        if (janela.tipoJanela === 2) { this.estatisticas.proibidas++ }
        if (janela.tipoJanela === 3) { this.estatisticas.desconhecidas++ }
        janela.arrayEventosJanela.forEach(evento => {
          if (evento.tipoEvento === 'ocorrencia') { 
            this.estatisticas.ocorrencias++
            janela.ocorrencia = true
          }
        })
      });
    },
    tamanhoJanela() {
      return window.innerWidth
    },
    duploClickLinha(row, index) {
      if(this.chkIndex != index) {
        this.tabListaAcoes.forEach(obj => obj._showDetails = false)
        this.chkIndex = index
        let caminho = row.caminhoScreenshots
        let dtInicio = row.arrayEventosJanela[0].dataHoraEvento
        let dtFim = row.arrayEventosJanela
        dtFim = dtFim[dtFim.length -1].dataHoraEvento
        let janela = row.handlerJanela
        this.listaEvidencias(caminho, dtInicio, dtFim, janela)
      }
      row._showDetails = !row._showDetails;
    },
    pegaCodMaquina() {
      var maq = this.selMaquina.split('-')
      maq = maq.reverse()
      return maq[0]
    },
    pegaMaquina(){
      var maq = this.selMaquina.split('-')
      maq = maq.reverse()
      var remove = '-' + maq[0]
      return this.selMaquina.replace(remove, '')
    },
    getParaphase() {
      return this.token +'%'+ this.locationInfo
    },
    setCrpStorage(dados){
      var dados = dados
      dados = this.$CryptoJS.AES.encrypt(dados, this.getParaphase()).toString()
      localStorage.setItem('x-mon-maq', dados)
    },
    getCrpStorage() {
      var dados = localStorage.getItem('x-mon-maq')
      dados = !!dados ? this.$CryptoJS.AES.decrypt(dados, this.getParaphase()).toString(this.CryptoJS.enc.Utf8) : '';
      return dados
    },
    abreModal(ref) {
      this.$refs[ref].show()
    },
    fechaModal(ref) {
      this.$refs[ref].hide()
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
    }
  },
  mounted() {
    this.loaderCheck = true;
    this.verificaMaquina();
    window.onresize = this.tamanhoJanela()

    /* FICA OBSERVANDO SEMPRE QUE A PAGINA E RECARREGADA */
    console.log("Teste: "+ (new Date().getMinutes() - parseInt(localStorage.getItem('dataLogin'))));

    if((new Date().getMinutes() - parseInt(localStorage.getItem('dataLogin'))) >= localStorage.getItem('limiteSessao')) {
      localStorage.removeItem('infoUser');
      localStorage.removeItem('user');
      localStorage.removeItem('dataLogin');

      location.reload(true);

    }
    
  }
}
