    /* FILTRA O TOKEN RETIRANDO O SINAL DE ^ */
    getGrupoDpl() {
      let tk = window.atob(this.token)
      console.log('MEU TOKEN CONVERTIDO');
      console.log(window.atob(this.token));
      tk = tk.split('^')
      return tk[1]
    },
