<template>
  <div>
    <b-modal ok-only :ref="`plvNova`" :id="`plvNova`" size="md" hide-header-close no-close-on-backdrop no-close-on-esc>
      <template #modal-title>
        Adiciona Palavra
      </template>
      <div class="container" v-if="!!dadosPlv">
        <div class="row">
          <div class="col-xs-12 col-sm-12 pb-3">
            <b-form-group
              label="Insira as palavras"
              label-for="plvListaPalavra"
              :description="`Separe as palavras com virgula (,) ou [Entre]. Quantidade de palavras ${ !!listaPalavra ? listaPalavra.length : 0}`"
              class="mb-0"
            >
              <b-form-tags
                input-id="plvListaPalavra"
                v-model="listaPalavra"
                separator=","
                :state="valida.listaPalavra = listaPalavra.length > 0"
                :placeholder="listaPalavra.length > 0 ? 'Insira mais palavras' : 'Coloque suas palavras'"
                addButtonText="Inserir"
                style="min-height: 60px"
              ></b-form-tags>
            </b-form-group>
          </div>
          <div class="col-xs-12 col-sm-12  pb-3" v-if="!!listaTipo">
            <b-form-group
              label="Tipo"
              label-for="plvTipo"
              class="mb-0"
            >
              <b-form-input 
                list="lista-tipos"
                v-model="setTipo" 
                :state="valida.tipo = !!setTipo"
                @keyup="chkTipo(setTipo)"
                maxlength="30"
              ></b-form-input>
              <!-- LISTA OS TIPOS DE PALAVRAS SENSIVEIS CADASTRADOS NO SISTEMA -->
              <datalist id="lista-tipos">
                <option v-for="item in listaTipo" :key="item">{{ item }}</option>
              </datalist>
              <!-- <b-dropdown class="drop-custom" block right :text="!setTipo ? 'Selecione' : setTipo" :variant="!setTipo ? 'outline-secondary' : 'outline-success'">
                <b-dropdown-item v-for="(item, i) in listaTipo" :key="i" @click="setTipo = item">{{item}}</b-dropdown-item>
              </b-dropdown> -->
            </b-form-group>
          </div>

          <!-- TIPO DE GRAVIDADE -->
          <div class="col-xs-12 col-sm-12  pb-3">
            <b-form-group
              label="Tipo de Gravidade"
              label-for="plvGravidade"
              class="mb-0"
            >
              <b-form-input 
                list="lista-gravidades"
                v-model="setGravidadeOpcao" 
                :state="valida.setGravidadeOpcao = setGravidadeOpcao.length > 0"
                @keyup="chkGravidadeOpcao(setGravidadeOpcao)"
                maxlength="30"
              ></b-form-input>
              <datalist id="lista-gravidades">
                <option v-for="item in opcoesDeGravidade" :key="item">{{ item }}</option>
              </datalist>
            </b-form-group>
          </div>

          <!-- GRUPOS DLPs LOCALIZADOS DE ACORDO COM O TOKEN -->
          <div class="col-xs-12 col-sm-12  pb-3">
            <b-form-group
              label="Grupo DLP"
              label-for="gruposDLPs"
              class="mb-0"
            >
            <b-form-input 
                list="lista-grupos"
                v-model="grupoSelecionado" 
                :state="valida.grupoSelecionado = grupoSelecionado.length > 0"
                @keyup="selecaoDeGrupo(grupoSelecionado)"
                maxlength="30"
              ></b-form-input>
              <datalist id="lista-grupos">
                <option v-for="item in gruposDLPs" :key="item">{{ item }}</option>
              </datalist>
            </b-form-group>
          </div>

          <!-- <div class="col-xs-12 col-sm-6  pb-3" v-if="!!listaGravidade">
            <b-form-group
              label="Classificação:"
              label-for="plvGrav"
              class="mb-0"
            >
              <b-dropdown 
                class="drop-custom" 
                block 
                right
                split 
                :text="!setGravidade ? 'Selecione' : setGravidade" 
                :variant="!setGravidade ? 'outline-secondary' : 'outline-success'"
              >
                <b-dropdown-item v-for="(item, i) in listaGravidade" :key="i" @click="setGravidade = item">{{item}}</b-dropdown-item>
              </b-dropdown>
            </b-form-group>
          </div> -->
        </div>
      </div>
      <template #modal-footer>
        <b-icon icon="arrow-clockwise" animation="spin" variant="primary" v-if="loaderCheck" font-scale="2"></b-icon>
        <b-button-group>
          <b-button variant="success" style="color: white" @click="getAddPalavras()" :disabled="!chkAddPalavra()">
            <b-icon icon="plus" class="mr-1"></b-icon>
            Incluir Palavras
          </b-button>
          <b-button variant="danger" @click="limpaPalavras('plvNova')">
            <b-icon icon="x" class="mr-1"></b-icon> Cancelar
          </b-button>
        </b-button-group>
      </template>
    </b-modal>
    <b-modal ok-only :ref="`plvConf`" :id="`plvConf`" size="xl" hide-header-close no-close-on-backdrop no-close-on-esc>
    <template #modal-title>
      Adiciona PALAVRAS CLASSIFICADAS
    </template>
    <div class="container" v-if="!!dadosPlv">
      <div class="row">
        <div class="col-xs-12 col-sm-12 p-0 pb-3">
          <!-- <div class="e-tab"> -->
            <b-table
              ref="tabAddPalavras" 
              id="tabAddPalavras"
              class="tab-add-palavras mb-0"
              show-empty
              small
              striped 
              stacked="md"
              :items="dadosPlv"
              :fields="titTabela"
              v-if="!!dadosPlv"
              empty-text="Não há registro para sua pesquisa."
            >
              <template v-slot:cell(palavra)="row">
                <div class="wrap">
                  {{row.item.palavra}}
                </div>
              </template>
              <template v-slot:cell(fonetico)="row">
                <div class="wrap">
                  {{row.item.fonetico}}
                </div>
              </template>
              <template v-slot:cell(dataCriacao)="row">
                <div>
                  {{formatData(row.item.dataCriacao)}} 
                </div>
              </template>
              <template v-slot:cell(gravado)="row">
                <div class="block text-center">
                  <b-icon icon="square-fill" variant="secondary" font-scale="1.2" class="mr-2" v-if="row.item.gravado == 0"></b-icon>
                  <b-icon icon="check-square-fill" variant="success" font-scale="1.2" class="mr-2" v-else-if="row.item.gravado == 1"></b-icon>
                  <b-icon icon="x-square-fill" variant="danger" font-scale="1.2" class="mr-2" v-else-if=" row.item.gravado == 2"></b-icon>
                </div>
              </template>
            </b-table>
          <!-- </div> -->
        </div>       
      </div>
      <!--<b-progress class="my-3" :value="progress.valor" :max="progress.max" show-progress animated v-if="progress.ver"></b-progress>-->
      <b-alert :variant="resposta.status == 1 ? 'info' : 'warning'" show v-if="resposta.status !== 0" dismissible>
        <b-icon :icon="resposta.status == 1 ? 'exclamation-circle-fill' : 'exclamation-circle-fill'" font-scale="1.2" class="mr-2"></b-icon>
        {{resposta.texto}}
      </b-alert>
    </div>
    <template #modal-footer>
      <b-icon icon="arrow-clockwise" animation="spin" variant="primary" v-if="loaderCheck" font-scale="2"></b-icon>
      <b-button-group>
        <!-- <b-button variant="info" style="color: white" @click="carregaErros()" v-if="dadosPlv.indexOf('gravado: 1') > 0">
          <b-icon icon="question-circle-fill" class="mr-1"></b-icon>
           Carregar Palavras com Erro?
        </b-button> -->
        <b-button variant="success" style="color: white" @click="salvaPalavra()">
          <b-icon icon="check" class="mr-1"></b-icon>
           Salvar Palavras
        </b-button>
        <b-button variant="danger" @click="limpaPalavras('plvConf')">
          <b-icon icon="x" class="mr-1"></b-icon> Fechar
        </b-button>
      </b-button-group>
    </template>
  </b-modal>
  </div>
</template>
<script>

const urlApi = ambientes.ativo[0].url + ":" + ambientes.ativo[0].porta_api



var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Content-Type", "text/plain");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
  props: ['listaTipo', 'listaGravidade'],
  data() {
    return {
      // variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      ambiente: this.$store.state.variaveisAmbiente.ambiente,
      loaderCheck: false,
      // vairáveis do componente
      setGrupoDLP: '',
      gruposDLPs: [],
      grupoSelecionado: '',
      listaPalavra: [],
      listaFonetico: [],
      setGravidade: '',
      setGravidadeOpcao: '',
      opcoesDeGravidade: ['1 - serio','2 - grave','3 - gravissimo'],
      setTipo: '',
      dadosPlv: [],
      dadosPlvTemp: [],
      titTabela: [
        { key: 'idx',label: 'Id',sortable: true,class: 'text-left'},
        { key: 'palavra',label: 'Palavra',sortable: true, class: 'text-left', sortDirection: 'asc'},
        { key: 'fonetico',label: 'Fonético',sortable: true, class: 'text-left'},
        { key: 'tipo',label: 'Tipo',sortable: true, class: 'text-left'},
        { key: 'grupo_dlp', label: 'Grupo DLP', sortable: true, class: 'text-left'},
        { key: 'gravidade',label: 'Gravidade',sortable: true,class: 'text-left'},
        { key: 'status', label: 'Status', class: 'text-left'},
        { key: 'gravado', label: 'gravado', class: 'text-left'},
      ],
      // validações de campos
      valida: {
        listaPalavra: null,
        selGravidade: null,
        setTipo: null,
        palavra: null,
        tipo: null,
        status: null,
        fonetico: null,
        grupo_dlp: null,
        gravidade: null
      },
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 20, 50, 100],
      filter: null,
      progress: {
        max: 100,
        valor: 0,
        ver: false
      },
      // resposta do endpoint
      resposta: {
        status: 0,
        texto: '',
        erro: 0
      }
    }
  },
  watch: {
    setTipo (t) {
      this.valida.setTipo = !!t
    },
    setGravidade (g) {
      this.valida.setGravidade = !!g
    }
  },
  methods: {
    dataAgora() {
      let agora = new Date();
      let hora = agora.toString().split(' ')
      let dia = agora.getDate()
      dia < 10 ? dia = '0' + dia : dia;
      let mes = agora.getMonth() + 1
      mes < 10 ? mes = '0' + mes : mes;
      let ano = agora.getFullYear() //- 100 
      let hoje = `${ano.toString()}-${mes.toString()}-${dia.toString()} ${hora[4]}`
      return hoje
    },
    formatData(d){
      let data = d
      if(!!data) {
        data = data.split(' ')
        let dia = data[0].split('-').reverse()
        return `${dia[0]}/${dia[1]}/${dia[2]} ${data[1]}`
      } else {
        return data
      }
    },
    chkPalavra(l) {
      let lista = l;
      lista.forEach(arr => {
        arr.length > 10 ? arr+',' : arr;
      });
    },
    chkAddPalavra() {
      return !!this.valida.listaPalavra && !!this.setTipo // && !!this.valida.setGravidade
    },
    chkBntEnvio() {
      let val = !!this.valida.palavra && !!this.valida.tipo && !!this.valida.fonetico && !!this.valida.gravidade && !!this.valida.grupo_dlp
      return val
    },
    chkTipo(t) {
      let tipo = t.replace(' ', '_').replace(/[`´~!@#$%^&*()|+\=?;:'"¨, <>\{\}\[\]\\\/àáâãäÀÁÂÃÄèéêëÈÉÊËìíîïÌÍÎÏóòôõöÓÒÔÕÖúùûüÚÙÛÜçÇñÑ]/gi, '');
      this.setTipo = tipo.toUpperCase()
    },
    selecaoDeGrupo(t) {
      let grupo = t;
      this.grupoSelecionado = grupo;
    },
    chkGravidadeOpcao(t) {
      let gravidadeOpcaoSelecionada = t.replace(' ', '_').replace(/[`´~!@#$%^&*()|+\=?;:'"¨, <>\{\}\[\]\\\/àáâãäÀÁÂÃÄèéêëÈÉÊËìíîïÌÍÎÏóòôõöÓÒÔÕÖúùûüÚÙÛÜçÇñÑ]/gi, '');
      let resultado = gravidadeOpcaoSelecionada[0]

      this.setGravidadeOpcao = resultado
    },
    // getFonetico(p) {
    //   let palavra = p
    //   this.dadosPlv.palavra.length > 0 ?
    //   this.dadosPlv.fonetico = this.dadosPlv.fonetico
    //   :
    //   ''
    // },
    verStatus(s) {
      this.dadosPlv.status = s === 'ATIVA' ? 'INATIVA' : 'ATIVA'
    },
    /* LIMPA OS DADOS PREENCHIDOS */
    limpaPalavras(ref) {
      this.dadosPlv = []
      this.listaPalavra = []
      this.setTipo = ''
      this.setGravidade = ''
      this.fechaModal(ref)
    },
    /* ADICIONA UMA NOVA PALAVRA POPULANDO O OBJETO COM OS DADOS VINDOS DO INPUT*/
    getAddPalavras() {
      let cont = 1
      this.listaPalavra.forEach(plv => {
        let obj = {
          idx: cont++,
          palavra: plv,
          tipo: this.setTipo,
          status: "ATIVA",
          fonetico: plv,
          //grupo_dlp: this.setGrupoDLP,
          grupo_dlp: this.grupoSelecionado,
          //gravidade: this.setGravidade ? this.setGravidade : 'NEUTRO',
          gravidade: this.setGravidadeOpcao ? this.setGravidadeOpcao : 'NEUTRO',
          //gravidade: this.setGravidade,
          gravado: 0
        }
        this.dadosPlv.push(obj) // ARMAZENA O O BEJTO PREENCHIDO PARA QUE POSSA SER UTILIZADO POSTERIORMENTE
      })
      this.fechaModal('plvNova')
      this.abreModal('plvConf')
    },
    /* SALVA OS DADOS DIGITADOS DENTRO DA BASE DE DADOS (EXISTE UM METODO PARECEIDO LOGO ABAIXO) */
    gravaPalavra(arr) {
      let retorno = arr.idx - 1
      let param = JSON.stringify({
        "tabela_end_point":"palavras",
        "campos_select_end_point":"palavra",
        "palavra":arr.palavra,
        "tipo":arr.tipo,
        "status":arr.status,
        "fonetico":arr.fonetico,
        "grupo_dlp":arr.grupo_dlp,
        "dataCriacao":arr.dataCriacao,
        "gravidade":arr.gravidade
        })

      console.log("GRUPO DLP ENVIADO NA PALAVRA: "+ arr.grupo_dlp);
      let url = `${this.locationUrl}/sql/updateSql/${this.token}/resp_rest`;
      this.$http.post(url, param, myHeaders)
      .then(res => {
        this.loaderCheck = false
        !!res ? true : false ;
        let grv = 0
        retorno % 2 > 0 ? grv = 1 : grv = 2;
        this.dadosPlv[retorno].gravado = grv
      })
      .catch(err => {
        !!err ? true : false ;
        this.dadosPlv[retorno].gravado = 2
      })
    },
    /* SALVA OS DADOS CHAMANDO O METODO PARA GRAVAR A PLAVRA E PASSANDO COMO PAREMTRO O OBJETO PALAVRA */
    /*salvaPalavra() {
      this.progress.ver = true
      let cont = 0
      let reg = this.dadosPlv.length
      // let retErro = this.dadosPlv
      this.dadosPlv.forEach(obj => {
        cont++
        obj.dataCriacao = this.dataAgora() // CAPTURA A HORA ATUAL
        setTimeout(() => {
          obj.gravado = this.gravaPalavra(obj) // INSERE OS DADOS NO METODO PARA QUE O MESMO POSSA SER POPULADO E ENVIAR ESSES DADOS A BASE DE DADOS
        }, (cont + 5000))
        console.log(obj.gravado)
        this.progress.valor = cont * (100 / reg)
      })
      // conclui o processo e faz as verificações
      if(cont === reg) {
        this.dadosPlv.forEach(arr => {
          if(arr.gravado == 2){ 
            this.dadosPlvTemp.push(arr)
          }
        })
        console.log(this.dadosPlvTemp.length)
        if(this.dadosPlvTemp.length > 0) {
          // this.dadosPlvTemp = retErro
          this.resposta.erro = this.dadosPlv.length
          this.respostasEnvio(false)
        } else {
          setTimeout(() => {
            this.progress.ver = false
            this.respostasEnvio(true)
          }, 2000)
        }
      }
    },*/
    salvaPalavra() {
      this.progress.ver = true
      let cont = 0
      let reg = this.dadosPlv.length
      // let retErro = this.dadosPlv

      var memoriaObj = {
        palavras: [],
        tipo: this.dadosPlv[0].tipo,
        //status: [],
        status: this.dadosPlv[0].status,
        fonetico: [],
        grupoDlp: this.dadosPlv[0].grupo_dlp,
        dataCriacao: this.dataAgora(),
        gravidade: this.dadosPlv[0].gravidade
      }

      this.dadosPlv.forEach(obj => {
        memoriaObj.palavras.push(obj.palavra);
        memoriaObj.fonetico.push(obj.fonetico);
        //memoriaObj.status.push(obj.status);
      })

      memoriaObj.palavras = JSON.stringify(memoriaObj.palavras);
      memoriaObj.fonetico = JSON.stringify(memoriaObj.fonetico);
      memoriaObj.status = JSON.stringify(memoriaObj.status);

      memoriaObj.palavras = memoriaObj.palavras.replaceAll('\"','');
      memoriaObj.palavras = memoriaObj.palavras.replaceAll('[','');
      memoriaObj.palavras = memoriaObj.palavras.replaceAll(']','');

      memoriaObj.fonetico = memoriaObj.fonetico.replaceAll('\"','');
      memoriaObj.fonetico = memoriaObj.fonetico.replaceAll('[','');
      memoriaObj.fonetico = memoriaObj.fonetico.replaceAll(']','');

      memoriaObj.status = memoriaObj.status.replaceAll('\"','');
      //memoriaObj.status = memoriaObj.status.replaceAll('[','');
      //memoriaObj.status = memoriaObj.status.replaceAll(']','');
      
      this.gravaPalavrasViaAPI(memoriaObj);

      //obj.gravado = this.gravaPalavra(memoriaObj);

      /* ATUALIZADO ENVIO PALAVRAS SENSIVIES */ 
      //this.dadosPlv.dataCriacao = this.dataAgora() // CAPTURA A HORA ATUAL
      //obj.gravado = this.gravaPalavra(obj) // INSERE OS DADOS NO METODO PARA QUE O MESMO POSSA SER POPULADO E ENVIAR ESSES DADOS A BASE DE DADOS

      // conclui o processo e faz as verificações
      /*if(cont === reg) {
        this.dadosPlv.forEach(arr => {
          if(arr.gravado == 2){ 
            this.dadosPlvTemp.push(arr)
          }
        })
        console.log(this.dadosPlvTemp.length)
        if(this.dadosPlvTemp.length > 0) {
          // this.dadosPlvTemp = retErro
          this.resposta.erro = this.dadosPlv.length
          this.respostasEnvio(false)
        } else {
          setTimeout(() => {
            this.progress.ver = false
            this.respostasEnvio(true)
          }, 2000)
        }
      }*/
    },
    /* SALVA OS DADOS DIGITADOS DENTRO DA BASE DE DADOS (EXISTE UM METODO PARECEIDO LOGO ACIMA) */
    /*gravaPalavra(arr) {
      let retorno = arr.idx - 1
      let param = JSON.stringify({
        "tabela_end_point":"palavras",
        "campos_select_end_point":"palavra",
        "palavra":arr.palavra,
        "tipo":arr.tipo,
        "status":arr.status,
        "fonetico":arr.fonetico,
        "grupo_dlp":arr.grupo_dlp,
        "dataCriacao":arr.dataCriacao,
        "gravidade":arr.gravidade
        })
      let url = `${this.locationUrl}/sql/updateSql/${this.token}/resp_rest`;
      this.$http.post(url, param, myHeaders)
      .then(res => {
        this.loaderCheck = false
        !!res ? true : false ;
        this.dadosPlv[retorno].gravado = 1
      })
      .catch(err => {
        !!err ? true : false ;
        this.dadosPlv[retorno].gravado = 2
      })
    },*/
    /* SALVA OS DADOS DIGITADOS DENTRO DA BASE DE DADOS (EXISTE UM METODO PARECEIDO LOGO ACIMA) */
    gravaPalavrasViaAPI(arr) {
      let qtdItens = arr.palavras.split(',');
      
      let retorno = arr.idx - 1

      let param = {
        "palavras":arr.palavras,
        "tipo":arr.tipo,
        "status":arr.status,
        "fonetico":arr.fonetico,
        "grupoDlp":arr.grupoDlp,
        "dataCriacao":arr.dataCriacao,
        "gravidade":arr.gravidade
      }

      console.log('OBJETO PALAVRAS ENVIO');
      console.log(param);

      //let ambiente = "POCNEW";

      //let url = `${this.locationUrl}/sql/updateSql/${this.token}/resp_rest`;
      let url = `${urlApi}/api/palflash/postpalavrasarray/${this.ambiente}/${this.token}`;
      this.$http.post(url, param, myHeaders)
      .then(res => {
        this.loaderCheck = false

        console.log('GRAVA PALAVRA VIA API');

        console.log(res.data);
        console.log(url);
        console.log('ENVIO DE DADOS AO BANCO VIA API');

        !!res ? true : false ;
        for(let i = 0; i < qtdItens.length; i++) {
          this.dadosPlv[i].gravado = 1
        }

        this.respostasEnvio(true);

        //!!res ? true : false ;
        //this.dadosPlv[retorno].gravado = 1
      })
      .catch(err => {
        //!!err ? true : false ;
        //this.dadosPlv[retorno].gravado = 2

        for(let i = 0; i < qtdItens.length; i++) {
          this.dadosPlv[i].gravado = 2
        }

        this.respostasEnvio(false);

      })
      //for(let i = 0; i < qtdItens.length; i++) {
      //  this.dadosPlv[i].gravado = 1
      //}
    },
    /* CARREGA A LISTA DE GRUPOS DE ACORDO COM O LOGIN LOGADO NO SISTEMA */
    buscaGruposDLPorToken() {
      //let ambiente = "POCNEW";
      let grupoToken = window.atob(this.token).split('^')[1];

      let url = `${urlApi}/api/apiflashsafe/b/${grupoToken}/${this.ambiente}/${this.token}`;

      console.log('METODO CARREGA GRUPOS DLPs');
      console.log(url);

      this.$http.get(url, myHeaders)
      .then(res => {
        let lista = res.data;

        lista.forEach(arr => {
          this.gruposDLPs.push(arr.grupo)
        });

        console.log('LISTA DE GRUPOS DE ACORDO COM O LOGIN: ');
        console.log(this.gruposDLPs);
      })
      .catch(err => {
        console.log(err);
      })
    },
    /* RETORNA UMA RESPOSTA AO USUARIO SEJA ELA POSITIVA OU NEGATIVA DE ACORDO COM A SOLICITACAO DESEJADA */
    respostasEnvio(resp) {
      if(!!resp){
        this.resposta = {
          status: 1,
          texto: `O processamento de gravação das PALAVRAS CLASSIFICADAS foi finalizado!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: '', erro: 0}
        }, 3500);
        this.$emit('carregaDados')
      } else {
        this.resposta = {
          status: 2,
          texto: `Ocorreu erro no processamento de alguma(s) palavra(s)!`
        }
        this.$emit('carregaDados')
      }
    },
    txtBtnAdd() {
      document.querySelectorAll(".btn.b-form-tags-button").innerHTML = 'Incluir'
    },
    fechaModal(ref) {
      this.$refs[ref].hide()
    },
    abreModal(ref){
      //this.$refs[ref].hide()
      this.$bvModal.show(ref);
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
    }
  },
  mounted(){
    let tk = window.atob(this.token)
    tk = tk.split('^')
    this.setGrupoDLP = tk[1]

    this.buscaGruposDLPorToken();
  }
}
</script>
