<template>
  <b-modal v-model="modal" ok-only ref="user-remove" id="user-remove" size="md" hide-header-close no-close-on-backdrop no-close-on-esc>
    <template #modal-title>
      Remover Usuário:
    </template>
    <div class="container">
      <div class="row">
        <div class="col-sm-12 col-md-12 col-lg-6 pb-3">
          <b-form-group
            label="Usuário"
            label-for="removeUsuario"
            class="mb-0"
          >
            <b-form-input
              id="removeUsuario"
              v-model="dadosUser.usernameLogin"
              disabled
            ></b-form-input>
          </b-form-group>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-6 pb-3">
          <b-form-group
            label="Grupo DLP"
            label-for="removeGrupo"
            class="mb-0"
          >
            <b-form-input
              id="vemoveGrupo"
              v-model="dadosUser.grupoDlp"
              disabled
            ></b-form-input>
          </b-form-group>
        </div>
        <div class="col-sm-12 pb-3">
          <b-form-group
            label="Aceite de remoção de usuário:"
            label-for="remover"
            class="mb-0"
          >
            <b-form-checkbox
              id="remover"
              class="remove"
              button-variant="danger"
              v-model="valida.remove"
              value="true"
              unchecked-value="false"
              switch
            >
              Deseja realmente remover o usuário do grupo DLP acima?
            </b-form-checkbox>
          </b-form-group>
        </div>
      </div>
      <b-alert :variant="resposta.status == 1 ? 'success' : 'warning'" show v-if="resposta.status !== 0" dismissible>
        <b-icon :icon="resposta.status == 1 ? 'check-circle-fill' : 'exclamation-circle-fill'" font-scale="1.2" class="mr-2"></b-icon>
        {{resposta.texto}}
      </b-alert>
    </div>
    <template #modal-footer>
      <b-icon icon="arrow-clockwise" animation="spin" variant="primary" v-if="loaderCheck" font-scale="2"></b-icon>
      <b-button-group>
        <b-button variant="warning" style="color: white" @click="removeLogin()" :disabled="!verificaRemover()">
          <b-icon icon="trash-fill" class="mr-1"></b-icon> Remover
        </b-button>
        <b-button variant="danger" @click="fechaModal('user-remove')">
          <b-icon icon="x" class="mr-1"></b-icon> Cancelar
        </b-button>
      </b-button-group>
    </template>
  </b-modal>
</template>

<script>
var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Content-Type", "text/plain");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
  props: ['modalUsuario', 'dados'],
  data() {
    return {
      // variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      loaderCheck: false,
      // vairáveis do componente
      modal: !!this.modalUsuario ? true : false ,
      dadosUser: this.dados,
      removeItem: false,
      // validações de campos
      valida: {
        remove: null,
      },
      // resposta do endpoint
      resposta: {
        status: 0,
        texto: ''
      }
    }
  },
  watch: {
    modalUsuario(vlr) {
      this.modal = vlr
    },
    dados(d) {
      this.dados = d
    }
  },
  methods: {
    descSenha(s) {
      return window.atob(s)
    },
    verificaRemover() {
      return this.valida.remove === "true"
    },
    async removeUser() {
      this.loaderCheck = true
      let url = `${this.locationUrl}/dpo/perfil/del/${this.dadosUser.loginId}/${this.token}/resp_rest`
      await this.$http.post(url, myHeaders)
      .then(res => {
        this.loaderCheck = false
        !!res ? true : false ;
        this.respostasEnvio(true)
      })
      .catch(err => {
        this.loaderCheck = false
        !!err ? true : false ;
        this.respostasEnvio(false)  
      })
    },
    async removeLogin() {
      this.loaderCheck = true
      let url = `${this.locationUrl}/dpo/login/del/${this.dadosUser.loginId}/${this.token}/resp_rest`;
      await this.$http.post(url, myHeaders)
      .then(res => {
        this.loaderCheck = false
        !!res ? true : false ;
        this.removeUser()
      })
      .catch(err => {
        this.loaderCheck = false
        !!err ? true : false ;
        this.respostasEnvio(false)
      })
    },
    
    respostasEnvio(resp) {
      if(!!resp){
        this.resposta = {
          status: 1,
          texto: `Usuário "${this.dadosUser.usernameLogin}" foi removido com sucesso!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
        setTimeout(() => {
          this.fechaModal()
        }, 3600);
      } else {
        this.resposta = {
          status: 2,
          texto: `Falha ao remover o usuário "${this.dadosUser.usernameLogin}"!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
      }
    },

    // modal __________________________________________________________________
    fechaModal() {
      this.$emit('limpaModal', 'user-remove')
    },
  },
  mounted(){
      this.$refs['user-remove'].hide()
      
    // this.resposta = {status: 1, texto: 'Usuário salvo com sucesso'}
  }
}
</script>

<style>
.row {
  -webkit-box-orient: initial !important;
  flex-direction: inherit !important;
}
.remove > .custom-control-input:checked ~ .custom-control-label::before {
    color: #fff;
    border-color: #dc3545 !important;
    background-color: #dc3545 !important;
    outline: none !important
}
.remove > .custom-control-input:focus:not(:checked) ~ .custom-control-label::before {
    border-color: #adb5bd !important;
}
.remove > .custom-control-input:focus ~ .custom-control-label::before {
    box-shadow: 0 0 0 0.2rem rgb(200 35 51 / 25%);
}
.remove > .custom-control-input:not(:disabled):active ~ .custom-control-label::before {
    color: #fff;
    background-color: rgba(200,35,51,0.15);
    border-color: rgba(200,35,51,0.15);
}
</style>