<template>
  <b-modal v-model="modal" ok-only ref="user-senha" id="user-senha" size="lg" hide-header-close no-close-on-backdrop no-close-on-esc>
    <template #modal-title>
      Alterar Senha do Usuário:
    </template>
    <div class="container">
      <div class="row">
        <div class="col-sm-12 col-md-12 col-lg-6 pb-3">
          <b-form-group
            label="Usuário"
            label-for="senhaUsuario"
            class="mb-0"
          >
            <b-form-input
              id="senhaUsuario"
              v-model="dadosUser.usernameLogin"
              disabled
            ></b-form-input>
          </b-form-group>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-6 pb-3">
          <b-form-group
            label="Grupo DLP"
            label-for="senhaGrupo"
            class="mb-0"
          >
            <b-form-input
              id="senhaGrupo"
              v-model="dadosUser.grupoDlp"
              disabled
            ></b-form-input>
          </b-form-group>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-6 pb-3">
          <b-form-group
            label="Nova Senha"
            label-for="senhaNova"
            class="mb-0"
          >
            <b-form-input
              :class="!showSenhaAntiga ? 'senha' : ''"
              id="senhaNova"
              maxlength="40"
              autocomplete="off"
              :type="!verSenha.senha ? 'password' : 'text'"
              placeholder="Digite sua senha"
              v-model="dadosUser.senhanova"
              :state="valida.senha"
              @keyup="verificaSenha(dadosUser.senhanova)"
              @blur="verificaSenha(dadosUser.senhanova)"
              :disabled="showSenhaAntiga"
            ></b-form-input>
            <b-icon class="verSenha" font-scale="1.5" variant="secondary" :icon="!verSenha.senha ?'eye-fill' : 'eye-slash-fill'" @click="verSenha.senha = !verSenha.senha" v-if="!showSenhaAntiga"></b-icon>
          </b-form-group>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-6 pb-3">
          <b-form-group
            label="Repita a Senha"
            label-for="senhaRep"
            class="mb-0"
          >
            <b-form-input
              :class="!showSenhaAntiga ? 'senha' : ''"
              id="senhaRep"
              maxlength="40"
              autocomplete="nope" 
              :type="!verSenha.repete ? 'password' : 'text'"
              placeholder="Digite novamente a sua senha!"
              v-model="dadosUser.senharep"
              :state="valida.repete"
              @keyup="verificaSenhaRep(dadosUser.senharep)"
              @blur="verificaSenhaRep(dadosUser.senharep)"
              :disabled="showSenhaAntiga"
            ></b-form-input>
            <b-icon class="verSenha" font-scale="1.5" variant="secondary" :icon="!verSenha.repete?'eye-fill' : 'eye-slash-fill'" @click="verSenha.repete = !verSenha.repete" v-if="!showSenhaAntiga"></b-icon>
          </b-form-group>
        </div>
      </div>
      <div class="row">
        <div class="col-sm-12 col-md-12 col-lg-6 pb-3">
          Qualidade da Senha: 
          <!-- [ {{valSenha.txt}} / {{valSenha.val}}]  -->
          <div class="gadget">
            <small class="status">{{valSenha.txt}}</small>
            <div class="mark" :style="{left: [valSenha.val+'%']}"></div>
          </div>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-6 pb-3">
          <small>
            Verifique se a senha contem pelo menos uma letra maiúscula, uma letra minúscula, um caractere especial, um número e no mínimo 8 posições digitadas.<br>
          </small>
        </div>
      </div>
      <!-- <div class="row" v-if="showSenhaAntiga">
        <div class="col">
          <span>Conversão de Senha Hash, clique em Salvar para alterar!</span><br>
        </div>
      </div> -->
      <b-alert variant="info" show v-if="showSenhaAntiga" dismissible>
        <b-icon icon="info-circle-fill" font-scale="1.2" class="mr-2"></b-icon>
        Clique em salvar para ajustar a senha atual no novo padão em hash!
      </b-alert>
      <b-alert :variant="resposta.status == 1 ? 'success' : 'warning'" show v-if="resposta.status !== 0" dismissible>
        <b-icon :icon="resposta.status == 1 ? 'check-circle-fill' : 'exclamation-circle-fill'" font-scale="1.2" class="mr-2"></b-icon>
        {{resposta.texto}}
      </b-alert>
    </div>
    <template #modal-footer>
      <b-icon icon="arrow-clockwise" animation="spin" variant="primary" v-if="loaderCheck" font-scale="2"></b-icon>
      <b-button-group>
        <b-button variant="success" @click="salvaSenha()" :disabled="!verificaSalvar()">
          <b-icon icon="check" class="mr-1"></b-icon> Salvar
        </b-button>
        <b-button variant="danger" @click="fechaModal('user-edit')">
          <b-icon icon="x" class="mr-1"></b-icon> Cancelar
        </b-button>
      </b-button-group>
    </template>
  </b-modal>
</template>

<script>
var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Content-Type", "text/plain");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
  props: ['modalUsuario', 'dados'],
  data() {
    return {
      // variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      loaderCheck: false,

      // vairáveis do componente
      modal: !!this.modalUsuario ? true : false ,
      dadosUser: this.dados,
      showSenhaAntiga: false,
      // validações de campos
      verSenha: {
        senha: false,
        repete: false
      },
      valida: {
        senha: null,
        repete: null
      },
      valSenha: {
        val: 0,
        txt: 'Força da senha'
      },
      // resposta do endpoint
      resposta: {
        status: 0,
        texto: ''
      }

    }
  },
  watch: {
    modalUsuario(vlr) {
      this.modal = vlr
    },
    dados(d) {
      this.dados = d
    }
  },
  methods: {
    getSenhaAntiga(s) {
      var dataUltAtual = new Date(this.dadosUser.dtAtualizacaoLogin)
      var dataCorte = new Date('2021-03-07 23:59:59')
      if(dataUltAtual < dataCorte) { 
        this.dadosUser.senhanova = this.senhaDesc(s)
        this.dadosUser.senharep = this.senhaDesc(s)
      } 
      this.showSenhaAntiga = dataUltAtual < dataCorte
    },
    senhaDesc(a){
      return window.atob(a)
    },
    verificaSalvar() {
      return (this.valida.senha === true && this.valida.repete === true) || this.showSenhaAntiga
    },
    verificaSenha(s) {
      let num = /(?=.*\d)/
      let min = /(?=.*[a-z])/
      let mai = /(?=.*[A-Z])/
      let esp = /(?=.*[$*&@#%!?+-=:;\(\)\{\}\<\>\\\?])/
      let tam = /[0-9a-zA-Z$*&@#%!?+-=:;\(\)\{\}\<\>\\\?)]{8,}$/
      let reg =/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[$*&@#%!?+-=:;\(\)\{\}\<\>\\\?])[0-9a-zA-Z$*&@#%!?+-=:;\(\)\{\}\<\>\\\?)]{8,}$/
      var conta = 0
      var chk = num.test(s) && min.test(s) && mai.test(s) && esp.test(s) && tam.test(s)
      conta += num.test(s) ? 12 : 0 ;
      conta += min.test(s) ? 12 : 0 ;
      conta += mai.test(s) ? 12 : 0 ;
      conta += esp.test(s) ? 12 : 0 ;
      conta += tam.test(s) ? 12 : 0 ;
      conta += s.length > 11 && chk  ? 20 : 0;
      conta += s.length > 15 && chk ? 20 : 0;
      this.valSenha.val = conta
      if(conta >= 100) {
        this.valSenha.txt = "Excelente"
      } else if(conta < 100 && conta >= 80) {
        this.valSenha.txt = "Ótimo"
      } else if(conta < 80 && conta >= 60) { 
        this.valSenha.txt = "Bom"
      } else if(conta < 60 && conta >= 40) {
        this.valSenha.txt = "Regular"
        } else if(conta < 40 && conta >= 20) {
        this.valSenha.txt = "Fraco"
      } else if(conta < 20 && conta >= 0) {
        this.valSenha.txt = "Ruim"
      } else {
        this.valSenha.txt = 'Força da senha'
      }
      let senha = reg.test(s)
      this.valida.senha = senha
      this.dadosUser.senhanova = s
      this.valida.repete = false
    },
    verificaSenhaRep(s){
       this.valida.repete = this.setCrpPsw(this.dadosUser.senhanova) === this.setCrpPsw(s) ? true : false;
        this.dadosUser.senharep = s
    },
    async salvaSenha() {
      this.loaderCheck = true
      let data = new Date()
      data = `${data.getFullYear()}-${(data.getMonth() + 1) < 10 ? '0' + (data.getMonth() + 1) : data.getMonth() + 1 }-${(data.getDate()) < 10 ? '0' + (data.getDate()) : data.getDate()} ${(data.getHours()) < 10 ? '0' + (data.getHours()) : data.getHours()}:${(data.getMinutes()) < 10 ? '0' + (data.getMinutes()) : data.getMinutes()}:${(data.getSeconds()) < 10 ? '0' + (data.getSeconds()) : data.getSeconds()}}`
      let psw = this.setCrpPsw(this.dadosUser.senhanova) //   this.dadosUser.senhanova 
      let param = JSON.stringify({
        "login_idLogin": this.dadosUser.loginId,
        "usernameLogin": this.dadosUser.usernameLogin,
        "emailLogin": this.dadosUser.emailDesc,
        "dtCriacaoLogin": this.dadosUser.dtCriacaoLogin,
        "dtAtualizacaoLogin": this.dadosUser.dtAtualizacaoLogin,
        "permissoesLogin": this.dadosUser.permissoesLogin,
        "hash_senhaLogin": psw,
        "dpo_idLogin": this.dadosUser.loginId,
        "grupoDlp":this.dadosUser.grupoDlp
      })
      let url = `${this.locationUrl}/dpo/login/atua/${this.dadosUser.loginId}/${this.token}/resp_rest`;
      await this.$http.post(url, param, myHeaders)
      .then(res => {
        this.loaderCheck = false
        !!res ? true : false ;
        this.respostasEnvio(true)
      })
      .catch(err => {
        this.loaderCheck = false
        !!err ? true : false ;
        this.respostasEnvio(false)  
      })
    },
    respostasEnvio(resp) {
      if(!!resp){
        this.resposta = {
          status: 1,
          texto: `A senha do usuário "${this.dadosUser.usernameLogin}" foi alterada com sucesso!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
        setTimeout(() => {
          this.fechaModal()
        }, 3600);
      } else {
        this.resposta = {
          status: 2,
          texto: `Falha ao salvar a alteração de senha do usuário "${this.dadosUser.usernameLogin}"!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
      }
    },
    getParaphase() {
      return this.token +'%'+ this.locationInfo
    },
    setCrpPsw(psw){
      var psw = psw
      psw = this.$CryptoJS.SHA256(psw).toString()
      return psw
    },

    // modal __________________________________________________________________
    fechaModal() {
      this.$emit('limpaModal', 'user-senha')
    },
  },
  mounted(){
      this.dadosUser.senhanova = ''
      this.dadosUser.senharep = ''
      this.$refs['user-senha'].hide()
    // this.resposta = {status: 1, texto: 'Usuário salvo com sucesso'}
    setTimeout(() => {
      this.getSenhaAntiga(this.dadosUser.hashSenhaLogin)
    },10)
  }
}
</script>

<style>
.row {
  -webkit-box-orient: initial !important;
  flex-direction: inherit !important;
}
.gadget {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 5px 0;
  width: 100%;
  /* max-width: 300px; */
  height: 38px;
  border-radius: 5px;
  background: #ff3232;
  background: -moz-linear-gradient(left,  #ff3232 0%, #ff9000 25%, #ffff00 50%, #2ff000 75%, #0f9000 100%);
  background: -webkit-linear-gradient(left,  #ff3232 0%,#ff9000 25%,#ffff00 50%,#2ff000 75%,#0f9000 100%);
  background: linear-gradient(to right,  #ff3232 0%,#ff9000 25%,#ffff00 50%,#2ff000 75%,#0f9000 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff3232', endColorstr='#0f9000',GradientType=1 );
}
.gadget > .mark {
  position: absolute;
  display: block;
  width: 20px;
  height: 20px;
  top: 30px;
  transform: rotate(45deg);
  transition: .5s;
  margin-left: -10px;
  left: 0;
  box-shadow: #000 1px 1px 3px;
  z-index: 2;
}
.gadget .status {
  position: relative;
  display: block;
  width: 100%;
  margin-top: -2px;
  color: #FFF;
  background-color: rgba(0,0,0,0.15);
  text-shadow: #000 1px 1px 5px;
  text-align: center;
  text-transform: uppercase;
}
.senha {
  width: calc(100% - 30px) !important;
}
.verSenha {
    position: absolute;
    display: block;
    right: 15px;
    top: 40px;
    cursor:pointer;
}
</style>