<template>
  <b-modal ok-only :ref="`exeCmd${idx}`" :id="`exeCmd${idx}`" size="md" no-close-on-backdrop no-close-on-esc>
    <template #modal-title>
      Execução de Comandos:
    </template>
    <div class="container">
      <div class="row">
        <div class="col-12 pb-3">
          <span>Máquina:</span><br>
          <strong class="text-uppercase">{{`${dadosMaq.grupo_dlp}-${dadosMaq.computer_name}-${dadosMaq.cod_maquina}`}}</strong>
        </div>
        <div class="col-12 pb-3">
          <b-form-group
           label="Comandos:" 
           label-for="execSelct"
          >
            <b-form-select 
              v-model="selComando" 
              :options="listaComandos"
            ></b-form-select>
          </b-form-group>
        </div>
        <div class="col-12 pb-3" v-if="!!selComando">
          <b-form-group
           :label="comando.titulo" 
           label-for="execOpcao"
          >
            <b-form-input
              class="contato"
              id="execOpcao"
              placeholder="Digite o comando"
              type="text"
              maxlength="100"
              v-model="comando.executar"
              @keyup="verificaComando(comando.executar)"
              @blur="verificaComando(comando.executar)"
              :state="valida.executar = !!comando.executar"
            ></b-form-input>
          </b-form-group>
        </div>
      </div>
    </div>
    <b-alert :variant="resposta.status == 1 ? 'success' : 'warning'" show v-if="resposta.status !== 0" dismissible>
      <b-icon :icon="resposta.status == 1 ? 'check-circle-fill' : 'exclamation-circle-fill'" font-scale="1.2" class="mr-2"></b-icon>
      {{resposta.texto}}
    </b-alert>
    <template #modal-footer>
      <b-icon icon="arrow-clockwise" animation="spin" variant="primary" v-if="loaderCheck" font-scale="2"></b-icon>
      <b-button-group>
        <b-button variant="success" style="color: white" @click="exeComando()" :disabled="!verificaComando()" v-if="!!selComando">
          <b-icon :icon="comando.ico" class="mr-1"></b-icon>
          Enviar
        </b-button>
        <b-button variant="danger" @click="fechaModal()">
          <b-icon icon="x" class="mr-1"></b-icon> Fechar
        </b-button>
      </b-button-group>
    </template>
  </b-modal>
</template>
<script>
var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');
export default {
  props: ['dados', 'idx'],
  data() {
    return {
      // variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      loaderCheck: false,
      // variáveis do componente
      dadosMaq: this.dados,
      selComando: null,
      listaComandos: [
          { value: null, text: 'Selecione um comando' },
          { value: 'web', text: 'Ir para um site' },
          // { value: 'cmd', text: 'Comando Windows' },
          // { value: 'bdj', text: 'Comando BD Java' },
          /*{ value: 'start', text: 'Abrir aplicativos' },
          { value: 'd', text: 'This one is disabled', disabled: true }*/
        ],
      // validações de campos
      comando: {
        titulo: '',
        ico: '',
        executar: ''
      },
      valida: {
        executar: ''
      },
      // resposta do endpoint
      resposta: {
        status: 0,
        texto: ''
      }
    }
  },
  watch: {
    dados(d) {
      this.dadosMaq = d
    },
    selComando(s) {
      let sel = s
      this.exibComando(sel)
    }
  },
  methods: {
    exibComando(s) {
      let sel = s
      sel == 'web' ? this.comando = {titulo: 'URL do site:', ico: 'globe', executar: ''} : '';
      sel == 'cmd' ? this.comando = {titulo: 'Comando Windows:', ico: 'terminal', executar: ''} : '';
      sel == 'bdj' ? this.comando = {titulo: 'Comando BDJava:', ico: 'terminal', executar: ''} : '';
    },
    verificaComando() {
      return !!this.selComando && !!this.valida.executar
    },
    pegaHost(u) {
      var url = u;
      var index = url.indexOf('.');
      var hostname = url.substring(index + 1);
      return hostname.replace('.com.br','')
    },
    async exeComando() {
      this.loaderCheck = true
      let linha = ''
      let maq = `${this.dadosMaq.computer_name}-${this.dadosMaq.cod_maquina}`
      // Como melhoria, devemos passar um comando do cmd, que será executado na máquina client, ou seja, não podemos utilizar "get hdFolha 'www.google.com' 'google.com' 'master'; faz #dlpResp 'COMANDO EXECUTADO OK!!'", mas devemos utilizar "sis 'start {{url}}'; faz #dlpResp 'COMANDO EXECUTADO OK!!'". Assim, conseguimos atender todas as máquinas e não as que precisam do caminho identico ao do servidor.
      // this.selComando == 'web' ? linha = `get hdFolha '${this.comando.executar}' '${this.pegaHost(this.comando.executar)}' 'master'; faz #dlpResp 'COMANDO EXECUTADO OK!!'` : '';
      this.selComando == 'web' ? linha = `sis 'start ${this.comando.executar}'; faz #dlpResp 'COMANDO EXECUTADO OK!!'` : '';
      this.selComando == 'cmd' ? linha = `sis '${this.comando.executar}'; faz #dlpResp 'COMANDO EXECUTADO OK!!'` : '';
      this.selComando == 'bdj' ? linha = `sis '${this.comando.executar}'; faz #dlpResp 'COMANDO BD JAVA EXECUTADO OK!!'` : '';
      var param = JSON.stringify({
        "maquina":maq,
        "comando":linha
      });
      let url = `${this.locationUrl}/subMenu/execComando/${this.token}/resp_rest`;
      console.log(url)
      console.log(param)
      await this.$http.post(url, param, myHeaders)
      .then(res => {
        this.loaderCheck = false
        !!res ? true : false ;
        this.respostasEnvio(true)
        this.valida.chat = null
        this.mensagem = ''
      })
      .catch(err => {
        this.loaderCheck = false
        !!err ? true : false ;
        this.respostasEnvio(false)
      })
    },
    respostasEnvio(resp) {
      if(!!resp){
        let msg = ''
        this.selComando == 'web' ? msg = `Comando para abrir o site "${this.comando.executar}" foi executado com sucesso!` : '';
        this.selComando == 'cmd' ? msg = `Comando Windows "${this.comando.executar}" foi executado com sucesso! ` : '';
        this.selComando == 'bdj' ? msg = `Comando BDJava "${this.comando.executar}" foi executado com sucesso!` : '';
        this.resposta = {
          status: 1,
          texto: msg
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
        setTimeout(() => {
          this.fechaModal()
        }, 3600);
      } else {
        this.resposta = {
          status: 2,
          texto: `Falha ao executar comando!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
      }
    },
    resetDados() {
      this.selComando = null
      this.comando = {
        titulo: '',
        ico: '',
        executar: ''
      }
    },
    fechaModal() {
      this.$refs[`exeCmd${this.idx}`].hide()
      this.resetDados()
    },
    abreModal(){
      this.$refs[`exeCmd${this.idx}`].hide()
    }
  },
  mounted(){
    this.fechaModal()
  }
}
</script>
<style>
.row {
  -webkit-box-orient: initial !important;
  flex-direction: inherit !important;
}
</style>