import Header from '../../shared/header/Header'
import Menu from '../../shared/menu/Menu'
import subMenu from '../../shared/subMenu/subMenu'
import Loader from '../../shared/loader/loader'
import editaDisp from './disp-edita'
import editaRemove from './disp-remove'

const filtroDados = require('../../shared/filtrosDeDados');
const retiraLoader = require('../../shared/retiraLoader');

var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');
myHeaders.append("Access-Control-Allow-Origin", "*");

export default { 
  components: {
  'meu-painel': Header,
  'menu-lateral':Menu,
  'sub-menu': subMenu,
  'edita-disp': editaDisp,
  'remove-disp': editaRemove,
  'loader': Loader
  },
  data() {
    return {
      //variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      loaderCheck: true,
      //Definição dos itens do sub-menu
      listaSubMenu: ['sms', 'chat', 'email'],
      // Recarrega pagina
      ultVerifServer: '',
      modVersaoDLP: false,
      versaoDLP: {
        maquina: '',
        codigo: '',
        versao: ''
      },
      listaDispositivos: [], // ARMAZENA A LISTA DE DISPOSITIVOS
      listaMaquinas: [],
      tabListaDispositivos: [], // RECEBE A LISTA DE DISPOSITIVOS
      tabTitulo: 'Total de Dispositivos',
      evidencias: [],
      imgFull: '',
      chkIndex: null,
      // modal
      modSelMaquina: false,
      // getMaquinaState: '',
      // getMaquinaStateCodMaquina: '',
      // getMaquinaStateFull: '',
      getItems: [],
      titTabela: [
        { key: 'actions', label: 'Ações'},
        { key: 'codigo_dispositivo',label: 'Cód Dispositivo', sortable: true, class: 'text-left'},
        { key: 'maquina',label: 'Cód. Máquina', sortable: true, class: 'text-left'},
        { key: 'grupo_dlp', label: 'Grupo DLP', sortable: true, class: 'text-left'},
        { key: 'id_alarme', label: 'Id Alarme', sortable: true, class: 'text-left'},
        { key: 'login_user', label: 'Gerenciado por', sortable: true, class: 'text-left'},
        { key: 'dataCadastramento',label: 'Cadastrado em', sortable: true, class: 'text-left'},
      ],
      titTabComents: [
        { key: 'stt', label: 'Status', sortable: true, class: 'text-left'},
        { key: 'data',label: 'Data da Alteração', sortable: true, class: 'text-left'},
        { key: 'user',label: 'Usuário', sortable: true, class: 'text-left'},
        { key: 'msg', label: 'Comentário', sortable: true, class: 'text-left'},
      ],
      estatisticas: {
        total: 0,
        liberado: 0,
        bloqueado: 0,
        pendente: 0
      },
      btnSel: {
        total: false,
        liberado: false,
        bloqueado: false,
        pendente: false
      },
      totalRows: 1,
      currentPage: 1,
      perPage: 20,
      pageOptions: [10, 20, 50, 100],
      filter: null,
      infoModal: {
        id: 'info-modal',
        title: '',
        content: '',
      },
      text: '',
      xlsTitulo: {
        'Máquina Ativa': 'ativo',
        'Código da Máquina': 'cod_maquina',
        'Máquina': 'computer_name',
        'Discos': 'discos',
        'Última Atividade': 'ult_atividade',
        'Login na Máquina': 'user_name',
        'Grupo DLP': 'grupo_dlp',
        'Versão do DLP': 'versoes',
      },
      xlsDados: []
    }
  },
  computed: {
    sortOptions() {
      return this.fields
        .filter(f => f.sortable)
        .map(f => {
          return { text: f.label, value: f.key }
        })
    }
  },
  watch: {
    /* LISTA O TOTAL DE LINHAS CONTIDOS NA TABELA */
    totalRows (t) {
      this.totalRows = t;
    },
    tabListaDispositivos (t) {
      this.geraXls(t)
    },
    listaDispositivos(l) {
      this.geraEstatisticas()
    },
    getItems(g){
      this.getItems = g
    }
  },
  methods: {
    geraXls(d){
      var dados = d;
      if(!!dados) {
        this.xlsDados = dados
      }
    },
    horaFormat(t) {
      var tempo = new Date(t).toString()
      tempo = tempo.split(' GMT')
      tempo = tempo[0].split(' ').reverse()
      tempo = tempo[0]
      return tempo 
    },
    formatDateMilisec(d) {
      let tempo = d
      let bGMT =  3 * 60 * 60 * 1000
      tempo = tempo - bGMT
      tempo = new Date(tempo)
      var dia = tempo.getDate()
      var mes = tempo.getMonth() + 1
      var ano = tempo.getFullYear()
      var hora = tempo.getUTCHours()
      var min = tempo.getMinutes()
      var seg = tempo.getSeconds()
      var dataOk = `${dia < 10 ? '0' + dia : dia}/${mes < 10 ? '0' + mes : mes}/${ano} - ${hora < 10 ? '0' + hora : hora}:${min < 10 ? '0' + min : min}:${seg < 10 ? '0' + seg : seg}`
      return dataOk
    },
    formatData(d) {
      let str = d
      if(!!str){
        str = str.split(' ')
        let data = str[0].split('-').reverse()
        let hora = str[1]
        return `${data[0]}/${data[1]}/${data[2]} ${hora}`
      } else {
        return ''
      }
      
    },
    htmlFormat(txt){
      let  texto = txt
      texto = texto.replace(/=(\r\n|\n|\r)/gm , '<br>')
    },
    botaoSelecionado(btn) {
      this.btnSel = {
        total: false,
        liberado: false,
        bloqueado: false,
        pendentes: false
      },
      this.btnSel[btn] = true
    },
    rowDisable(item, type) {
      if(!!item && item.maq_ativa !== null) {
        return item.maq_ativa === 'false' ? 'inativo' : 'ativo';
      }
    },
    abreComponentes(menu, dados, idx){
      this.getItens = dados
      this.$bvModal.show(menu + idx);
    },
    geraStatus(c) {
      var cont = c
      cont = cont % 3

      if(cont === 0) {
        return 'LIBERADO'
      } else if(cont === 1) {
        return 'BLOQUEADO'
      } else if(cont === 2) {
        return 'PENDENTE'
      } else {
        return 'LIBERADO'
      }
    },
    duploClickLinha(row, index) {
      if(this.chkIndex !== index) {
        this.tabListaDispositivos.forEach(obj => obj._showDetails = false)
        this.chkIndex = index
      }
      row._showDetails = !row._showDetails;
    },
    arrComents(obj) {
      let arr = obj;
      arr = arr.indexOf('**') > -1 ? arr.replaceAll('**', '"') : "[]";
      arr = JSON.parse(arr).reverse()
      return arr
    },
    /* REALIZA UMA BUSCA NA BASE DE DADOS E ARMAZENA O RETORNO DESSA REQUISICAO DENTRO DA 'listDispositivos' */
    async getListaDispositivo() {
      this.loaderCheck = true
      let param = JSON.stringify({ 
        "tabela":"dispositivos", 
        "select":"1=1", 
        "pagina":"1", 
        "qt":"500", 
        "campos_select_end_point":"grupo_dlp^maquina^codigo_dispositivo^dataCadastramento^status^id_alarme^login_user^data_alteracao^comentarios"
      })

      // SELECT grupo_dlp, maquina, codigo_dispositivo, dataCadastramento, status, id_alarme, login_user, data_alteracao, comentarios FROM dispositivos WHERE computer_name = 'VOLVO';

      // GET: http://localhos:8080/dispositivos/VOLVO
      // POST: http://localhos:8080/dispositivos
      /* 
        BODY: { 
          "tabela":"dispositivos", 
          "select":"1=1", 
          "pagina":"1", 
          "qt":"500", 
          "campos_select_end_point":"grupo_dlp^maquina^codigo_dispositivo^dataCadastramento^status^id_alarme^login_user^data_alteracao^comentarios"
        }
      */

      let url = this.locationUrl + '/sql/selectSql/'+this.token+'/resp_rest'

      console.log("Parametros: ");
      console.log(param);
      console.log("URL: ");
      console.log(url);

      await this.$http.post(url, param, myHeaders)
      .then(res => {
        console.log("Resposta: ");
        console.log(res);
        console.log("Resposta body: ");
        console.log(res.bodyText)

        console.log("Resposta Body: ");
        console.log(JSON.stringify(res));

        //alert(JSON.stringify(res))
        this.loaderCheck = false
        let resp = res.bodyText 
        resp = resp.replaceAll(/\n/gi, '')
        resp = JSON.parse(resp)

        console.log("Minha resposta dos dispositivos: ");
        console.log(resp);

        if(!!resp) {
          let cont = 0
          resp.forEach(obj => {
            obj.idx = cont++
            obj.status = !obj.status ? 'PENDENTE' : obj.status;
            // obj.status = obj.status == 'PEND' ? 'PENDENTE' : obj.status;
            // obj.status = obj.status == 'BLOQ' ? 'BLOQUEADO' : obj.status;
            // obj.status = obj.status == 'AUTO' ? 'LIBERADO' : obj.status;
            obj.login_user = !obj.login_user ? 'Não atribuido': obj.login_user;
            obj._showDetails = false
          });
          this.listaDispositivos = resp
          this.geraEstatisticas()
          this.listaDispositivosTodas()
        } else {
          this.listaDispositivos = []
          this.geraEstatisticas()
          this.listaDispositivosTodas()
        }
        this.ultVerifServer = this.formatDateMilisec(Date.now())
      })
      .catch( (err) => {
        this.loaderCheck = false
        this.listaDispositivos = []
        this.listaDispositivosTodas()
        this.ultVerifServer = this.formatDateMilisec(Date.now())
        console.log(err)
      })
    },
    listaDispositivosTodas() {
      this.tabTitulo = "Total de Dispositivos"
      this.botaoSelecionado('total')
      this.tabListaDispositivos = this.listaDispositivos;
      this.totalRows = this.tabListaDispositivos.length;
    },
    listaDispositivosLiberados() {
      this.tabTitulo = "Dispositivos Liberados"
      this.botaoSelecionado('LIBERADO')
      this.tabListaDispositivos = this.listaDispositivos.filter(obj => obj.status == 'LIBERADO')
      this.totalRows = this.tabListaDispositivos.length;
    },
    listaDispositivosBloqueados() {
      this.tabTitulo = "Dispositivos Inativos"
      this.botaoSelecionado('BLOQUEADO')
      this.tabListaDispositivos = this.listaDispositivos.filter(obj => obj.status == 'BLOQUEADO')
      this.totalRows = this.tabListaDispositivos.length;
    },
    listaDispositivosPendentes() {
      this.tabTitulo = "Dispositivos Pendentes"
      this.botaoSelecionado('PENDENTE')
      this.tabListaDispositivos = this.listaDispositivos.filter(obj => obj.status == 'PENDENTE')
      this.totalRows = this.tabListaDispositivos.length;
    },
    geraEstatisticas() {
      this.estatisticas = { total: 0, liberado: 0, bloqueado: 0, pendente: 0 },
      this.estatisticas.total = this.listaDispositivos.length
      this.listaDispositivos.forEach(disp => {
        if (disp.status == 'LIBERADO') { this.estatisticas.liberado++ }
        if (disp.status === 'BLOQUEADO') { this.estatisticas.bloqueado++ }
        if (disp.status === 'PENDENTE') { this.estatisticas.pendente++ }
      });
    },
    abreModal(ref) {
      this.$refs[ref].show()
    },
    fechaModal(ref) {
      this.$refs[ref].hide()
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
    }
  },
  mounted() {
    //this.getListaMaquinas()
    this.getListaDispositivo()
  }
}
