import Header from '../../shared/header/Header'
import Menu from '../../shared/menu/Menu'
import subMenu from '../../shared/subMenu/subMenu'
import Loader from '../../shared/loader/loader'

var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
    components: {
    'meu-painel': Header,
    'menu-lateral':Menu,
    'sub-menu': subMenu,
    'loader': Loader
  },
  data() {
    return {
      //variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      loaderCheck: true,
      //Definição dos itens do sub-menu
      listaSubMenu: ['sms', 'email', 'desliga', 'reinicia', 'reiniciaDlp', 'paraApp', 'exeCmd'],
      //seleciona maquina
      selMaquina: '',
      selMaquinaData: '',
      selMaquinaGdlp: '',
      setListaMaquinas: [],
      setListaDatas: [],
      listaAcoes: [],
      tabListaAcoes: [],
      tabTitulo: 'Todas Aplicações',
      evidencias: [],
      imgFull: '',
      chkIndex: null,
      // modal
      modSelMaquina: false,
      titTabela: [
        { key: 'actions', label: 'Ações' },
        { key: 'tipo_janela', label: '', sortable: false },
        // { key: 'terminada', label: 'Status', sortable: true },
        { key: 'hora_start', label: 'Entrada', sortable: true },
        { key: 'maquina', label: 'Máquina', sortable: false },
        { key: 'titulo_atual', label: 'Titulo', sortable: false },
        { key: 'executavel', label: 'Executável', sortable: false },
        { key: 'tem_alarmes', label: 'Tem alarmes?', sortable: false },
      ],
      titTabelaOcorrencias: [
        { key: 'dataHoraEvento', label: 'Horário', sortable: false },
        { key: 'tituloJanela', label: 'titulo', sortable: false },
        { key: 'tipoEvento', label: 'Tipo', sortable: false },
        { key: 'ocorrencia', label: 'Ocorrência', sortable: false }
      ],
      estatisticas: {
        total: 0,
        ativas: 0,
        terminadas: 0,
        desligadas: 0
      },
      btnSel: {
        todas: 0,
        permitidas: 0,
        proibidas: 0,
        desconhecidas: 0,
        ocorrencias: 0
      },
      totalRows: 1,
      currentPage: 1,
      perPage: 20,
      pageOptions: [10, 20, 50],
      filter: null,
      xlsTitulo: {
        'Início da Existência da Janela': 'horaInicio',
        'Grupo DLP': 'grupo_dlp',
        'Máquina': 'maquina',
        'Status da Máquina': 'maqAtiva',
        'Usuário': 'user_name',
        'Código da Janela': 'handler_janela',
        'Titulo Original': 'titulo_original',
        'Titulo Atualizado': 'titulo_atual',
        'Executável': 'executavel',
        'PID': 'pid' ,
        'Status do Último Alarme': 'status_alarme',
        'Status Janela' : 'statusApp',
        'Tipo de Janela': 'tipoStatusJanela',
        'Ocorrências': 'itensOcorr',
        'Caminho das Imagens': 'imagens',
        'Tem Alarmes?': 'temAlarmes',
        'Tempo de Atividade': 'hh_tempo_atividade',
        'Tempo de Existência': 'hh_tempo_existencia',
        'Comentários': 'comentarios'

      },
      xlsDados: []
    }
  },
  computed: {
    sortOptions() {
      return this.fields
        .filter(f => f.sortable)
        .map(f => {
          return { text: f.label, value: f.key }
        })
    }
  },
  watch: {
    tabListaAcoes (t) {
     this.geraXls(t)
    },
    selMaquina(maq) {
      this.selMaquina = maq.toUpperCase()
      !!maq ? this.listaDatas(maq) : this.selMaquinaData = '';
    },
    selMaquinaData(dat) {
       var x = this.selMaquina+'|'+dat+'|'+this.selMaquinaGdlp
       this.setCrpStorage(x)
    },
    totalRows (t) {
      this.totalRows = t;
    },
    listaAcoes(l) {
      this.geraEstatisticas()
    }
  },
  methods: {
    geraXls(d){
      var dados = d;
      if(!!dados) {
        dados.forEach(obj => {
          obj.maqAtiva = obj.maquina_fora == true ? 'Inativa' : 'Ativa';
          obj.statusApp = obj.terminada == 1 ? 'Sim' : 'Não';
          obj.temAlarmes = obj.tem_alarmes == true ? 'Sim' : 'Não';
          obj.imagens = obj.caminhoScreenshots.replaceAll('\\\\', '\\')
          if(obj.tipo_janela === 1) {
            obj.tipoStatusJanela = 'Permitida';
          } else if(obj.tipo_janela === 2) {
            obj.tipoStatusJanela = 'Proibida';
          } else if(obj.tipo_janela === 3) {
            obj.tipoStatusJanela = 'Desconhecida'
          }
          obj.horaInicio = this.formatDate(obj.hora_inicio);
          var ocorr = obj.ocorrencias;
          ocorr.forEach(arr => arr.dataHoraEvento = this.formatDateMilisec(arr.dataHoraEvento))
          ocorr = JSON.stringify(ocorr);
          ocorr = ocorr.indexOf('\\n') > -1 ? ocorr.replaceAll('\\n', '') : '';
          ocorr = ocorr.indexOf('[') > -1 ? ocorr.replaceAll('[', '') : '';
          ocorr = ocorr.indexOf(']') > -1  ? ocorr.replaceAll(']', '') : '';
          ocorr = ocorr.indexOf('},{') > -1  ? ocorr.replaceAll('},{', '; \n\n') : '';
          ocorr = ocorr.indexOf('{') > -1  ? ocorr.replaceAll('{', '') : '';
          ocorr = ocorr.indexOf('}') > -1  ? ocorr.replaceAll('}', '') : '';
          ocorr = ocorr.indexOf('":') > -1  ? ocorr.replaceAll('":', ': ') : '';
          ocorr = ocorr.indexOf('","') > -1  ? ocorr.replaceAll('","', ', ') : '';
          ocorr = ocorr.indexOf('"') > -1  ? ocorr.replaceAll('"', '') : '';
          // ocorr = ocorr.indexOf('\\\\') > -1  ? ocorr.replaceAll('\\\\', '\\') : '';
          ocorr = ocorr.indexOf('ocorrencia') > -1  ? ocorr.replaceAll('ocorrencia', 'Item de Ocorrência') : '';
          ocorr = ocorr.indexOf('dataHoraEvento') > -1  ? ocorr.replaceAll('dataHoraEvento', 'Hora do Evento') : '';
          obj.itensOcorr = ocorr;
        })
        this.xlsDados = dados
      }
    },
    voltaPagina() {
      this.$router.go(-1)
    },
    verificaMaquina() {
      var maqSession = this.getCrpStorage()
      var link = window.location.hash
      link = link.replace('#/resumo-aplicacoes/','')
      var maquina = '';
      var hoje = new Date()
      if(!!link) {
        link = link.split('/').reverse()
        maquina = `${link[1]}-${link[0]}`.toUpperCase();
        this.selMaquinaGdlp = link[1]
        var dia = hoje.getDate()
        dia < 10 ? dia = '0' + dia : dia;
        var mes = hoje.getMonth() + 1
        mes < 10 ? mes = '0' + mes : mes;
        var ano = hoje.getYear() - 100
        hoje = ano.toString() + mes.toString() + dia.toString()
        this.selMaquina = maquina
        this.selMaquinaData = hoje
        var param = this.selMaquina + '|' + this.selMaquinaData
        this.listaAcoesMaquina(param)
      } else  {
        if(!!maqSession) {
          var maq = maqSession.split('|')
          this.selMaquina = maq[0]
          this.selMaquinaData = maq[1]
          this.selMaquinaGdlp = maq[2]
          this.listaAcoesMaquina(maqSession)
          } else {
          this.listaMaquinas()
          this.modSelMaquina = true;
        }
      }
    },
    novaPesquisa() {
      this.listaMaquinas()
      this.modSelMaquina = true;
    },
    dataHeader(data){
      return data.slice(4,6) + "/" + data.slice(2,4) + "/20" + data.slice(0,2)
    },
    horaFormat(t) {
      var tempo = new Date(t).toString()
      tempo = tempo.split(' GMT')
      tempo = tempo[0].split(' ').reverse()
      tempo = tempo[0]
      return tempo
    },
    formatDate(d) {
      let tempo = d.split(' ')
      let hora = tempo[1]
      let data = tempo[0].split('-').reverse()
      data = data[0]+'/'+data[1]+'/'+data[2]
      var dataOk = `${data} - ${hora}`
      return dataOk
    },
    formatDateMilisec(d) {
        let tempo = d
        let bGMT =  3 * 60 * 60 * 1000
        tempo = tempo - bGMT
        tempo = new Date(tempo)
        var dia = tempo.getDate()
        var mes = tempo.getMonth() + 1
        var ano = tempo.getFullYear()
        var hora = tempo.getUTCHours()
        var min = tempo.getMinutes()
        var seg = tempo.getSeconds()
        var dataOk = `${dia < 10 ? '0' + dia : dia}/${mes < 10 ? '0' + mes : mes}/${ano} - ${hora < 10 ? '0' + hora : hora}:${min < 10 ? '0' + min : min}:${seg < 10 ? '0' + seg : seg}`
        return dataOk
    },
    htmlFormat(txt){
      let  texto = txt
      texto = texto.replace(/=(\r\n|\n|\r)/gm , '<br>')
    },
    async listaMaquinas() {
      this.loaderCheck = true
      let url = this.locationUrl + '/getWatcherMachinesListSecret/'+ this.token +'/resp_rest'
      console.log(url)
      await this.$http.post(url, myHeaders)
      .then(res => {
        this.loaderCheck = false
        localStorage.removeItem('x-rapp-maq')
        this.setListaMaquinas = res.data
      })
      .catch(function (e){
        this.loaderCheck = false
        this.setListaMaquinas = []
        this.setListaDatas = []
      })
    },   
    async listaDatas (maquina) {
      this.loaderCheck = true
      let url = this.locationUrl + '/getWatcherDatesListSecret/'+this.token+'/resp_rest'
      await this.$http.post(url, maquina, myHeaders)
      .then(res => {
        this.loaderCheck = false
        var lista = res.data;
        lista = lista.reverse();
        this.setListaDatas = lista.map(data => {
          return {
            text: data.slice(4,6) + "/" + data.slice(2,4) + "/20" + data.slice(0,2),
            value: data
          }
        })  
      })
      .catch(function (e){
        this.loaderCheck = false
        this.setListaDatas = []
      })
    },
    botaoSelecionado(btn) {
      this.btnSel = {
        todas: false,
        ativas: false,
        termiandas: false,
        desligadas: false
      },
      this.btnSel[btn] = true
    },
    async listaAcoesMaquina() {
      this.loaderCheck = true
      this.listaAcoes = []
      var param = JSON.stringify({
        "data": this.selMaquinaData,
        "grupo_dlp": this.selMaquinaGdlp,
        "lista_todos": "1",
        "maquina": this.selMaquina
      });
      let url = this.locationUrl + '/gestao/janelas/'+this.token+'/resp_rest' 

      console.log('PARAMETROS: ');
      console.log(param);
      console.log('URL: '+ url);

      await this.$http.post(url, param, myHeaders)
      .then(res => {
        this.loaderCheck = false
        let resp = res.data
        console.log('RESPOSTA DATA: ');
        console.log(resp);
        if(!!resp || resp !== []) {
          let cont = 0
          let maq = this.selMaquina.split('-')
          resp.forEach(obj => {
            obj.idx = cont++
            obj.computer_name = maq[1]
            obj.cod_maquina = maq[2]
            obj.handlerJanela = obj.handler_janela
            obj.maq_ativa = !!obj.maquina_fora ? 'false' : 'true';
            obj._showDetails = false
          });
          this.listaAcoes = resp
          console.log(this.listaAcoes)
          this.geraEstatisticas()
          this.listaAcoesTodas()
        } else {
          this.listaAcoes = []
        }
      })
      .catch( (err) => {
        this.loaderCheck = false
        !!err ? this.listaAcoes = [] : '';
        console.log(err)
      })
    },
    listaAcoesTodas() {
      this.tabTitulo = "Todas Aplicações"
      this.botaoSelecionado('todas')
      this.tabListaAcoes = this.listaAcoes;
      this.totalRows = this.tabListaAcoes.length;
    },
    listaAcoesAtivas() {
      this.tabTitulo = "Aplicações Ativas"
      this.botaoSelecionado('ativas')
      this.tabListaAcoes = this.listaAcoes.filter(obj => obj.terminada !== 1)
      this.totalRows = this.tabListaAcoes.length;
    },
    listaAcoesTerminadas() {
      this.tabTitulo = "Aplicações Terminadas"
      this.botaoSelecionado('terminadas')
      this.tabListaAcoes = this.listaAcoes.filter(obj => obj.terminada == 1)
      this.totalRows = this.tabListaAcoes.length;
    },
    geraEstatisticas() {
        this.estatisticas = { todas: 0, ativas: 0, terminadas: 0, desligadas: 0 },
        this.estatisticas.todas = this.listaAcoes.length
        this.listaAcoes.forEach(app => {
          if (app.terminada !== 1) { this.estatisticas.ativas++ }
          if (app.terminada === 1) { this.estatisticas.terminadas++ }
        });
    },
    tamanhoJanela() {
      return window.innerWidth
    },
    duploClickLinha(row, index) {
      if(this.chkIndex != index) {
        this.tabListaAcoes.forEach(obj => obj._showDetails = false)
        this.chkIndex = index
      }
      row._showDetails = !row._showDetails;
    },
    pegaCodMaquina() {
      var maq = this.selMaquina.split('-')
      maq = maq.reverse()
      return maq[0]
    },
    pegaMaquina(){
      var maq = this.selMaquina.split('-')
      maq = maq.reverse()
      var remove = '-' + maq[0]
      return this.selMaquina.replace(remove, '')
    },
    getParaphase() {
      return this.token +'%'+ this.locationInfo
    },
    setCrpStorage(dados){
      var dados = dados
      dados = this.$CryptoJS.AES.encrypt(dados, this.getParaphase()).toString()
      localStorage.setItem('x-rapp-maq', dados)
    },
    getCrpStorage() {
      var dados = localStorage.getItem('x-rapp-maq')
      dados = !!dados ? this.$CryptoJS.AES.decrypt(dados, this.getParaphase()).toString(this.CryptoJS.enc.Utf8) : '';
      return dados
    },
    abreModal(ref) {
      this.$refs[ref].show()
    },
    fechaModal(ref) {
      this.$refs[ref].hide()
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
    }
  },
  mounted() {
    this.verificaMaquina()
    window.onresize = this.tamanhoJanela()
  }
}
