<template>
  <b-modal v-model="modal" ok-only ref="modal-remove" id="modal-remove" size="md" hide-header-close no-close-on-backdrop no-close-on-esc>
    <template #modal-title>
      Remover Parâmetro:
    </template>
    <div class="container">
      <div class="row">
        <div class="col-sm-12 pb-3">
          <b-form-group
            label="Campo:"
            label-for="campo"
            class="mb-0"
          >
            <b-form-input
              id="campo"
              v-model="dadosParam.descricao"
              disabled
            ></b-form-input>
          </b-form-group>
        </div>
        <div class="col-sm-12 pb-3">
          <b-form-group
            label="Valor:"
            label-for="valor"
            class="mb-0"
          >
            <b-form-tags
              class="tag-area"
              input-id="valor"
              add-button-text="Inserir"
              placeholder="Adicione..."
              disabled
              separator=","
              v-model="dadosParam.conteudo"
              :value="dadosParam.conteudo"
              v-if="typeof dadosParam.conteudo == 'object'"
            ></b-form-tags>
            <b-form-input
              id="valor"
              name="valor"
              type="text"
              disabled
              v-model="dadosParam.conteudo"
              :value="dadosParam.conteudo"
              v-else-if="typeof dadosParam.conteudo == 'number'"
            ></b-form-input>
            <b-form-checkbox
              id="valor"
              name="valor"
              disabled
              switch 
              v-model="dadosParam.conteudo"
              v-else-if="typeof dadosParam.conteudo == 'boolean'"
            >
              Sim
            </b-form-checkbox>
            <b-form-input
              id="valor"
              name="valor"
              type="text"
              required
              disabled
              v-model="dadosParam.conteudo"
              :value="dadosParam.conteudo"
              v-else
            ></b-form-input>
          </b-form-group>
        </div>
        <div class="col-sm-12 pb-3">
          <b-form-group
            label="Aceite de remoção de parâmetro:"
            label-for="remover"
            class="mb-0"
          >
            <b-form-checkbox
              id="remover"
              class="remove"
              button-variant="danger"
              v-model="valida.remove"
              value="true"
              unchecked-value="false"
              switch
            >
              Deseja realmente remover esse parâmetro?
            </b-form-checkbox>
          </b-form-group>
        </div>
      </div>
      <b-alert :variant="resposta.status == 1 ? 'danger' : 'warning'" show v-if="resposta.status !== 0" dismissible>
        <b-icon :icon="resposta.status == 1 ? 'check-circle-fill' : 'exclamation-circle-fill'" font-scale="1.2" class="mr-2"></b-icon>
        {{resposta.texto}}
      </b-alert>
    </div>
    <template #modal-footer>
      <b-icon icon="arrow-clockwise" animation="spin" variant="primary" v-if="loaderCheck" font-scale="2"></b-icon>
      <b-button-group>
        <b-button variant="warning" style="color: white" @click="removeLogin()" :disabled="!verificaRemover()">
          <b-icon icon="trash-fill" class="mr-1"></b-icon> Remover
        </b-button>
        <b-button variant="danger" @click="fechaModal('modal-remove')">
          <b-icon icon="x" class="mr-1"></b-icon> Cancelar
        </b-button>
      </b-button-group>
    </template>
  </b-modal>
</template>

<script>
var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Content-Type", "text/plain");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
  props: ['modalParam', 'dados'],
  data() {
    return {
      // variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      loaderCheck: false,
      // vairáveis do componente
      modal: !!this.modalParam ? true : false ,
      dadosParam: this.dados,
      removeItem: false,
      // validações de campos
      valida: {
        remove: null,
      },
      // resposta do endpoint
      resposta: {
        status: 0,
        texto: ''
      }
    }
  },
  watch: {
    modalParam(vlr) {
      this.modal = vlr
    },
    dados(d) {
      this.dados = d
    }
  },
  methods: {
    verificaRemover() {
      return this.valida.remove === "true"
    },
    //async removeParam() {
    removeParam() {
      this.loaderCheck = true
      let param = JSON.stringify({
        "tabela": "json_configs",
        "select": `descricao='${dadosParam.descricao}'`
      })
      let url = `${this.locationUrl}/sql/deleteSql/${this.token}/resp_rest`
      console.log(param)
      console.log(url)
      // await this.$http.post(url, myHeaders)
      // .then(() => {
        this.loaderCheck = false
        this.respostasEnvio(true)
      // })
      // .catch(err => {
      //   this.loaderCheck = false
      //   console.log(err)
      //   this.respostasEnvio(false)  
      // })
    },

    respostasEnvio(resp) {
      if(!!resp){
        this.resposta = {
          status: 1,
          texto: `Parâmetro removido com sucesso!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
        setTimeout(() => {
          this.fechaModal()
        }, 3600);
      } else {
        this.resposta = {
          status: 2,
          texto: `Falha ao remover o parâmetro!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
      }
    },

    // modal __________________________________________________________________
    fechaModal() {
      this.$emit('limpaModal', 'modal-remove')
    },
  },
  mounted(){
      this.$refs['modal-remove'].hide()
      console.log(this.modal)
      
    // this.resposta = {status: 1, texto: 'Usuário salvo com sucesso'}
  }
}
</script>

<style>
.row {
  -webkit-box-orient: initial !important;
  flex-direction: inherit !important;
}
.remove > .custom-control-input:checked ~ .custom-control-label::before {
    color: #fff;
    border-color: #dc3545 !important;
    background-color: #dc3545 !important;
    outline: none !important
}
.remove > .custom-control-input:focus:not(:checked) ~ .custom-control-label::before {
    border-color: #adb5bd !important;
}
.remove > .custom-control-input:focus ~ .custom-control-label::before {
    box-shadow: 0 0 0 0.2rem rgb(200 35 51 / 25%);
}
.remove > .custom-control-input:not(:disabled):active ~ .custom-control-label::before {
    color: #fff;
    background-color: rgba(200,35,51,0.15);
    border-color: rgba(200,35,51,0.15);
}
</style>