<template>
  <b-modal ok-only :ref="`reinicia${idx}`" :id="`reinicia${idx}`" size="md" no-close-on-backdrop no-close-on-esc>
    <template #modal-title>
      Reinicia Máquina:
    </template>
    <div class="container">
      <div class="row">
        <div class="col-12 pb-3">
          <span>Máquina:</span><br>
          <strong class="text-uppercase">{{`${dadosMaq.grupo_dlp}-${dadosMaq.computer_name}-${dadosMaq.cod_maquina}`}}</strong>
        </div>
        <div class="col-12  pb-3">
          <b-form-group
            label="Confirmação para Reinicia:"
            label-for="modReinicia"
            class="mb-0"
          >
            <b-form-checkbox
              id="modReinicia"
              class="reinicia"
              button-variant="danger"
              v-model="valida.reinicia"
              value="true"
              unchecked-value="false"
              switch
            >
              Você tem certeza que deseja reiniciar essa máquina?
            </b-form-checkbox>
          </b-form-group>
        </div>
      </div>
      <b-alert :variant="resposta.status == 1 ? 'success' : 'warning'" show v-if="resposta.status !== 0" dismissible>
        <b-icon :icon="resposta.status == 1 ? 'check-circle-fill' : 'exclamation-circle-fill'" font-scale="1.2" class="mr-2"></b-icon>
        {{resposta.texto}}
      </b-alert>
    </div>
    <template #modal-footer>
      <b-icon icon="arrow-clockwise" animation="spin" variant="primary" v-if="loaderCheck" font-scale="2"></b-icon>
      <b-button-group>
        <b-button variant="warning" style="color: white" @click="reiniciaMaquina()" :disabled="!verificaDesliga()">
          <b-icon icon="arrow-repeat" class="mr-1"></b-icon>
           Reiniciar
        </b-button>
        <b-button variant="danger" @click="fechaModal()">
          <b-icon icon="x" class="mr-1"></b-icon> Cancelar
        </b-button>
      </b-button-group>
    </template>
  </b-modal>
</template>

<script>
var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Content-Type", "text/plain");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
  props: ['dados', 'idx'],
  data() {
    return {
      // variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      loaderCheck: false,
      // vairáveis do componente
      modal: false,
      dadosMaq: this.dados,
      // validações de campos
      valida: {
        reinicia: null,
      },
      // resposta do endpoint
      resposta: {
        status: 0,
        texto: ''
      }
    }
  },
  watch: {
    modalDesliga(vlr) {
      this.modal = vlr
    },
    dados(d) {
      this.dadosMaq = d
    }
  },
  methods: {
    verificaDesliga() {
      return this.valida.reinicia === "true"
    },
    async reiniciaMaquina() {
      this.loaderCheck = true
      let url = `${this.locationUrl}/subMenu/reinicMaq/${this.token}/${this.dadosMaq.cod_maquina}/resp_rest`;
      await this.$http.get(url, myHeaders)
      .then(res => {
        this.loaderCheck = false
        !!res ? true : false ;
        this.respostasEnvio(true)
        this.valida.reinicia = false
      })
      .catch(err => {
        this.loaderCheck = false
        !!err ? true : false ;
        this.respostasEnvio(false)
      })
    },
    respostasEnvio(resp) {
      if(!!resp){
        this.resposta = {
          status: 1,
          texto: `A máquina "${this.dadosMaq.computer_name}" foi reiniciado com sucesso!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
        setTimeout(() => {
          this.fechaModal()
          this.$emit('carregaDados')
        }, 3600);
      } else {
        this.resposta = {
          status: 2,
          texto: `Falha ao reiniciar a máquina "${this.dadosMaq.computer_name}"!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
      }
    },

    // modal __________________________________________________________________
    fechaModal() {
      //this.$emit('limpaModal', 'user-remove')
      this.$refs[`reinicia${this.idx}`].hide()
      this.valida.reinicia = false
    },
    abreModal(){
      this.$refs[`reinicia${this.idx}`].hide()
    }
  },
  mounted(){
      this.fechaModal() 
  }
}
</script>

<style>
.row {
  -webkit-box-orient: initial !important;
  flex-direction: inherit !important;
}
.reinicia > .custom-control-input:checked ~ .custom-control-label::before {
    color: #fff;
    border-color: #dc3545 !important;
    background-color: #dc3545 !important;
    outline: none !important
}
.reinicia > .custom-control-input:focus:not(:checked) ~ .custom-control-label::before {
    border-color: #adb5bd !important;
}
.reinicia > .custom-control-input:focus ~ .custom-control-label::before {
    box-shadow: 0 0 0 0.2rem rgb(200 35 51 / 25%);
}
.reinicia > .custom-control-input:not(:disabled):active ~ .custom-control-label::before {
    color: #fff;
    background-color: rgba(200,35,51,0.15);
    border-color: rgba(200,35,51,0.15);
}
</style>