import Header from '../../shared/header/Header'
import Menu from '../../shared/menu/Menu'
import Loader from '../../shared/loader/loader'

import modalAdicionaUsuarios from './modal-adiciona'
import modalEditaUsuarios from './modal-edita'
import modalRemoveUsuarios from './modal-remove'
import modalSenhaUsuarios from './modal-senha'

const filtroDados = require('../../shared/filtrosDeDados');
const retiraLoader = require('../../shared/retiraLoader');

var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
  components: {
    'meu-painel': Header,
    'menu-lateral': Menu,
    'modal-adiciona': modalAdicionaUsuarios,
    'modal-edita': modalEditaUsuarios,
    'modal-remove': modalRemoveUsuarios,
    'modal-senha': modalSenhaUsuarios,
    'loader': Loader
  },
  data() {
    return {
      //variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      loaderCheck: true,

      tempoAtividade: Date.now()  - (2 * 60 * 1000), // tempo de 10 min da ultima atividade 
      tempoAtualizacao: 10 * 1000, // // intervalo de tempo de verificação de enpoint
      ultVerifServer: '',
      //seleciona maquina
      modalUsuario:{
        adiciona: false,
        edita: false,
        remove: false,
        senha: false
      },
      // versaoDLP: {
      //   maquina: '',
      //   codigo: '',
      //   versao: ''
      // },
      listaUsuarios: [],
      tabListaUsuarios: [],
      dadosUsuario: '',
      tabTitulo: 'Total',
      evidencias: [],
      imgFull: '',
      chkIndex: null,
      // modal
      // modSelMaquina: false,
      // liberaSMS: false,
      // getMaquinaState: '',
      // getMaquinaStateCodMaquina: '',
      // getMaquinaStateFull: '',
      titTabela: [
        { key: 'actions',               label: 'Ações'},
        //{ key: 'icon', label: '#', class: 'text-center'},
        { key: 'loginId',               label: 'ID',                 sortable: true,  class: 'text-left'},
        { key: 'usernameLogin',         label: 'Usuário',            sortable: true,  class: 'text-left', sortDirection: 'desc'},
        { key: 'emailLogin',            label: 'Email',              sortable: true,  class: 'text-left', sortDirection: 'desc'},
        { key: 'dtCriacaoLogin',        label: 'Cadastro',           sortable: true,  class: 'text-left'},        
        { key: 'dtAtualizacaoLogin',    label: 'Data Atualização',   sortable: true,  class: 'text-left'},       
        { key: 'grupoDlp',              label: 'grupo dlp',          sortable: true,  class: 'text-left'}
      ],
      estatisticas: {
        total: 0,
      },
      btnSel: {
        total: false,
      },
      totalRows: 1,
      currentPage: 1,
      perPage: 20,
      pageOptions: [10, 20, 50, 100],
      filter: null,
      xlsTitulo: {
        'Id Login': 'loginId',
        'Usuário': 'usernameLogin',
        'E-mail': 'emailDesc',
        'Senha': 'senhaDesc',
        'Grupo DLP': 'grupoDlp',
        'Criado em:': 'criacaoDat',
        'Atualizado em:': 'atualizacaoDat',
        // 'Permissão': 'permissoesLogin',
      },
      xlsDados: []
    }
  },
  computed: {
    sortOptions() {
      return this.fields
        .filter(f => f.sortable)
        .map(f => {
          return { text: f.label, value: f.key }
        })
    }
  },
  watch: {
    totalRows (t) {
      this.totalRows = t;
    },
    tabListaUsuarios (t) {
      this.geraXls(t)
    },
    listaUsuarios(l) {
      this.geraEstatisticas()
    }
  },
  methods: {
    geraXls(d){
      var dados = d;
      if(!!dados) {
        dados.forEach(obj => {
          obj.emailDesc = this.descrEmail(obj.emailLogin)
          obj.criacaoDat = this.formatDate(obj.dtCriacaoLogin)
          obj.atualizacaoDat = this.formatDate(obj.dtAtualizacaoLogin)
          obj.senhaDesc = '*************************'
        })
        this.xlsDados = dados
      }
    },
    horaFormat(t) {
      var tempo = new Date(t).toString()
      tempo = tempo.split(' GMT')
      tempo = tempo[0].split(' ').reverse()
      tempo = tempo[0]
      return tempo 
    },
    formatDate(d) {
      let tempo = d
      if(tempo != 'NULL'){
        tempo = tempo.split(' ')
        let hora = tempo[1]
        let data = tempo[0].split('-').reverse()
        data = data[0]+'/'+data[1]+'/'+data[2]
        var dataOk = `${data} - ${hora}`
        return dataOk
      } else {
        return 'Data Indefinida'
      }
    },
    formatDateMilisec(d) {
      let tempo = d
      let bGMT =  3 * 60 * 60 * 1000
      tempo = tempo - bGMT
      tempo = new Date(tempo)
      var dia = tempo.getDate()
      var mes = tempo.getMonth() + 1
      var ano = tempo.getFullYear()
      var hora = tempo.getUTCHours()
      var min = tempo.getMinutes()
      var seg = tempo.getSeconds()
      var dataOk = `${dia < 10 ? '0' + dia : dia}/${mes < 10 ? '0' + mes : mes}/${ano} - ${hora < 10 ? '0' + hora : hora}:${min < 10 ? '0' + min : min}:${seg < 10 ? '0' + seg : seg}`
      return dataOk
    },
    htmlFormat(txt){
      let  texto = txt
      texto = texto.replace(/=(\r\n|\n|\r)/gm , '<br>')
    },
    descrEmail(e) {
      let email = window.atob(e)
      return email
    },
    botaoSelecionado(btn) {
      this.btnSel = {
        total: false,
      },
      this.btnSel[btn] = true
    },
    rowDisable(item, type) {
      if(!!item && item.maq_ativa !== null) {
        return item.maq_ativa === 'false' ? 'inativo' : 'ativo';
      }
    },
    async getListaUsuarios() {
      this.loaderCheck = true
      this.listaUsuarios = []
      let url = `${this.locationUrl}/dpo/login/todos/${this.token}/resp_rest`
      await this.$http.get(url, myHeaders)
      .then(res => {
        this.loaderCheck = false
        console.log(res)
        let resp = JSON.parse(`[${res.bodyText}]`)
        resp.forEach(obj => {
          let gdlp = obj.grupoDlp
          if(gdlp === '*') {
            obj.empresa = '*'
            obj.dpto = ''
          } else if(gdlp.indexOf('*') > -1  && gdlp.length > 1) {
            obj.empresa = gdlp.replace('*', '')
            obj.dpto = '*'
          } else if(gdlp.indexOf('_') > -1) {
            var grupo = gdlp.split('_')
            obj.empresa = grupo[0]
            obj.dpto = grupo[1]
          } else {
            obj.empresa = gdlp
            obj.dpto = ''
          }
        }) 
        // if(!!resp) {
        this.listaUsuarios = resp
          this.geraEstatisticas()
          this.listaUsuariosTodos()
        // } else {
        //   this.listaUsuarios = []
        //   this.geraEstatisticas()
        //   this.listaUsuariosTodos()
        // }
        this.ultVerifServer = this.formatDateMilisec(Date.now())
      })
      .catch( (err) => {
        this.loaderCheck = false
        this.listaUsuarios = []
        this.listaUsuariosTodos()
        this.ultVerifServer = this.formatDateMilisec(Date.now())
        console.log(err)
      })
    },
    listaUsuariosTodos() {
      this.tabTitulo = "Todos os usuários"
      this.botaoSelecionado('total')
      this.tabListaUsuarios = this.listaUsuarios;
      this.totalRows = this.tabListaUsuarios.length;
    },
    // listaMaquinasAtivas() {
    //   this.tabTitulo = "Máquinas com Atividades"
    //   this.botaoSelecionado('ativo')
    //   this.tabListaUsuarios = this.listaUsuarios.filter(obj => obj.maq_ativa == 'true')
    //   this.totalRows = this.tabListaUsuarios.length;
    // },
    // listaMaquinasInativas() {
    //   this.tabTitulo = "Maquinas sem Atividades "
    //   this.botaoSelecionado('inativo')
    //   this.tabListaUsuarios = this.listaUsuarios.filter(obj => obj.maq_ativa == 'false')
    //   this.totalRows = this.tabListaUsuarios.length;
    // },
    geraEstatisticas() {
      //if(this.listaUsuarios.length > 1){
        this.estatisticas = { total: 0, ativo: 0, inativo: 0 },
        this.estatisticas.total = this.listaUsuarios.length
        // this.listaUsuarios.forEach(maq => {
        //   if (maq.maq_ativa == 'true') { this.estatisticas.ativo++ }
        //   if (maq.maq_ativa === 'false') { this.estatisticas.inativo++ }
        // });
      //}
    },
    getParaphase() {
      return this.token +'%'+ this.locationInfo
    },
    setCrpStorage(dados){
      var dados = dados
      dados = this.$CryptoJS.AES.encrypt(dados, this.getParaphase()).toString()
      localStorage.setItem('x-mon-maq', dados)
    },
    getCrpStorage() {
      var dados = localStorage.getItem('x-mon-maq')
      dados = !!dados ? this.$CryptoJS.AES.decrypt(dados, this.getParaphase()).toString(this.CryptoJS.enc.Utf8) : '';
      return dados
    },
    // modal __________________________________________________________________
    abreModal(ref, dados) {
      !!dados ? this.dadosUsuario = dados : '';
      this.modalUsuario= {
        adiciona: ref == 'user-adiciona' ? true : false,
        edita:  ref == 'user-edita' ? true : false,
        remove:  ref == 'user-remove' ? true : false,
        senha:  ref == 'user-senha' ? true : false
      }
      setTimeout(()=> {
        this.$bvModal.show(ref)
      },10)
    },
    fechaModal(ref) {
      this.$bvModal.hide(ref)
      this.getListaUsuarios()
      setTimeout(()=>{
        this.dadosUsuario = ''
        this.modalUsuario= {
          adiciona: false,
          edita: false,
          remove: false,
          senha: false
        }
      },500)
    },
    // tabela __________________________________________________________________
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
    }
  },
  mounted() {
    this.getListaUsuarios()
    
    
    // window.onresize = this.tamanhoJanela()
  }
}
