<template>
<div class="text-center">
  <b-modal v-model="modal" ok-only show ref="modal-adiciona" id="modal-adiciona" size="md" hide-header-close no-close-on-backdrop no-close-on-esc>
    <template #modal-title>
      Adicionar Parâmetro:
    </template>
    <div class="container">
      <div class="row">
        <div class="col-sm-12 pb-3">
          <b-form-group
            label="Tipo de Dado"
            label-for="tipo"
            description="Selecione o tipo de dado que vai incluir"
            class="mb-0"
          >
          <b-form-select
            v-model="dadosParam.tipo"
            :state="valida.tipo"
            @change="verificaTipo(dadosParam.tipo)"
          >
            <b-form-select-option
              v-for="(opt, idx) in dadosParam.opcoes"
              :key="idx + 1"
              :value="idx + 1"
            >
              {{idx + 1}}) {{opt}}
            </b-form-select-option>
          </b-form-select>
            <!-- <b-form-input
              id="tipo"
              v-model="dadosParam.tipo"
              placeholder="Coloque a identificação"
              type="number"
              @keyup="verificaId(dadosParam.tipo)"
              @blur="verificaId(dadosParam.tipo)"
              :state="valida.id"
              maxlength="10"
            ></b-form-input> -->
          </b-form-group>
        </div>
        <div class="col-sm-12 pb-3">
          <b-form-group
            label="Nome do Campo"
            label-for="campo"
            description="Caracteres permitidos: ([-_.], [a-z], [A-Z] e [0-9])"
            class="mb-0"
          >
            <b-form-input
              id="campo"
              v-model="dadosParam.campo"
              placeholder="Nome do Campo"
              @keyup="verificaCampo(dadosParam.campo)"
              @blur="verificaCampo(dadosParam.campo)"
              :state="valida.campo"
              maxlength="30"
            ></b-form-input>
          </b-form-group>
        </div>
        <div class="col-sm-12 pb-3">
          <b-form-group
            label="Valor"
            label-for="valor"
            description="Caracteres permitidos ([-_.], [a-z], [A-Z] e [0-9])"
            class="mb-0"
          >
            <b-form-tags
              class="tag-area"
              input-id="valor"
              add-button-text="Inserir"
              placeholder="Adicione..."
              separator=","
              v-model="dadosParam.valor"
              v-if="dadosParam.tipo == 4"
            ></b-form-tags>
            <b-form-input
              id="valor"
              type="number"
              required
              name="valor"
              v-model="dadosParam.valor"
              v-else-if="dadosParam.tipo == 2"
            ></b-form-input>
            <b-form-checkbox
              id="valor"
              switch
              v-model="dadosParam.valor"
              v-else-if="dadosParam.tipo == 3"
            >
              Sim
            </b-form-checkbox>
            <b-form-input
              v-else
              id="valor"
              name="valor"
              v-model="dadosParam.valor"
              type="text"
              required
            ></b-form-input>
          </b-form-group>
        </div>
      </div>
      <div class="row">
      <b-alert :variant="resposta.status == 1 ? 'success' : 'warning'" show v-if="resposta.status !== 0" dismissible>
        <b-icon :icon="resposta.status == 1 ? 'check-circle-fill' : 'exclamation-circle-fill'" font-scale="1.2" class="mr-2"></b-icon>
        {{resposta.texto}}
      </b-alert>
      </div>
    </div>
    <template #modal-footer>
      <b-icon icon="arrow-clockwise" animation="spin" variant="primary" v-if="loaderCheck" font-scale="2"></b-icon>
      <b-button-group>
        <b-button variant="success" @click="salvarParam()" :disabled="!verificaSalvar()">
          <b-icon icon="check" class="mr-1"></b-icon> Salvar
        </b-button>
        <b-button variant="danger" @click="fechaModal('modal-adiciona')">
          <b-icon icon="x" class="mr-1"></b-icon> Cancelar
        </b-button>
      </b-button-group>
    </template>
  </b-modal>
</div>
</template>

<script>
var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Content-Type", "text/plain");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
  props: ['modalParam'],
  data() {
    return {
      // variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      loaderCheck: false,
      userLogado: {
        grupoDlp: '',
        user: ''
      },
      // vairáveis do componente
      modal: !!this.modalParam ? true : false ,
      dadosParam: {
        "tipo":"",
        "opcoes": ["Texto", "Número", "Booleano", "Objeto"],
        "campo": "",
        "valor": null
      },

      // validações de campos
      valida: {
        tipo: null,
        campo: null,
        valor: null
      },

      // resposta do endpoint
      resposta: {
        status: 0,
        texto: ''
      }
    }
  },
  watch: {
    modalParam(vlr) {
      this.modal = vlr
      console.log(vlr)
    },
    dados(d) {
      this.dados = d
    }
  },
  methods: {
    descSenha(s) {
      return window.atob(s)
    },
    verificaSalvar() {
      console.log(!!this.valida.tipo && !!this.valida.campo)
      return !!this.valida.tipo && !!this.valida.campo
    },
    verificaTipo(c) {
      this.valida.tipo = !!c
    },
    verificaCampo(c) {
      var campo = c.replace(/[`´~!@#$%^&*()|\+\-\=_?;:'"¨, <>\{\}\[\]\\\/àáâãäÀÁÂÃÄèéêëÈÉÊËìíîïÌÍÎÏóòôõöÓÒÔÕÖúùûüÚÙÛÜçÇñÑ]/gi, '');
      this.valida.campo = !!campo ? true : false; 
      this.dadosParam.campo = campo
    },
    //async salvarParam() {
    salvarParam() {
      this.loaderCheck = true
      let param = JSON.stringify({
        "tabela_end_point": "json_configs",
        "campos_select_end_point": "conteudo",
        "grupo_dlp": "*",
        "maquina": "SERVER",
        "descricao": this.dadosParam.campo,
        "conteudo": this.dadosParam.valor
      })
      let url = this.locationUrl + '/sql/updateSql/'+this.token+'/resp_rest'
      console.log(param)
      console.log(url)
      // await this.$http.post(url, param, myHeaders)
      // .then(() => {
        this.loaderCheck = false
        this.respostasEnvio(true)
      // })
      // .catch(() => {
      //   this.loaderCheck = false
      //   this.respostasEnvio(false)
      // })
    },
    respostasEnvio(resp) {
      if(!!resp){
        this.resposta = {
          status: 1,
          texto: `Parâmetro foi criado com sucesso!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3000);
        setTimeout(() => {
          this.fechaModal()
        }, 3100);
      } else {
        this.resposta = {
          status: 2,
          texto: `Falha ao criar o Parâmetro!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3000);
      }
    },
    abreModal() {
      // this.$refs['modal-adiciona'].show()
      this.$bvModal.show('modal-adiciona');
    },
    fechaModal() {
      this.$emit('limpaModal', 'modal-adiciona')
    }
  },
  mounted(){
    // this.$refs['modal-adiciona'].show()
    this.abreModal()
  }
}
</script>

<style>
.row {
  -webkit-box-orient: initial !important;
  flex-direction: inherit !important;
}
.gadget {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 5px 0;
  width: 100%;
  /* max-width: 300px; */
  height: 38px;
  border-radius: 5px;
  background: #ff3232;
  background: -moz-linear-gradient(left,  #ff3232 0%, #ff9000 25%, #ffff00 50%, #2ff000 75%, #0f9000 100%);
  background: -webkit-linear-gradient(left,  #ff3232 0%,#ff9000 25%,#ffff00 50%,#2ff000 75%,#0f9000 100%);
  background: linear-gradient(to right,  #ff3232 0%,#ff9000 25%,#ffff00 50%,#2ff000 75%,#0f9000 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff3232', endColorstr='#0f9000',GradientType=1 );
}
.gadget > .mark {
  position: absolute;
  display: block;
  width: 20px;
  height: 20px;
  top: 30px;
  transform: rotate(45deg);
  transition: .5s;
  margin-left: -10px;
  left: 0;
  box-shadow: #000 1px 1px 3px;
  z-index: 2;
}
.gadget .status {
  position: relative;
  display: block;
  width: 100%;
  margin-top: -2px;
  color: #FFF;
  background-color: rgba(0,0,0,0.15);
  text-shadow: #000 1px 1px 5px;
  text-align: center;
  text-transform: uppercase;
}
.senha {
  width: calc(100% - 30px) !important;
}
.verSenha {
    position: absolute;
    display: block;
    right: 15px;
    top: 40px;
    cursor:pointer;
}
</style>