<template>
<div>
  <div id="mySidenav" class="sidenav">
    <i href="javascript:void(0)" class="closebtn" @click="closeNav()">&times;</i>
    <nav id="menu-lateral">
      <ul>       
          <li v-for="route in routes" :key="route.id" :style="{display: [verItemMenu(route.ver)]}">
            <!-- <router-link :to="route.path ? route.path : '/'" v-if="route.name!='signup' && 
            route.name!='login' && route.name!='janelas' && route.name!='InformacoesAlerta' && route.name!='configuracoesId' "> -->
            <router-link :to="route.path ">
            <!-- {{route}} -->
            {{route.titulo}}
            </router-link>
          </li>
          <!--<li class="menu-li-react">
            <form style="width: 100%; height: auto; border: none;" action="http://localhost:3003/demo/star-admin-free/react/template/demo_1/preview/form-Elements/basic-elements/">
              <button style="border: none; padding: 8px 8px 8px 8px; color: white; -webkit-transition: 0.3s; transition: 0.3s; white-space: normal; border-bottom: 1px solid transparent; background-color: transparent;" type="submit">REACT CONFIG.</button><br />
              <input style="display: none;" type="text" id="ss" name="ss" v-model="this.tokenUsuario" />
            </form>
          </li>-->
          <!-- <li class="sairMobile"><router-link to="/conta" class="link-router-novo">Conta</router-link></li> -->
          <!--<li class="sairMobile" @click="redireciona"><router-link id="sair" to="" class="link-router-novo">Sair</router-link></li>-->
      </ul>

    </nav>
  </div>
  <!-- [<span style="font-size:30px;cursor:pointer;margin-right:0.5em;margin-left: 1em;" @click="openNav()">&#9776;</span>] -->
  <!-- <span style="font-size:30px;cursor:pointer;margin-right:0.5em;margin-left: 0.5em;" @click="viewSideBar()">&#9776;</span> -->
  <!-- <b-button variant="transparente" class="text-white px-1 outline-none" @click="voltaPagina()" title="Voltar"><b-icon icon="chevron-left" font-scale="2"></b-icon></b-button> -->
  <b-button variant="transparente" class="text-white px-1 outline-none" @click="viewSideBar()" title="Menu"><b-icon icon="list" font-scale="2"></b-icon></b-button>

</div>

</template>

<script>
import iconeFlash from '../../../assets/img/flashIcon2.png';
import foot2 from '../../../assets/img/user.png';
import btnNotification from '../../shared/buttonNotification/ButtonNotification';
import buttonConfiguracoes from '../../shared/buttonConfiguracoes/buttonConfiguracoes';
import { routes }  from '../../../routes';
//import { mapActions } from 'vuex'
export default {
  components:{
    'btnNotification': btnNotification,
    'buttonConfiguracoes':buttonConfiguracoes
  },
  data() {
    return {
        routes,
        sideBar: true,
        foto: {
          url: iconeFlash,
          titulo: 'logo flash'
        },
        foto2: {
          url: foot2,
          titulo: 'foto user'
        },
        status: false,
        tokenUsuario: '',
    }
  },
  methods: {
    voltaPagina() {
      this.$router.go(-1)
    },
      //...mapActions(['ActionSignOut']),
    viewSideBar () {
      this.sideBar = !this.sideBar
      //alert()
      this.sideBar == false ? this.closeNav() : this.openNav();
    },
    closeNav() {
      this.sideBar = false;
      document.getElementById("mySidenav").style.width = "0";
      document.getElementById("main").style.marginLeft= "0";
    },
    openNav(){
      this.sideBar = true;
      var resolucao = screen.width;
      var url = window.location.pathname;

      if(resolucao >= 992){
        document.getElementById("mySidenav").style.width = "200px";
        document.getElementById("main").style.marginLeft = "200px";
      }else{
        document.getElementById("mySidenav").style.width = "200px";
      }
    },   
    // redireciona(){
    //   var token =JSON.parse(localStorage.getItem('user')).keyAcesssSecret;

    //   var url = `${this.$store.state.variaveisAmbiente.locationUrl}/dpo/acesso/sessao/${token}/resp_rest`;
    //   this.$http.post(url)
    //   .then(response => response.text())
    //   .then(result => this.ActionSignOut())
    //   .then(result => this.$store.dispatch('ActionSignOut'))
    //   .catch(error => console.log('error', error));      
      
    //   this.$router.push({name:'login'})   
    // },
    redireciona(){
      /* ARMAZENA O TOKEN DENTRO DO STORAGE DO NAVEGADOR */
      var token =JSON.parse(localStorage.getItem('user'));
      var url = `${this.$store.state.variaveisAmbiente.locationUrl}/dpo/acesso/sessao/${token}/resp_rest`;
      this.$http.post(url)
     .then(response => response.text())
      //.then(result => this.ActionSignOut(),localStorage.removeItem('user'))
      .then(result => this.$store.dispatch('ActionSignOut'),localStorage.removeItem('user'))
      .catch(error => console.log('error', error));  
      this.$router.push({name:'login'})
      localStorage.clear()
    },
    verItemMenu(ver) {
     return !!ver ? 'block' : 'none';

      // switch(path) {
      //   case '/configuracoes/:grupo/:maquina':
      //     return 'none'
      //     break;
      //   default:
      //     return 'block'
      // }
    }

  },
  mounted() {
    var resolucao = screen.width;
    var url = window.location.pathname;
    if((resolucao >= 992) && (!url.includes('monitoramento'))){
      this.status = true;
      this.openNav();
    }
    //this.closeNav()
    this.openNav();
    //this.viewSideBar();
    
    /* CAPITURA O TOKEN PELO LOCAL STORAGE */
    this.tokenUsuario = JSON.parse(localStorage.getItem('user')).keyAcesssSecret;

  }

    
}
</script>

<style scoped>

nav#menu-lateral {
    text-transform: uppercase;
}
