  box-shadow: none !important;
}
.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  background-color: #4c7ab2;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
}

.sidenav a {
  padding: 8px 8px 8px 8px;
  text-decoration: none;
  color: white;
  display: block;
  transition: 0.3s;
  white-space: normal;
  border-bottom: 1px solid transparent;
}

.sidenav a:hover {
  color: #f1f1f1;
  border-bottom: 1px solid #f1f1f1;
}

/* .sidenav .closebtn {
  position: absolute;
  display: none;
  color: #FFF;
  font-style: normal;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
} */

#main {
  transition: margin-left .5s;
  padding: 16px;
}

/* @media screen and (max-width: 992){
  .sidenav .closebtn {
    display: block;
  }  
} */

.menu-li-react {
  border-bottom: 0px solid white;

  transition: border-bottom, .3 ease-in-out;

}

.menu-li-react:hover {
  border-bottom: 1px solid white;

}

@media screen and (max-height: 450px) {
  .sidenav {padding-top: 80px;}
  .sidenav a {font-size: 16px;}
}

@media (min-width: 64.2em) {
  .sairMobile{
    display: none;
  }
