<template>
  <b-modal v-model="modal" ok-only ref="user-edita" id="user-edita" size="lg" hide-header-close no-close-on-backdrop no-close-on-esc>
    <template #modal-title>
    </template>
    <div class="container">
      <div class="row">
        <div class="col-sm-12 col-md-12 col-lg-6 pb-3">
          <b-form-group
            label="Usuário"
            label-for="editUsuario"
            description="Caracteres permitidos: ([-_.], [a-z], [A-Z] e [0-9])"
            class="mb-0"
          >
            <!-- RECEBE O USUARIO -->
            <b-form-input
              id="editUsuario"
              v-model="dadosUser.usernameLogin"
              placeholder="Nome de usuário"
              @keyup="verificaUsuario(dadosUser.usernameLogin)"
              @blur="verificaUsuario(dadosUser.usernameLogin)"
              :state="valida.usuario"
              maxlength="30"
            ></b-form-input>
          </b-form-group>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-6 pb-3">
          <b-form-group
            label="E-mail"
            label-for="editEmail"
            description="Caracteres permitidos ([-_.], [a-z], [A-Z] e [0-9])"
            class="mb-0"
          >
            <!-- RECEBE O EMAIL -->
            <b-form-input
              id="editEmail"
              v-model="dadosUser.emailDesc"
              placeholder="E-mail do usuário"
              @keyup="verificaEmail(dadosUser.emailDesc)"
              @blur="verificaEmail(dadosUser.emailDesc)"
              :state="valida.email"
              maxlength="50"
            ></b-form-input>
          </b-form-group>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-4 pb-3">
          <b-form-group
            label="Empresa"
            label-for="editEmpresa"
            description="Caracteres permitidos ([*], [a-z] e [0-9])"
            class="mb-0"
          >
            <!-- RECEBE OS DADOS DA EMPRESA -->
            <b-form-input
              id="editEmpresa"
              v-model="dadosUser.empresa"
              placeholder="Identicação empresa"
              @keyup="verificaEmpresa(dadosUser.empresa)"
              @blur="verificaEmpresa(dadosUser.empresa)"
              maxlength="30"
              :disabled="userLogado.grupoDlp !== '*'"
            ></b-form-input>
          </b-form-group>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-4 pb-3">
          <b-form-group
            label="Departamento"
            label-for="editDepartamento"
            description="Caracteres permitidos ([*], [a-z] e [0-9])"
            class="mb-0"
          >
            <!-- RECEBE O DEPARTAMENTO -->
            <b-form-input
              id="editDepartamento"
              v-model="dadosUser.dpto"
              placeholder="Identicação deparamento!"
              @keyup="verificaDpto(dadosUser.dpto)"
              @blur="verificaDpto(dadosUser.dpto)"
              :disabled="!dadosUser.empresa || dadosUser.empresa === '*' || userLogado.grupoDlp.indexOf('*') < 0"
              maxlength="20"
            ></b-form-input>
          </b-form-group>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-4 pb-3">
          <b-form-group
            label="Grupo DLP"
            label-for="editGrupo"
            description="Resultado de empresa e depart."
            class="mb-0"
          >
            <!-- RECEBE O GRUPO DLP -->
            <b-form-input
              id="editGrupo"
              v-model="dadosUser.grupoDlp"
              placeholder="Grupo DLP gerado"
              disabled
              :state="valida.grupoDlp"
            ></b-form-input>
          </b-form-group>
        </div>

      </div>
      <b-alert :variant="resposta.status == 1 ? 'success' : 'warning'" show v-if="resposta.status !== 0" dismissible>
        <b-icon :icon="resposta.status == 1 ? 'check-circle-fill' : 'exclamation-circle-fill'" font-scale="1.2" class="mr-2"></b-icon>
        {{resposta.texto}}
      </b-alert>
    </div>
    <template #modal-footer>
      <b-icon icon="arrow-clockwise" animation="spin" variant="primary" v-if="loaderCheck" font-scale="2"></b-icon>
      <b-button-group>
        <!-- CHAMA O METODO QUE IRA ENVIAR OS DADOS ATUALIZADOS A BASE DE DADOS -->
        <b-button variant="success" @click="salvarUser()" :disabled="verificaSalvar()">
          <b-icon icon="check" class="mr-1"></b-icon> Salvar
        </b-button>
        <b-button variant="danger" @click="fechaModal('user-edita')">
          <b-icon icon="x" class="mr-1"></b-icon> Cancelar
        </b-button>
      </b-button-group>
    </template>
  </b-modal>
</template>

<script>
var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Content-Type", "text/plain");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
  props: ['modalUsuario', 'dados'],
  data() {
    return {
      // variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      loaderCheck: false,
      userLogado: {
        grupoDlp: '',
        user: ''
      },
      // vairáveis do componente
      modal: !!this.modalUsuario ? true : false ,
      dadosUser: this.dados,
      // validações de campos
      valida: {
        usuario: null,
        email: null,
        grupoDlp: null
      },
      // resposta do endpoint
      resposta: {
        status: 0,
        texto: ''
      }
    }
  },
  watch: {
    modalUsuario(vlr) {
      this.modal = vlr
    },
    dados(d) {
      this.dados = d
    }
  },
  methods: {
    descSenha(s) {
      return window.atob(s)
    },
    verificaSalvar() {
      return this.valida.usuario !== false && this.valida.email !== false && this.valida.grupoDlp !== false ? false : true;
    },
    /* VERIFICA SE O EMAIL E UM EMAIL VALIDO */
    verificaEmail(e) {
      let reg = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/;
      let email = e
      let result = email.search(reg)
      result == -1 ? this.valida.email = false : this.valida.email = true;
    },
    verificaUsuario(u) {
      var user = u.replace(/[`´~!@#$%^&*()|+\=?;:'"¨, <>\{\}\[\]\\\/àáâãäÀÁÂÃÄèéêëÈÉÊËìíîïÌÍÎÏóòôõöÓÒÔÕÖúùûüÚÙÛÜçÇñÑ]/gi, '');
      this.valida.usuario = !!user ? true : false; 
      this.dadosUser.usernameLogin = user
    },
    /* VERIFICA SE O USUARIO LOGADO SE TRATA DE UM (*) QUE TENHA PRIVILEGIOS PARA INSERIR OU ALTERAR PARAMETROS NO SISTEMA */
    verificaEmpresa(e){
      var emp = e.replace(/[`´~!@#$\%^&()|_\+\-\=?;:'"¨., <>\{\}\[\]\\\/àáâãäÀÁÂÃÄèéêëÈÉÊËìíîïÌÍÎÏóòôõöÓÒÔÕÖúùûüÚÙÛÜçÇñÑ]/gi, '');
      // var emp = e.replace(/[`´~!@#$%^&*()|+\=?;:'"¨,_ <>\-\{\}\[\]\\\/àáâãäÀÁÂÃÄèéêëÈÉÊËìíîïÌÍÎÏóòôõöÓÒÔÕÖúùûüÚÙÛÜçÇñÑ]/gi, '');
      emp = emp.toLowerCase()
      emp.indexOf(' ') != -1 ? emp = emp.replaceAll(' ', '') : emp = emp;
      this.dadosUser.empresa = emp
      if(emp.length > 1 && emp.indexOf('*') > -1 ){
          this.dadosUser.empresa = emp.replace('*', '')

        this.dadosUser.dpto = '*';
      }
      emp === '*' || !emp ? this.dadosUser.dpto = '' : this.dadosUser.dpto = this.dadosUser.dpto;
      this.dadosUser.grupoDlp = this.verificaGrupoDLPEdit();
    },
    /* VERIFICA O DEPARTAMENTO DO USUARIO */
    verificaDpto(d){
      var dep = d.replace(/[`´~!@#$\%^&()|_\+\-\=?;:'"¨., <>\{\}\[\]\\\/àáâãäÀÁÂÃÄèéêëÈÉÊËìíîïÌÍÎÏóòôõöÓÒÔÕÖúùûüÚÙÛÜçÇñÑ]/gi, '');
      dep = dep.toLowerCase()
      this.dadosUser.dpto = dep
      
      if(dep.length > 1 && dep.indexOf('*') > -1 ){
          this.dadosUser.dpto = dep.replace('*', '')
      }
      this.dadosUser.grupoDlp = this.verificaGrupoDLPEdit();
    },
    /* VERIFICA SE O GRUPO DPL NO QUAL O USUARIO PERTECE E UM (*) QUE TENHA PRIVILEGIOS PARA ALTERAR OS PARAMENTROS DO SISTEMA */
    verificaGrupoDLPEdit() {
      if(this.dadosUser.empresa == '' && this.dadosUser.dpto == '') {
        this.valida.grupoDlp = false
      } else if(this.dadosUser.empresa === '*'){
        this.valida.grupoDlp = true
        return this.dadosUser.empresa;
      } else if(!!this.dadosUser.empresa && this.dadosUser.dpto === '*') {
        this.valida.grupoDlp = true
        return this.dadosUser.empresa+this.dadosUser.dpto
      } else if(!!this.dadosUser.empresa && !!this.dadosUser.dpto) {
        this.valida.grupoDlp = true
        return this.dadosUser.empresa+'_'+this.dadosUser.dpto
      } else if(this.dadosUser.empresa.indexOf('*') > -1 && !this.dadosUser.dpto){
        this.valida.grupoDlp = true
        var emp = this.dadosUser.empresa.split('*')
        return !!emp[1] ? emp[0]+emp[1]+'*' : emp[0]+'*';
      } else if(this.dadosUser.empresa.indexOf('*') < 0 && !this.dadosUser.dpto) {
        this.valida.grupoDlp = true
        return this.dadosUser.empresa;
      } else if(!this.dadosUser.empresa){
        this.valida.grupoDlp = true
        this.dadosUser.dpto = ''
      } else {
        this.valida.grupoDlp = false
      }
    },
    /* REALIZA A ALTERACAO DO USUARIO E CHAMA O METODO RESPOSAVEL POR ENVIAR A REQUISICAO A BASE DE DADOS, INFORMANDO OS NOVOS VALORES*/
    async salvarUser() {
      this.loaderCheck = true
      let param = JSON.stringify({
        "nomeDpo": this.dadosUser.usernameLogin,
        "emailDpo": this.dadosUser.emailLogin,
        "tipoPerfilDpo":1
      });
      let url = `${this.locationUrl}/dpo/perfil/atu/${this.dadosUser.dpo_idLogin}/${this.token}/resp_rest`
      await this.$http.post(url, param, myHeaders)
      .then(res => {
        this.loaderCheck = false
        !!res ? true : false ;
        this.salvaLogin()
      })
      .catch(err => {
        this.loaderCheck = false
        !!err ? true : false ;
        this.respostasEnvio(false)
      })
    },
    /* RECEBE OS NOVOS DADOS DO USUARIO E ENVIA UMA REQUISICAO AO BANCO DE DADOS PARA QUE AS ALTERACOES SEJAM REALIZADAS */
    async salvaLogin() {
      let psw = window.atob(this.dadosUser.hashSenhaLogin)
      let data = new Date()
      data = `${data.getFullYear()}-${(data.getMonth() + 1) < 10 ? '0' + (data.getMonth() + 1) : data.getMonth() + 1 }-${(data.getDate()) < 10 ? '0' + (data.getDate()) : data.getDate()} ${(data.getHours()) < 10 ? '0' + (data.getHours()) : data.getHours()}:${(data.getMinutes()) < 10 ? '0' + (data.getMinutes()) : data.getMinutes()}:${(data.getSeconds()) < 10 ? '0' + (data.getSeconds()) : data.getSeconds()}}`
      let param = JSON.stringify({
        "login_idLogin": this.dadosUser.loginId, // ID DO USUARIO PARA LOCALIZACAO NA BASE DE DADOS E ALTERACAO DOS DADOS CADASTRADOS
        "usernameLogin": this.dadosUser.usernameLogin,
        "emailLogin": this.dadosUser.emailDesc,
        "dtCriacaoLogin": this.dadosUser.dtCriacaoLogin,
        "dtAtualizacaoLogin": data,
        "permissoesLogin": this.dadosUser.permissoesLogin,
        "hash_senhaLogin": psw,
        "dpo_idLogin": this.dadosUser.loginId,
        "grupoDlp":this.dadosUser.grupoDlp
      })
      let url = `${this.locationUrl}/dpo/login/atua/${this.dadosUser.loginId}/${this.token}/resp_rest`;
      await this.$http.post(url, param, myHeaders)
      .then(res => {
        this.loaderCheck = false
        !!res ? true : false ;
        this.respostasEnvio(true)
      })
      .catch(err => {
        this.loaderCheck = false
        !!err ? true : false ;
        this.respostasEnvio(false)  
      })
    },
    /* RETORNA UMA MENSAGEM POSITIVA OU NEGATIVA EM RELACAO A ALTERACAO DO USUARIO */
    respostasEnvio(resp) {
      if(!!resp){
        this.resposta = {
          status: 1,
          texto: `Usuário "${this.dadosUser.usernameLogin}" foi alterado com sucesso!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3000);
        setTimeout(() => {
          this.fechaModal()
        }, 3100);
      } else {
        this.resposta = {
          status: 2,
          texto: `Falha ao salvar as alterações do usuário "${this.dadosUser.usernameLogin}"!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3000);
      }
    },
    fechaModal() {
      this.$emit('limpaModal', 'user-edita')
    },
    userLog(){
      let dados = window.atob(this.token)
      let user = dados.split('-')
      this.userLogado.user = user[0]
      let grupo = dados.split('^')
      this.userLogado.grupoDlp = grupo[1]
    }
  },
  mounted(){
      this.$refs['user-edita'].hide()
      this.userLog()
    // this.resposta = {status: 1, texto: 'Usuário salvo com sucesso'}
  }
}
</script>

<style>
.row {
  -webkit-box-orient: initial !important;
  flex-direction: inherit !important;
}
</style>