import Header from '../../shared/header/Header'
import Menu from '../../shared/menu/Menu'
import Loader from '../../shared/loader/loader'

import dadosToken from '../../shared/dadosToken/dadosToken'

import caminhoRelativo from '../../../store/state'

var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
  components: {
    'meu-painel': Header,
    'menu-lateral': Menu,
    'loader': Loader
  },
  data() {
    return {
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret,
      chkToken: dadosToken.getToken(),
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      //redirecionamento: `${this.$store.state.variaveisAmbiente.locationUrl.split(':')[0]}:${this.$store.state.variaveisAmbiente.locationUrl.split(':')[1]}:${this.$store.state.variaveisAmbiente.porta}/flashsafe/conf/form-Elements/configuracoes/`,
      redirecionamento: `https://homologa.epsoft.com.br:${this.$store.state.variaveisAmbiente.porta}/flashsafe/conf/form-Elements/configuracoes/`,
      loaderCheck: false,
      tokenUsuario: '',
    }
  },
  methods: {
    /* RECEBE COMO PAREMTRO UM DETERMIANDO ENDPOINT DE ACORDO COM A OPCAO CLICADA */
    abreModulo(mod) {
      this.$router.push(mod)
    },
  },
  mounted() {
    console.log(this.chkToken)

    /* CAPITURA O TOKEN PELO LOCAL STORAGE */
    this.tokenUsuario = JSON.parse(localStorage.getItem('user')).keyAcesssSecret;


    /* FICA OBSERVANDO SEMPRE QUE A PAGINA E RECARREGADA */
    console.log("Teste: "+ (new Date().getMinutes() - parseInt(localStorage.getItem('dataLogin'))));

    if((new Date().getMinutes() - parseInt(localStorage.getItem('dataLogin'))) >= localStorage.getItem('limiteSessao')) {
      localStorage.removeItem('infoUser');
      localStorage.removeItem('user');
      localStorage.removeItem('dataLogin');

      location.reload(true);

    }
    
  }
}
