<template>
  <b-modal ok-only :ref="`sms${idx}`" :id="`sms${idx}`" size="md" no-close-on-backdrop no-close-on-esc>
    <template #modal-title>
      SMS:
    </template>
    <div class="container">
      <div class="row">
        <div class="col-12 pb-3">
          <span>Máquina:</span><br>
          <strong class="text-uppercase">{{`${dadosMaq.grupo_dlp}-${dadosMaq.computer_name}-${dadosMaq.cod_maquina}`}}</strong>
        </div>
        <div class="col-12 pb-3">
          <b-form-group
            label="Celular: "
            label-for="smsCelular"
            description="Coloque somente numeros ex: (XX) XXXXX-XXXX"
            class="mb-0"
            v-if="mensagem.contato"
          >
            <b-form-input
              class="contato"
              id="smsCelular"
              placeholder="Coloque um Número de Celular"
              v-mask="maskNum()"
              v-model="mensagem.numero"
              @keyup="verificaNum(mensagem.numero)"
              @blur="verificaNum(mensagem.numero)"
              :state="valida.numero"
            ></b-form-input>
            <b-icon class="contato-icon" font-scale="1.5" icon="person-fill" variant="secondary" @click="verificaContato()" v-b-tooltip.hover.topleft="'Alternar para Meta Dados?'"></b-icon>
          </b-form-group>
          <b-form-group
            label="Meta Dados:"
            label-for="smsUser"
            :description="`Coloeque o metadado sem <>, ex: 'mariasantana' `"
            class="mb-0"
            v-if="!mensagem.contato"
          >
            <b-form-input
              class="contato"
              id="smsMeta"
              placeholder="Coloque um Meta Dados"
              type="text"
              maxlength="20"
              v-model="mensagem.metadados"
              @keyup="verificaMeta(mensagem.metadados)"
              @blur="verificaMeta(mensagem.metadados)"
              :state="valida.metadados"
            ></b-form-input>
            <b-icon class="contato-icon" font-scale="1.5" icon="telephone-fill" variant="secondary" @click="verificaContato()" v-b-tooltip.hover.topleft="'Alternar para Número de Celular?'"></b-icon>
          </b-form-group>
        </div>
        <div class="col-12 pb-3">
          <b-form-group 
            label="Texto do SMS:"
            label-for="smsTexto"
            class="mb-0 descricao"
            :description="`${mensagem.max - mensagem.texto.length} caracteres restantes`"
          >
            <b-form-textarea
              id="smsTexto"
              class="mensagem"
              v-model="mensagem.texto"
              placeholder="Limite 159 caracteres"
              :maxlength="mensagem.max"
              @keydown="verificaTexto(mensagem.texto)"
              @blur="verificaTexto(mensagem.texto)"
              :state="valida.texto"
              no-resize
              rows="4"
            >
            </b-form-textarea>
          </b-form-group>
        </div>
        <div class="col-12 pb-3">
          <b-form-group
            label="Comentario:"
            label-for="smsComentario"
            class="mb-0"
            :description="`${mensagem.max - mensagem.comentario.length} caracteres restantes`"
          >
            <b-form-textarea
              id="smsComentario"
              class="mensagem"
              v-model="mensagem.comentario"
              :state="valida.comentario"
              :maxlength="mensagem.max"
              @keydown="verificaComent(mensagem.comentario)"
              @blur="verificaComent(mensagem.comentario)"
              no-resize
              rows="4"
            ></b-form-textarea>
          </b-form-group>
        </div>
      </div>
      <b-alert :variant="resposta.status == 1 ? 'success' : 'warning'" show v-if="resposta.status !== 0" dismissible>
        <b-icon :icon="resposta.status == 1 ? 'check-circle-fill' : 'exclamation-circle-fill'" font-scale="1.2" class="mr-2"></b-icon>
        {{resposta.texto}}
      </b-alert>
    </div>
    <template #modal-footer>
      <b-icon icon="arrow-clockwise" animation="spin" variant="primary" v-if="loaderCheck" font-scale="2"></b-icon>
      <b-button-group>
        <b-button variant="success" style="color: white" @click="geraIdSms()" :disabled="!verificaSms()">
          <b-icon icon="chat-right-text-fill" class="mr-1"></b-icon>
          Enviar
        </b-button>
        <b-button variant="danger" @click="fechaModal()">
          <b-icon icon="x" class="mr-1"></b-icon> Cancelar
        </b-button>
      </b-button-group>
    </template>
  </b-modal>
</template>
<script>
var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Content-Type", "text/plain");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
  props: ['dados', 'idx'],
  data() {
    return {
      // variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      loaderCheck: false,
      // vairáveis do componente
      dadosMaq: this.dados,
      mensagem: {
        id: '',
        numero: '',
        metadados: '',
        contato: true,
        texto: '',
        comentario: '',
        max: 159
      },
      // validações de campos
      valida: {
        numero: null,
        metadados: null,
        texto: null,
        comentario: null
      },
      // resposta do endpoint
      resposta: {
        status: 0,
        texto: ''
      }
    }
  },
  watch: {
    dados(d) {
      this.dadosMaq = d
    },
  },
  methods: {
    verificaSms() {
      return this.valida.texto && this.valida.comentario && (this.valida.numero || this.valida.metadados)
    },
    maskNum() {
      var num = this.mensagem.numero
      if(!num) {
        return '(##) #####-####'
      } else {
        return num.length == 15 ? '(##) #####-####' : '(##) ####-#####'
      }
    },
    maskMeta() {
      let regexp = /[`´~!@#$%^&*()|+\=?;:'"¨, <>\{\}\[\]\\\/àáâãäÀÁÂÃÄèéêëÈÉÊËìíîïÌÍÎÏóòôõöÓÒÔÕÖúùûüÚÙÛÜçÇñÑ]/gi
      let meta = this.mensagem.metadados
      meta.replace(regexp, '')
     this.mensagem.metadados = meta.toLowerCase()
    },
    verificaNum(n) {
      let cel = n
      if(!cel) {
        this.valida.numero = null;
      } else {
        this.valida.numero = cel.length >= 14 ? true : false;
      }
    },
    verificaMeta(m){
      let reg = /[`´~!@#$%^&*()|+\=?;:'"¨, <>\{\}\[\]\\\/àáâãäÀÁÂÃÄèéêëÈÉÊËìíîïÌÍÎÏóòôõöÓÒÔÕÖúùûüÚÙÛÜçÇñÑ]/gi
      let meta = m.replaceAll(reg, '')
      meta = meta.toLowerCase()
      this.mensagem.metadados = meta
      this.valida.metadados = !meta ? null : true ;
    },
    verificaComent(c){
      let com = c;
      if(!com) { 
        this.valida.comentario = null
      } else {
        this.mensagem.comentario = com
        this.valida.comentario = com.length >= 2 ? true : false;
      } 
    },
    verificaContato() {
      this.mensagem.contato = !this.mensagem.contato
      this.mensagem.numero = ''
      this.mensagem.metadados = ''
      this.valida.numero = null
      this.valida.metadados = null
    },
    verificaTexto(t){
      let txt = t
      this.valida.texto = !!txt ? true : false
    },
    resetDados() {
      this.mensagem = {
        id: '',
        num: '',
        contato: true,
        texto: '',
        comentario: '',
        max: 159
      }
      this.valida= {
        numero: null,
        metadados: null,
        texto: null,
        comentario: null
      }
      this.dadosMaq = []
    },
    async geraIdSms() {
      this.loaderCheck = true
      let url = `${this.locationUrl}/subMenu/getIdSms/${this.token}/resp_rest`;
      await this.$http.get(url, myHeaders)
      .then(res => {
        this.loaderCheck = false
        this.mensagem.id = res.data.id_sms
        !!res ? true : false ;
        setTimeout(()=>{
          this.enviaSMS()
        },100)
      })
      .catch(err => {
        this.loaderCheck = false
        this.respostasEnvio(false)
        !!err
      })
    },
    async enviaSMS() {
      this.loaderCheck = true
      let contato = this.mensagem.numero != '' ? `55${this.mensagem.numero.replace(/\D/gim, '')}` : `<${this.mensagem.metadados}>`
      
      console.log("ID alarme: " + this.dadosMaq.id_alarme)
      console.log("Meu contato: " + contato)
      console.log("Meu Texto: " + this.mensagem.texto.replace(/(\r\n|\n|\r)/gm," "))
      console.log("Meu ID: " + this.mensagem.id)
      console.log("Meu Comentário: " + this.mensagem.comentario)

      let param = !!this.dadosMaq.id_alarme ? 
        JSON.stringify({
          "id_alarme": this.dadosMaq.id_alarme,
          "telefone":contato,
          "texto": `[ID DO ALARME: ${this.dadosMaq.id_alarme}] - ` + this.mensagem.texto.replace(/(\r\n|\n|\r)/gm," "),
          "id_sms":this.mensagem.id,
          "comentarios":this.mensagem.comentario
        })
       : 
        JSON.stringify({
          "telefone":contato,
          "texto":this.mensagem.texto.replace(/(\r\n|\n|\r)/gm," "),
          "id_sms":this.mensagem.id,
          "comentarios":this.mensagem.comentario
        })
      ;
      
      // let param = ''
      // if(!!this.dadosMaq.id_alarme) {
      //   param = JSON.stringify({
      //     "id_alarme": this.dadosMaq.id_alarme,
      //     "telefone":contato,
      //     "texto":this.mensagem.texto.replace(/(\r\n|\n|\r)/gm," "),
      //     "id_sms":this.mensagem.id,
      //     "comentarios":this.mensagem.comentario
      //   })
      // } else {
      //   param = JSON.stringify({
      //     "telefone":contato,
      //     "texto":this.mensagem.texto.replace(/(\r\n|\n|\r)/gm," "),
      //     "id_sms":this.mensagem.id,
      //     "comentarios":this.mensagem.comentario
      //   })
      // }
      let url = `${this.locationUrl}/subMenu/enviaSms/${this.token}/resp_rest`;
      await this.$http.post(url, param, myHeaders)
      .then(res => {
        this.loaderCheck = false
        !!res ? true : false ;
        this.respostasEnvio(true)
        this.valida.chat = null
        this.mensagem = ''
      })
      .catch(err => {
        this.loaderCheck = false
        !!err ? true : false ;
        this.respostasEnvio(false)
      })
    },
    respostasEnvio(resp) {
      if(!!resp){
        this.resposta = {
          status: 1,
          texto: `Mensagem de SMS enviada com sucesso!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
        setTimeout(() => {
          this.fechaModal()
          this.resetDados()
        }, 3600);
      } else {
        this.resposta = {
          status: 2,
          texto: `Falha ao enviar mensagem de SMS!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 3500);
      }
    },
    fechaModal() {
      this.$refs[`sms${this.idx}`].hide()
    },
    abreModal(){
      this.$refs[`sms${this.idx}`].hide()
    }
  },
  mounted(){
    this.fechaModal()
  }
}
</script>
<style>
.row {
  -webkit-box-orient: initial !important;
  flex-direction: inherit !important;
}
.mensagem {
  width: 100%;
}
.descricao small{
  position: relative;
  display: block;
  width: 100%;
  text-align: right;
}
/* Remove os arrows do campo numero */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type=number] {
  -moz-appearance: textfield;
}
/* fim remove arrows */
.contato {
  width: calc(100% - 30px) !important;
}
.contato-icon {
    position: absolute;
    display: block;
    right: 15px;
    top: 40px;
    cursor:pointer;
}
</style>