<template> 
  <b-modal ok-only :ref="`dispEdita${idx}`" :id="`dispEdita${idx}`"  size="md" no-close-on-backdrop no-close-on-esc>
    <template #modal-title>
      {{dadosDisp.status !== 'LIBERADO'? 'Liberar' : 'Bloquear'}} Dispositivo: 
    </template>
    <div class="container">
      <div class="row">
        <div class="col-12 pb-3">
          <span>Grupo DLP / Código de Máquina:</span><br>
          <strong class="text-uppercase">{{`${dadosDisp.grupo_dlp} / ${dadosDisp.maquina == '*' ? 'Todas as máquinas': dadosDisp.maquina}`}}</strong>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-12 col-sm-6  pb-3" v-if="!!dadosDisp.codigo_dispositivo">
          <span>Cod. Dispositivo:</span><br>
          <strong class="text-uppercase">{{dadosDisp.codigo_dispositivo}}</strong>
        </div>
        <div class="col-xs-12 col-sm-6 pb-3" v-if="!!dadosDisp.id_alarme">
          <span>ID do Alerta:</span><br>
          <strong class="text-uppercase">{{dadosDisp.id_alarme}}</strong>
        </div>
        <div class="col-12 pb-3" v-if="!!dadosDisp.status">
          <span>Alterar o status para:</span><br>
          <b-card class="disp-status text-uppercase" body-bg-variant="#f9f9f9" :body-text-variant="dadosDisp.status !== 'LIBERADO'? 'success' : 'warning'">
            <b-card-text>
                <b-icon class=" mx-2" icon="hdd-fill" font-scale="1" v-if="dadosDisp.status !== 'LIBERADO'"></b-icon>
                <b-iconstack class="mx-2" font-scale="1" v-else>
                  <b-icon icon="hdd-fill" scale="1"></b-icon>
                  <b-icon icon="slash-circle" scale="1.6"></b-icon>
                </b-iconstack>
                <strong>{{dadosDisp.status !== 'LIBERADO'? 'Liberado' : 'Bloqueado'}}</strong>
              </b-card-text>
          </b-card>
        </div>
        <div class="col-12 pb-3">
          <b-form-group
            label="Comentario:"
            label-for="dispComentario"
            class="mb-0"
          >
            <b-form-textarea
              id="dispComentario"
              class="comentario"
              v-model="mensagem"
              :state="valida.mensagem"
              placeholder="Coloque pelo menos 3 caracteres"
              no-resize
              rows="4"
            ></b-form-textarea>
          </b-form-group>
        </div>
      </div>
      <b-alert :variant="resposta.status == 1 ? 'success' : 'warning'" show v-if="resposta.status !== 0" dismissible>
        <b-icon :icon="resposta.status == 1 ? 'check-circle-fill' : 'exclamation-circle-fill'" font-scale="1.2" class="mr-2"></b-icon>
        {{resposta.texto}}
      </b-alert>
    </div>
    <template #modal-footer>
      <b-icon icon="arrow-clockwise" animation="spin" variant="primary" v-if="loaderCheck" font-scale="2"></b-icon>
      <b-button-group>
        <b-button variant="success" style="color: white" @click="alteraStatus()" :disabled="!verificaChat()">
          <b-icon icon="pencil" class="mr-1"></b-icon>
           Alterar
        </b-button>
        <b-button variant="danger" @click="fechaModal()">
          <b-icon icon="x" class="mr-1"></b-icon> Cancelar
        </b-button>
      </b-button-group>
    </template>
  </b-modal>
</template>
<script>

const urlApi = ambientes.ativo[0].url + ":" + ambientes.ativo[0].porta_api


var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Content-Type", "text/plain");
myHeaders.append("Cache-Control", "no-cache");
myHeaders.append('Expires', '-1');
myHeaders.append('Pragma', 'no-cache');

export default {
  props: ['dados', 'idx', 'dados.id_dispositivo'],
  data() {
    return {
      // variaveis de ambiente
      token: JSON.parse(localStorage.getItem('user')).keyAcesssSecret, 
      locationUrl: this.$store.state.variaveisAmbiente.locationUrl,
      locationImg: this.$store.state.variaveisAmbiente.locationImg,
      locationInfo: this.$store.state.variaveisAmbiente.infoTxt,
      ambiente: this.$store.state.variaveisAmbiente.ambiente,

      loaderCheck: false,
      // vairáveis do componente
      dadosDisp: this.dados,
      mensagem: '',
      // validações de campos
      valida: {
        mensagem: null,
      },
      // resposta do endpoint
      resposta: {
        status: 0,
        texto: ''
      }
    }
  },
  watch: {
    modalDesliga(vlr) {
      this.modal = vlr
    },
    dados(d) {
      this.dadosDisp = d
    }
  },
  methods: {
    dataAgora() {
      let agora = new Date();
      let hora = agora.toString().split(' ')
      let dia = agora.getDate()
      dia < 10 ? dia = '0' + dia : dia;
      let mes = agora.getMonth() + 1
      mes < 10 ? mes = '0' + mes : mes;
      let ano = agora.getFullYear() //- 100 
      let hoje = `${ano.toString()}-${mes.toString()}-${dia.toString()} ${hora[4]}`
      return hoje
    },
    getUser() {
      let  user = window.atob(this.token)
      user = user.split('-')
      return user[0]
    },
    verificaChat() {
      var cont = this.mensagem.length
      this.valida.mensagem = cont > 2 ? true : null;
      return this.valida.mensagem
    },
    async alteraStatus() {
      this.loaderCheck = true
      let paramComent = this.arrComents()

      console.log('ENTROU NO METODO: alteraStatus()');

      let dispStt = this.dadosDisp.status !== 'LIBERADO' ? 'LIBERADO' : 'BLOQUEADO';

      // let param = JSON.stringify({
      //   "tabela_end_point": "dispositivos", // NOME DA TABELA
      //   "campos_select_end_point": "codigo_dispositivo", // PK
      //   "maquina":this.dadosDisp.maquina,
      //   "grupo_dlp":this.dadosDisp.grupo_dlp,
      //   "codigo_dispositivo":this.dadosDisp.codigo_dispositivo,
      //   "dataCadastramento":this.dadosDisp.dataCadastramento,
      //   "status":dispStt,
      //   "id_alarme":this.dadosDisp.id_alarme,
      //   "login_user":this.getUser(),
      //   "data_alteracao":this.dataAgora(),
      //   "comentarios":paramComent
      // })

      let param = JSON.stringify({
        "tabela_end_point": "dispositivos", // NOME DA TABELA
        "campos_select_end_point": "codigo_dispositivo", // PK
        "codigo_dispositivo": this.dadosDisp.codigo_dispositivo,
        "status": dispStt,
        "id_dispositivo": "",
        "grupo_dlp": this.dadosDisp.grupo_dlp,
        "maquina": this.dadosDisp.maquina,
        "dataCadastramento": this.dadosDisp.dataCadastramento,
        "id_alarme": this.dadosDisp.id_alarme,
        "login_user": this.getUser(),
        "data_alteracao": this.dataAgora(),
        "comentarios": paramComent
      })

      //let url = `${this.locationUrl}/sql/updateSql/${this.token}/resp_rest`;
      let url = `${urlApi}/api/dispositivos/updatestatuspen/${this.dadosDisp.maquina}/${this.ambiente}/${this.token}`

      console.log('DADOS PENDRIVE: ');
      console.log(param);
      console.log('dadosDisp')
      console.log()
      console.log(url);
      
      await this.$http.put(url, param, myHeaders)
      .then(res => {
        this.loaderCheck = false
        !!res ? true : false ;
        this.respostasEnvio(true)
        this.valida.mensagem = null
        this.mensagem = ''

        // if(dispStt == "BLOQUEADO") {
        //   this.alteraPendriveReiniciar() // CHAMA O MOTODO PARA REINICIAR A MAQUINA
        // }

      })
      .catch(err => {
        this.loaderCheck = false
        !!err ? true : false ;
        this.respostasEnvio(false)
      })
    },

    // NOVO ENDPOINT
    async alteraPendriveReiniciar() {
      this.loaderCheck = true

      console.log('CHAMOU O NOVO METODO DE REINICIAR DLP PENDRIVE');

      // subMenu/reInicDlp/:secret/:maquina/:resp_rest
      let url = `${this.locationUrl}/subMenu/reInicDlp/${this.token}/${this.dadosDisp.maquina}/resp_rest`;

      console.log("URL: "+ url);

      await this.$http.get(url, myHeaders)
      .then(res => {
        this.loaderCheck = false
        !!res ? true : false ;
        this.respostasEnvio(true)
        this.valida.mensagem = null
        this.mensagem = ''
      })
      .catch(err => {
        this.loaderCheck = false
        !!err ? true : false ;
        this.respostasEnvio(false)
      })
    },

    arrComents() {
      console.log("dadosDisp.comentarios: "+ this.dadosDisp.comentarios);
      let arr = this.dadosDisp.comentarios
      let status = this.dadosDisp.status !== 'LIBERADO' ? 'LIBERADO' : 'BLOQUEADO';
      //let mens = this.mensagem.indexOf('\\n') > -1 ? this.mensagem.replaceAll('\\n', '<br>') : this.mensagem;
      arr = arr.indexOf('**') > -1 ? arr.replaceAll('**', '"') : "[]";
      arr = JSON.parse(arr)
      arr.push({msg:this.mensagem, user:this.getUser(), data: this.dataAgora(), stt: status})
      arr = JSON.stringify(arr).replaceAll('"', "**")
      arr = arr.indexOf('\\n') > -1 ? arr.replaceAll('\\n', '<br>') : arr;
      return arr
    },
    respostasEnvio(resp) {
      if(!!resp){
        this.resposta = {
          status: 1,
          texto: `Alteração do dispositivo com código "${this.dadosDisp.codigo_dispositivo}" realizado com sucesso!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 1500);
        setTimeout(() => {
          this.fechaModal()
          this.$emit('carregaDados')
        }, 1600);
      } else {
        this.resposta = {
          status: 2,
          texto: `Falha ao realizar alteração do dispositivo com código "${this.dadosDisp.codigo_dispositivo}"!`
        }
        setTimeout(() => {
          this.resposta = {status: 0, texto: ''}
        }, 1500);
      }
    },
    fechaModal() {
      this.$refs[`dispEdita${this.idx}`].hide()
    },
    abreModal(){
      this.$refs[`dispEdita${this.idx}`].hide()
    }
  },
  mounted(){
      this.fechaModal()
      this.dataAgora()
  }
}
</script>

<style>
.row {
  -webkit-box-orient: initial !important;
  flex-direction: inherit !important;
}
.card.disp-status {
  border: none !important;
}
.disp-status .card-body {
  padding: 5px 10px;
  border-radius: 3px;
}
.mensagem {
  width: 100%;
}
</style>
